import { Tools } from "@gdk/core";

import { PopinButtonAction, PopinLinkTarget } from "./definitions";
import "./style.less";
import type { IPopinDefinition, IPopinButton, IPopinActions } from "./definitions";

/**
 * InGame HTML Popin
 */
export class Popin {
	protected static instances: Popin[] = [];
	protected static _defaultOrientation: undefined | "portrait" | "landscape";

	public static wrapper: HTMLElement;

	/**
	 * Define popin orientation. If not set, popin will follow natural page orientation.
	 * If specified, and if it does not match page orientation, popups will be rotated
	 * to fit with the new orientation.
	 * This is useful when game orientation differs from device orientation.
	 */
	public static set defaultOrientation(orientation: "portrait" | "landscape") {
		this._defaultOrientation = orientation;

		// update existing popins
		for (const instance of Popin.instances) {
			instance.orientation = orientation;
		}
	}

	/**
	 * Instantiate and display a popin
	 * @param definition popin definition
	 * @param actions callback actions
	 */
	public static async display(definition: IPopinDefinition, actions: IPopinActions): Promise<void> {
		const popin = new Popin(definition, actions);
		await popin.display();
	}

	private _definition: IPopinDefinition;
	private _actions: IPopinActions;
	private _domElement: HTMLElement;
	private _notifyClosed: () => void;

	public constructor(definition: IPopinDefinition, actions: IPopinActions) {
		this._definition = definition;
		this._actions = actions;
		this._configure();
		Popin.instances.push(this);
	}

	public dispose(): void {
		const index = Popin.instances.indexOf(this);
		if (index >= 0) {
			Popin.instances.splice(index, 1);
		}
	}

	/**
	 * Change popin orientation, rotate it if orientation is not the same as device
	 * orientation (rotation is applied through css tranform, thanks to classes "portrait"
	 * and "landscape").
	 */
	public set orientation(orientation: "portrait" | "landscape") {
		const popin = this._domElement.querySelector(".gdk-popin");
		if (popin !== null) {
			popin.classList.remove("portrait", "landscape");
			popin.classList.add(orientation);
		}
	}

	/**
	 * Display the popin
	 */
	public async display(): Promise<void> {
		const closed = new Promise<void>((resolve) => {
			this._notifyClosed = resolve;
		});

		Popin.wrapper.appendChild(this._domElement);
		// you have to wait some ticks to be able to trigger the fade-in animation
		await new Promise<void>((resolve) => setTimeout(resolve, 10));
		this._domElement.classList.add("show");

		await closed;
	}

	/**
	 * Close the popin
	 */
	public async close(): Promise<void> {
		if (!this._domElement.classList.contains("show")) {
			return;
		}
		// remove the show animation to trigger de fade-out animation
		this._domElement.classList.remove("show");
		this._notifyClosed();

		await new Promise<void>((resolve) => setTimeout(resolve, 200));
		Popin.wrapper.removeChild(this._domElement);
		this.dispose();
	}

	private _configure(): void {
		this._domElement = document.createElement("div");
		this._domElement.setAttribute("class", "gdk-popin-background");

		const popin = document.createElement("div");
		popin.setAttribute(
			"class",
			[
				"gdk-popin",
				"gdk-popin-" + this._definition.level.toString(),
				Popin._defaultOrientation ?? "",
			]
				.join(" ")
				.trim()
		);

		popin.innerHTML += `<div class="gdk-popin-title">${this._definition.title || "&nbsp"}</div>`;
		if (this._definition.titleStyle) {
			const title = popin.getElementsByClassName("gdk-popin-title")[0] as HTMLElement;
			Tools.assign(title.style, this._definition.titleStyle);
		}

		popin.innerHTML += `<div class="gdk-popin-text">${
			this._definition.content || this._definition.text || "&nbsp"
		}</div>`;
		if (this._definition.textStyle) {
			const text = popin.getElementsByClassName("gdk-popin-text")[0] as HTMLElement;
			Tools.assign(text.style, this._definition.textStyle);
		}

		if (this._definition.buttons && this._definition.buttons.length) {
			const buttons = this._getButtons();
			popin.appendChild(buttons);
		}

		if (this._definition.closable !== false) {
			const closeCross = this._getCloseCross();
			popin.appendChild(closeCross);
		}

		if (this._definition.style) {
			Tools.assign(popin.style, this._definition.style);
		}

		this._domElement.appendChild(popin);

		if (this._definition.clickOutsideToClose) {
			const closeLayer = this._getCloseLayer();
			this._domElement.appendChild(closeLayer);
		}
	}

	private _getButtons(): HTMLElement {
		const buttons = document.createElement("div");
		buttons.setAttribute("class", "gdk-popin-buttons");

		for (const buttonDefinition of this._definition.buttons) {
			buttons.appendChild(this._getButton(buttonDefinition));
		}

		return buttons;
	}

	private _getButton(definition: IPopinButton): HTMLElement {
		const wrapper = document.createElement("div");
		wrapper.setAttribute("class", "gdk-popin-button-wrapper");
		wrapper.innerHTML = `<div class="gdk-popin-button gdk-popin-button-${definition.action}">${definition.label}</div>`;

		if (definition.style) {
			const button = wrapper.firstChild as HTMLElement;
			Tools.assign(button.style, definition.style);
		}

		wrapper.firstChild.addEventListener("click", this._getButtonAction(definition));

		return wrapper;
	}

	private _getButtonAction(definition: IPopinButton): () => void {
		switch (definition.action) {
			case PopinButtonAction.ClosePopin: {
				return this.close.bind(this);
			}
			case PopinButtonAction.CloseGame: {
				return this._actions.closeGame;
			}
			case PopinButtonAction.RefreshGame: {
				return this._actions.refreshGame;
			}
			case PopinButtonAction.Callback: {
				return definition.callback.bind({}, this);
			}
			case PopinButtonAction.Link: {
				if (!definition.link) {
					return () => undefined;
				}
				if (definition.link.target === PopinLinkTarget.Self) {
					return () => {
						window.location.href = definition.link.url;
					};
				} else {
					return () => {
						window.open(definition.link.url);
					};
				}
			}
			default: {
				return () => undefined;
			}
		}
	}

	private _getCloseCross(): HTMLElement {
		const closeCross = document.createElement("a");
		closeCross.setAttribute("class", "gdk-popin-close");
		closeCross.addEventListener("click", this.close.bind(this));
		return closeCross;
	}

	private _getCloseLayer(): HTMLElement {
		const closeLayer = document.createElement("div");
		closeLayer.setAttribute("class", "gdk-popin-click-layer");
		closeLayer.addEventListener("click", this.close.bind(this));
		return closeLayer;
	}
}
