import { Sprite } from "pixi.js";
import { TextArea, EAlign, Docking, DockingBounds } from "@gdk/core-pixi";
import { Tools } from "@gdk/core";
import type { IStyles } from "@gdk/core-pixi";
import type { Container } from "pixi.js";

import { Slide } from "./slide";
import type { Rules } from "./rules";

export enum ELayout {
	TextLeft = 0,
	TextTop = 1,
	TextPadding = 2,
}

export type SlideStylePaddingFunction<SlideType> = (slide: SlideType) => number;
export type SlideStylePaddingValue<SlideType> = SlideStylePaddingFunction<SlideType> | number;
export const getSlideStylePaddingValue = <SlideType>(
	value: SlideStylePaddingValue<SlideType>,
	slide: SlideType
): number => {
	switch (typeof value) {
		case "number":
			return value as number;
		case "function":
			return (value as SlideStylePaddingFunction<SlideType>)(slide);
		default:
			return 0;
	}
};

export interface ISlideStylePadding<SlideType> {
	top?: SlideStylePaddingValue<SlideType>;
	bottom?: SlideStylePaddingValue<SlideType>;
	left?: SlideStylePaddingValue<SlideType>;
	right?: SlideStylePaddingValue<SlideType>;
}

export interface ISlideTextStyle {
	text?: {
		text?: string;
		style?: IStyles;
		align?: EAlign;
		padding?: ISlideStylePadding<TextSlide>;
	};
	title?:
		| string
		| Sprite
		| {
				text?: string;
				style?: IStyles;
				align?: EAlign;
				padding?: ISlideStylePadding<TextSlide>;
		  };
	image?: string | Container;
	layout?: ELayout;
	hasBullet?: boolean;
}

export class TextSlide extends Slide {
	// Public members
	public title: TextArea | Container;
	public text: TextArea;
	public image: Container;
	public layout: ELayout;

	// Protected members
	protected textPadding: ISlideStylePadding<TextSlide> = {};
	protected titlePadding: ISlideStylePadding<TextSlide> = {};

	/**
	 * Constructor
	 * @param config: the slide's style
	 */
	public constructor(config: ISlideTextStyle = {}) {
		// PIXI.Container
		super();

		// Setup slide's configuration (style)
		this.config = config;
	}

	/**
	 * Sets the slide text style (config)
	 */
	public set config(config: ISlideTextStyle) {
		// Title
		if (this.title) {
			this.title.destroy();
		}

		config.title = config.title || {};
		if (typeof config.title === "string") {
			// Title is a sprite texture name
			this.title = Sprite.fromFrame(config.title);
		} else if (config.title instanceof Sprite) {
			// Title is a sprite
			this.title = config.title;
		} else {
			// Title is a text field
			this.title = new TextArea(
				config.title.text || "",
				config.title.style || {
					default: {
						fontName: "Arial",
					},
				},
				{
					align: config.title.align === undefined ? EAlign.Center : config.title.align,
				}
			);
			(this.title as TextArea).wordWrap = true;

			this.titlePadding = Tools.assign(this.titlePadding, config.title.padding);
		}

		this.title.name = "title";
		this.title.docking = new Docking(Docking.centerHorizontal);
		this.addChild(this.title);

		// Text-field
		if (this.text) {
			this.text.destroy();
		}

		config.text = config.text || {};
		this.text = new TextArea(
			config.text.text || "",
			config.text.style || {
				default: {
					fontName: "Arial",
				},
			},
			{
				align: config.text.align === undefined ? EAlign.Center : config.text.align,
			}
		);

		this.text.name = "text";
		this.text.wordWrap = true;
		this.text.docking = new Docking(Docking.centerAll);
		this.addChild(this.text);

		this.textPadding = Tools.assign(this.textPadding, config.text.padding);

		// Image
		if (this.image) {
			this.image.destroy();
		}

		if (config.image) {
			this.image = typeof config.image === "string" ? Sprite.fromFrame(config.image) : config.image;
			this.image.docking = new Docking(Docking.centerAll);
			this.addChild(this.image);
		}

		this.layout = config.layout || ELayout.TextLeft;

		if (config.hasBullet !== undefined) {
			this.hasBullet = config.hasBullet;
		}
	}

	/**
	 * Configures the slide in the given popup
	 * @param popup the popup containing the slide
	 */
	public configure(rules: Rules): void {
		// Misc.
		this.dockingBounds = new DockingBounds(rules.dockingBounds.width, rules.dockingBounds.height);

		// Title
		this.title.y = 15;

		switch (this.layout) {
			case ELayout.TextLeft:
				// Image
				if (this.image) {
					this.image.docking.x = this.dockingBounds.width / 4;
				}
				// Text
				this.text.docking.x = this.image ? -this.dockingBounds.width / 4 + 20 : 0;
				this.text.width = this.image
					? this.dockingBounds.width / 2 - 20
					: this.dockingBounds.width - 40;
				break;
			case ELayout.TextTop:
				// both text and image are centered horizontally
				// image is set bellow the text
				this.text.width = Math.min(this.text.width, this.dockingBounds.width - 40);
				this.text.docking.y = -this.dockingBounds.height / 4 + 10;
				if (this.image) {
					this.image.docking.y = this.dockingBounds.height / 4 - 40;
				}
				break;
			case ELayout.TextPadding:
				// apply padding on title and remove docking;
				delete this.title.docking;

				if (this.titlePadding.left !== undefined || this.titlePadding.right !== undefined) {
					this.title.x = getSlideStylePaddingValue(this.titlePadding.left, this);
					this.title.width =
						this.dockingBounds.width -
						(getSlideStylePaddingValue(this.titlePadding.right, this) + this.title.x);
				}
				if (this.titlePadding.top !== undefined || this.titlePadding.bottom !== undefined) {
					this.title.y = getSlideStylePaddingValue(this.titlePadding.top, this);
					this.title.height =
						this.dockingBounds.height -
						(getSlideStylePaddingValue(this.titlePadding.bottom, this) + this.title.y);
				}

				// apply padding on text (center all);
				delete this.text.docking;

				if (this.textPadding.left !== undefined || this.titlePadding.right !== undefined) {
					this.text.x = getSlideStylePaddingValue(this.textPadding.left, this);
					this.text.width =
						this.dockingBounds.width -
						(getSlideStylePaddingValue(this.textPadding.right, this) + this.text.x);
				}
				if (this.textPadding.top !== undefined || this.textPadding.bottom !== undefined) {
					this.text.y = getSlideStylePaddingValue(this.textPadding.top, this);
					this.text.height =
						this.dockingBounds.height -
						(getSlideStylePaddingValue(this.textPadding.bottom, this) + this.text.y);
				}
				break;
		}
	}

	/**
	 * Called when showing the slide in the rules popup
	 */
	public onShow(rules: Rules): void {
		super.onShow(rules);
	}

	/**
	 * Called when hiding the slide in the rules popup
	 */
	public onHide(rules: Rules): void {
		super.onHide(rules);
	}
}
