import { TweenMax } from "gsap";

import { EGameEvents } from "./gameEvents";
import { EVisualIdentityEvent } from "../adaptativeVisualIdentity/events";
import type { AbstractGame } from "./abstractGame";

export interface IInactivityOptions {
	/**
	 * If true, the timer will restart after each call of "onInactivity" event
	 */
	loop?: boolean;

	/**
	 * Delay of the timer before calling "onInactivity" event
	 */
	delay?: number;

	/**
	 * If true, the timer will be launched after instantiate
	 */
	autoStart?: boolean;

	/**
	 * Set a delay before each loop. If this attribute is not assigned, the delay between loops will be the default delay
	 */
	loopDelay?: number;
}

const defaultInactivityOptions = {
	delay: 5,
	autoStart: true,
};

export class Inactivity {
	/* ATTRIBUTES */

	/**
	 * Set to true to call event every delay exceed
	 */
	protected loop: boolean = false;

	/**
	 * The function called when delay has been exceeded
	 */
	protected onInactivity: () => Promise<void> | void;

	/**
	 * The function called when inactivity has been resetted
	 */
	protected onEndInactivity: () => Promise<void> | void;

	/**
	 * The timeout TweenMax to call the event with delay
	 */
	protected timeOut: TweenMax;

	/**
	 * Check if the click is maintained
	 */
	protected isClickMaintained: boolean = false;

	/**
	 * Check if settings is opened
	 */
	protected isSettingsOpened: boolean = false;

	/**
	 * Check if rules are opened
	 */
	protected isRulesOpened: boolean = false;

	/**
	 * The current game
	 */
	protected game: AbstractGame;

	/**
	 * Check if it has been subscribed to event listener of the document
	 */
	protected subscribed: boolean = false;

	/**
	 * Check if inactivity event has been called
	 */
	protected inactive: boolean = false;

	/**
	 * Options of the inactivity
	 */
	protected options: IInactivityOptions = {};

	/* LIFECYCLE */

	/**
	 * @constructor
	 */
	public constructor(
		game: AbstractGame,
		onInactivity: () => Promise<void> | void,
		onEndInactivity: () => Promise<void> | void,
		options: IInactivityOptions = {}
	) {
		this.game = game;
		this.onInactivity = onInactivity;
		this.onEndInactivity = onEndInactivity;
		this.options = {
			...defaultInactivityOptions,
			...options,
		};

		Object.keys(options).forEach((key) => (this[key] = options[key]));

		if (this.options.autoStart) {
			this.start();
		}
	}

	/* METHODS */

	/**
	 * Stop the TweenMax timeout
	 */
	protected stopTimeOut(): void {
		if (this.timeOut) {
			this.timeOut.kill();
		}
	}

	/**
	 * Start the TweenMax timeout
	 */
	protected startTimeOut(customDelay?: number): void {
		this.stopTimeOut();

		this.timeOut = TweenMax.delayedCall(
			customDelay && !isNaN(parseInt(customDelay.toString(), 10))
				? customDelay
				: this.options.delay,
			this.onDelayExceeded
		);
	}

	/**
	 * Event called to start the inactivity event
	 */
	protected onDelayExceeded = async (): Promise<void> => {
		this.stopTimeOut();

		this.inactive = true;

		if (this.onInactivity) {
			await this.onInactivity();
		}

		if (this.loop) {
			this.startTimeOut(this.options.loopDelay);
		}
	};

	/**
	 * Event trigger when click is maintained
	 */
	protected onClickMaintained = async (): Promise<void> => {
		this.isClickMaintained = true;
		await this.pause();
	};

	/**
	 * Event trigger when click is ended
	 */
	protected onClickEnd = async (): Promise<void> => {
		if (this.isClickMaintained && !this.isSettingsOpened && !this.isRulesOpened) {
			await this.start();
		}
	};

	/**
	 * Event trigger when pointer is moving
	 */
	protected onPointerMove = async (): Promise<void> => {
		if (!this.isClickMaintained && !this.isSettingsOpened && !this.isRulesOpened) {
			await this.start();
		}
	};

	/**
	 * Event trigger when settings are shown
	 */
	protected onShowSettings = async (): Promise<void> => {
		this.isSettingsOpened = true;
		await this.pause();
	};

	/**
	 * Event trigger when rules are shown
	 */
	protected onShowRules = async (): Promise<void> => {
		this.isRulesOpened = true;
		await this.pause();
	};

	/**
	 * Event trigger when settings are hide
	 */
	protected onHideSettings = async (): Promise<void> => {
		this.isSettingsOpened = false;

		if (!this.isRulesOpened) {
			await this.start();
		}
	};

	/**
	 * Event trigger when settings are hide
	 */
	protected onHideRules = async (): Promise<void> => {
		this.isRulesOpened = false;
		this.isSettingsOpened = false;

		await this.start();
	};

	/**
	 * Event trigger when game is paused / unpaused
	 */
	protected onGamePaused = async (paused: boolean): Promise<void> => {
		if (paused) {
			this.pause();
		} else {
			this.start();
		}
	};

	/**
	 * Subscribe to all document events
	 */
	protected subscribe(): void {
		if (!this.subscribed) {
			this.subscribed = true;

			document.addEventListener("pointerdown", this.onClickMaintained, false);
			document.addEventListener("touchstart", this.onClickMaintained, false);
			document.addEventListener("pointerup", this.onClickEnd, false);
			document.addEventListener("touchend", this.onClickEnd, false);
			document.addEventListener("touchmove", this.onPointerMove, false);
			document.addEventListener("mousemove", this.onPointerMove, false);
			document.addEventListener("mouseend", this.onPointerMove, false);

			this.game.vi.emitter.on(EVisualIdentityEvent.ShowSettings, this.onShowSettings);
			this.game.vi.emitter.on(EVisualIdentityEvent.ShowRules, this.onShowRules);
			this.game.vi.emitter.on(EVisualIdentityEvent.ShowMainRules, this.onShowRules);
			this.game.vi.emitter.on(EVisualIdentityEvent.HideSettings, this.onHideSettings);
			this.game.events.on(EGameEvents.GamePaused, this.onGamePaused);
			this.game.events.on(EGameEvents.RulesQuit, this.onHideRules);
		}
	}

	/**
	 * Unsubscribe from all document events
	 */
	protected unsubscribe(): void {
		if (this.subscribed) {
			document.removeEventListener("pointerdown", this.onClickMaintained, false);
			document.removeEventListener("touchstart", this.onClickMaintained, false);
			document.removeEventListener("pointerup", this.onClickEnd, false);
			document.removeEventListener("touchend", this.onClickEnd, false);
			document.removeEventListener("touchmove", this.onPointerMove, false);
			document.removeEventListener("mousemove", this.onPointerMove, false);
			document.removeEventListener("mouseend", this.onPointerMove, false);
			this.subscribed = false;

			this.game.vi.emitter.off(EVisualIdentityEvent.ShowSettings, this.onShowSettings);
			this.game.vi.emitter.off(EVisualIdentityEvent.ShowRules, this.onShowRules);
			this.game.vi.emitter.off(EVisualIdentityEvent.ShowMainRules, this.onShowRules);
			this.game.vi.emitter.off(EVisualIdentityEvent.HideSettings, this.onHideSettings);
			this.game.events.off(EGameEvents.GamePaused, this.onGamePaused);
			this.game.events.off(EGameEvents.RulesQuit, this.onHideRules);
		}
	}

	/* PUBLIC */

	/**
	 * Change the delay before calling the inactivity event
	 * @param delay: The delay to change
	 */
	public setDelay(delay: number): void {
		this.options.delay = delay;
		this.start();
	}

	/**
	 * Start/Restart the timer
	 */
	public start = async (): Promise<void> => {
		if (this.inactive) {
			await this.onEndInactivity();
		}

		this.isClickMaintained = false;
		this.isSettingsOpened = false;
		this.isRulesOpened = false;
		this.inactive = false;
		this.startTimeOut();
		this.subscribe();
	};

	public pause = async (): Promise<void> => {
		if (this.inactive) {
			await this.onEndInactivity();
		}

		this.inactive = false;
		this.stopTimeOut();
	};

	/**
	 * Stop the current timer
	 */
	public stop = async (): Promise<void> => {
		this.unsubscribe();
		this.stopTimeOut();

		if (this.onEndInactivity) {
			await this.onEndInactivity();
		}
	};

	/* GETTERS */

	/**
	 * True if inactivity event has been called
	 */
	public isInactive(): boolean {
		return this.inactive;
	}
}
