import { DockingBounds, ContainerExporter } from "@gdk/core-pixi";
import { Tools } from "@gdk/core";

import { Slide } from "./slide";
import type { Rules } from "./rules";

export class BaseSlide extends Slide {
	/**
	 * Constructor
	 */
	public constructor() {
		// PIXI.Container
		super();
	}

	/**
	 * Configures the slide in the given popup
	 * @param rules the popup containing the slide
	 */
	public configure(rules: Rules): void {
		// Misc.
		this.dockingBounds = new DockingBounds(rules.dockingBounds.width, rules.dockingBounds.height);
	}

	/**
	 * Called when showing the slide in the rules popup
	 * @param rules the popup containing the slide
	 */
	public onShow(rules: Rules): void {
		super.onShow(rules);
	}

	/**
	 * Called when hiding the slide in the rules popup
	 * @param rules the popup containing the slide
	 */
	public onHide(rules: Rules): void {
		super.onHide(rules);
	}

	/**
	 * Exports the base slide
	 */
	public export(): any {
		return Tools.assign(ContainerExporter.exportContainer(this), {
			type: "rules.BaseSlide",
			hasBullet: this.hasBullet,
		});
	}

	/**
	 * Creates a new base slide. Function used by project loader
	 * @param config the JSON representation of the save slide.
	 * @param baseUrl the base Url where to find other assets if needed to be loaded incrementally.
	 */
	public static parse(config: any, baseUrl: string): BaseSlide {
		const slide = new BaseSlide();

		if (config.hasBullet !== undefined) {
			slide.hasBullet = config.hasBullet;
		}

		return slide;
	}
}
