import { Container } from "pixi.js";
import { Docking, DockingBounds, Stackable } from "@gdk/core-pixi";
import type { Button } from "@gdk/core-pixi";

import { DSPopin } from "./popin";
import { ShineableButton } from "./shineableButton";
import type { IDSPopinOptions } from "./popin";

const LEFT_TOP_RIGHT = 30;
const BOTTOM = 40;
const BUTTONS_SPACE = 80;
const DEFAULT_COLOR = "#000000";

const TEXTURES = {
	primary: {
		idleTexture: "buttons/primary/idle",
		overTexture: "buttons/primary/over",
		clickedTexture: "buttons/primary/idle",
		disabledTexture: "buttons/primary/idle",
	},
	secondary: {
		idleTexture: "buttons/secondary/idle",
		overTexture: "buttons/secondary/over",
		clickedTexture: "buttons/secondary/idle",
		disabledTexture: "buttons/secondary/idle",
	},
};

export interface IDSNineSliceButtonOptions {
	label: string;
	color?: string;
	on?: () => Promise<void>;
	primary?: boolean;
}

export interface IDSTwoButtonsPopinOptions {
	base: IDSPopinOptions;
	left?: IDSNineSliceButtonOptions;
	right: IDSNineSliceButtonOptions;
	onLeft?: () => Promise<void>;
	onRight: () => Promise<void>;
	shine?: boolean;
}

export class DSTwoButtonsPopin extends DSPopin {
	private leftButton: ShineableButton;
	private rightButton: ShineableButton;

	private baseContent: Container;

	public constructor(options: IDSTwoButtonsPopinOptions) {
		const baseContent = options.base.content || new Container();
		if (!baseContent.dockingBounds) {
			baseContent.dockingBounds = new DockingBounds(0, 0);
		}
		options.base.content = null;

		super(options.base);

		this.content.addChild(baseContent);
		this.content.dockingBounds = new DockingBounds(
			baseContent.dockingBounds.width,
			baseContent.dockingBounds.height + BUTTONS_SPACE
		);
		this.baseContent = baseContent;

		if (options.left) {
			const primary = options.left.primary ?? false;
			const textures = TEXTURES[primary ? "primary" : "secondary"];
			this.leftButton = new ShineableButton(
				{
					button: textures,
					shine: primary && options.shine,
				},
				{
					default: {
						fontName: "OpenSans-SemiBold",
						fontSize: DSPopin.buttonFontSize,
						fill: options.left.color ? options.left.color : DEFAULT_COLOR,
					},
				},
				options.left.label
			);
			// this.leftButton.docking = new Docking(Docking.bottomLeft);
			this.leftButton.on("pointertap", () => {
				this.executeButtonCallback(options.onLeft);
			});
		}

		const rightIsPrimary = options.right?.primary ?? true;
		const rightTextures = TEXTURES[rightIsPrimary ? "primary" : "secondary"];
		this.rightButton = new ShineableButton(
			{
				button: rightTextures,
				shine: rightIsPrimary && options.shine,
			},
			{
				default: {
					fontName: "OpenSans-SemiBold",
					fontSize: DSPopin.buttonFontSize,
					fill: options.right.color ? options.right.color : DEFAULT_COLOR,
				},
			},
			options.right.label
		);
		// this.rightButton.docking = new Docking(options.left ? Docking.bottomRight : Docking.bottomCenterHorizontal);
		this.rightButton.on("pointertap", () => {
			this.executeButtonCallback(options.onRight);
		});

		const buttons: Button[] = [this.rightButton];
		if (options.left) {
			buttons.unshift(this.leftButton);
		}

		for (const button of buttons) {
			button.isNineSlice = true;
			button.backgroundNineSlice.leftWidth = LEFT_TOP_RIGHT;
			button.backgroundNineSlice.topHeight = LEFT_TOP_RIGHT;
			button.backgroundNineSlice.rightWidth = LEFT_TOP_RIGHT;
			button.backgroundNineSlice.bottomHeight = BOTTOM;

			button.nineSliceMinSize.x = 280;
			button.nineSliceMinSize.y = 70;

			button.nineSlicePadding.x = 20;
			button.nineSlicePadding.y = 21;
			button.nineSlicePadding.width = 20;
			button.nineSlicePadding.height = 22;

			// this.content.addChild(button);
			this.buttonStack.push(button, false);
			// Useful for resizing a niceSlice Button
			button.label = button.label;
		}

		if (this.leftButton !== undefined) {
			this.leftButton.updateSize();
		}

		this.rightButton.updateSize();
	}

	/**
	 * Resize the popin to fit the content
	 *
	 * @access public
	 */
	public resizePopin(): void {
		this.content.dockingBounds.width = this.baseContent.dockingBounds.width;
		this.content.dockingBounds.height = this.baseContent.dockingBounds.height;

		if (this.content.dockingBounds.width < DSPopin.minWidth) {
			this.content.dockingBounds.width = DSPopin.minWidth;
		}

		super.resizePopin();
	}

	/**
	 * Show with or without close button
	 *
	 * @access public
	 * @param {boolean} withClose=false, should add the close button
	 */
	public show(withClose: boolean = false): void {
		this.closeButton.visible = Boolean(withClose);

		super.show();
	}

	protected activateButtons(activate: boolean): void {
		for (const key of ["leftButton", "rightButton", "closeButton"]) {
			if (this[key]) {
				this[key].enabled = activate;
			}
		}
	}
}
