import { TweenMax } from "gsap";
import { Container, particles, Sprite } from "pixi.js";
import * as i18n from "i18next";
import { Docking, DockingBounds, ContainerExporter, ContainerImporter } from "@gdk/core-pixi";
import { Amount, Tools } from "@gdk/core";
import type { IStyles, SpineTimeline, TextField, IProjectContainer } from "@gdk/core-pixi";
import type { IAmount } from "@gdk/core";
import type { Howl } from "howler";

import { TextResultStage } from "./textResultStage";
import { AmountResultStage } from "./amountResultStage";
import type { IScratchResultStyle } from "./scratchStage";
import { ScratchStage } from "./scratchStage";
import type { IAmountCoinWizzStyle } from "./amountCoinWizzStage";
import { AmountCoinWizzStage } from "./amountCoinWizzStage";
import type { IAmountCurrencyWizzStyle } from "./amountCurrencyWizzStage";
import { AmountCurrencyWizzStage } from "./amountCurrencyWizzStage";
import type { ISpineStageStyle } from "./spineStage";
import { SpineStage } from "./spineStage";
import type { AbstractGame } from "../../abstractGame";
import type { IAmountResultStyle } from "./amountResultStage";
import type { ITextResultStyle } from "./textResultStage";

export enum EResultBoxEvents {
	StageAppear = "StageAppear",
	StageDisappear = "StageDisappear",
	StageHasEntered = "StageHasEntered",
	StageWillExit = "StageWillExit",
}

export interface IDefaultResultBoxStyle {
	textStyles: {
		mainTextWinner: Sprite | IStyles;
		subText1Winner: Sprite | IStyles;

		mainTextLooser: Sprite | IStyles;
		subText1Looser: Sprite | IStyles;
		subText2Looser: Sprite | IStyles;
	};
	winAmountTextStyle: IStyles;
	disappears?: boolean;
	amountDisappears?: boolean;
	amountVerticalMargin?: number;
	thresholds?: Array<{
		mainText?: string;
		subText?: string;
		duration?: number;
		animated?: boolean;
		multiplier?: number;
	}>;
	thresholdsTexts?: Array<
		Partial<{
			mainText: string;
			subText: string;
		}>
	>;
	looserTexts?: Partial<{
		mainText: string;
		subText1: string;
		subText2: string;
	}>;
	sounds?: {
		loser?: string | Howl;
		winner?: string | Howl;
		incrementation?: string | Howl;
	};
	backgroundScratch?: {
		loser?: string;
		winner?: string;
	};
	particleSystems?: IParticleSystemStyle;
	minimumFractionDigits?: number;
	looserDuration?: number;
}

interface IParticleSystemStyle {
	count: number;
	sprites?: string[];
	alpha: boolean;
	position: boolean;
	scale: boolean;
}

export interface IWizzResultBoxStyle {
	game: AbstractGame;
	winAmountTextStyle: IStyles;
	sounds?: {
		loser?: string | Howl;
		winner?: string | Howl;
		incrementation?: string | Howl;
	};
	hasCoinsAnimation?: boolean;
	animationSpine?: {
		assetIDWinner?: string;
		assetIDLoser?: string;
		assetIDCoins?: string;
	};
	minimumFractionDigits?: number;
	loserDuration?: number;
	winnerDuration?: number;
}

export interface IWizzCurrencyResultBoxStyle {
	game: AbstractGame;
	winAmountTextStyle: IStyles;
	winAmountStrokeTextStyle: IStyles;
	sounds?: {
		loser?: string | Howl;
		winner?: string | Howl;
		incrementation?: string | Howl;
	};
	hasCoinsAnimation?: boolean;
	animationSpine?: {
		assetIDLoser?: string | SpineTimeline;
		assetIDRollingFX?: string | SpineTimeline;
	};
	sprites?: {
		currencyBackSprite?: Sprite;
		currencyMovingSprite?: Sprite;
		winTextSprite?: Sprite;
	};
	minimumFractionDigits?: number;
}

export interface IDefaultSpineResultBoxStyle {
	thresholdsTexts: Array<{
		mainText: string;
		subText: string;
	}>;

	looserTexts: {
		mainText: string;
		subText: string;
	};

	winMainText?: TextField;
	winSubText?: TextField;
	winAmountText?: TextField;

	loseMainText?: TextField;
	loseSubText?: TextField;

	thresholdsWinAmount?: number[];

	winSpineTimeline: SpineTimeline;
	loseSpineTimeline: SpineTimeline;

	winEnterAnimationName?: string;
	winExitAnimationName?: string;

	loseEnterAnimationName?: string;
	loseExitAnimationName?: string;

	winnerDuration?: number;
	loserDuration?: number;
}

export interface IProjectResulBox extends IProjectContainer {
	resultBox: {
		particleSystems?: IParticleSystemStyle;
		stages: {
			type: string;
			config:
				| IAmountCoinWizzStyle
				| IAmountCurrencyWizzStyle
				| IAmountResultStyle
				| IScratchResultStyle
				| ISpineStageStyle
				| ITextResultStyle;
		}[];
	};
}

export type Stage =
	| AmountCoinWizzStage
	| AmountCurrencyWizzStage
	| AmountResultStage
	| ScratchStage
	| SpineStage
	| TextResultStage;

export class ResultBox extends Container {
	// Public members
	public particleSystem: particles.ParticleContainer = null;

	protected particleSystemConfig: IParticleSystemStyle;
	public onNext: (stage: Stage) => void;

	// Protected members
	protected _amount: IAmount = null;
	protected _stages: Stage[] = [];

	protected _currentStage: Stage = null;
	protected particleTweens: TweenMax[] = [];

	/**
	 * Constructor
	 * @param config the result box config (style)
	 */
	public constructor() {
		// PIXI.Container
		super();

		// Misc.
		this.docking = new Docking(Docking.centerAll);
		this.dockingBounds = new DockingBounds(1, 1); // A point
	}

	/**
	 * Returns the available stages (only get)
	 */
	public get stages(): Stage[] {
		return this._stages;
	}

	/**
	 * Returns the current amount won by the player
	 */
	public get amount(): IAmount {
		return this._amount;
	}

	/**
	 * Animates the result box (automatically send the
	 * matching tracking event)
	 */
	public animate(amount: IAmount, done: () => void): void {
		this._currentStage = this.stages[0];
		this._amount = amount;

		for (const stage of this.stages) {
			stage.hide();
		}

		this.next(() => {
			if (this.particleSystem) {
				this.removeChild(this.particleSystem);
			}

			this._amount = null;
			done();
		});
	}

	/**
	 * Goes to the next stage
	 */
	public next(done?: () => void): void {
		const index = this._stages.indexOf(this._currentStage);

		this._currentStage.visible = true;
		this.emit(EResultBoxEvents.StageAppear, this._currentStage);

		this._currentStage.onShow(this, (stop: boolean, hide: boolean = true) => {
			if (hide) {
				const progressive = index === this._stages.length - 1;
				for (const stage of this.stages) {
					stage.onHide(progressive);
				}
			}

			this.emit(EResultBoxEvents.StageDisappear, this._currentStage);

			if (index + 1 >= this._stages.length || stop) {
				this._currentStage = this._stages[0];
				done();
			} else {
				this._currentStage = this._stages[index + 1];
				this.next(done);
			}
		});

		if (this.onNext) {
			this.onNext(this._currentStage);
		}
	}

	/**
	 * Add a stage to the result box
	 * @param stage the stage to add
	 */
	public addStage(stage: Stage, index?: number): void {
		if (!this._currentStage) {
			this._currentStage = stage;
		}

		if (index === undefined) {
			this._stages.push(stage);
		} else {
			this._stages.splice(index, 0, stage);
		}

		this.addChild(stage);
	}

	/**
	 * Removes a stage from the result box
	 * @param stage the stage reference to remove
	 */
	public removeStage(stage: Stage): void {
		this.removeChild(stage);

		const index = this._stages.indexOf(stage);
		if (index !== -1) {
			this._stages.splice(index, 1);
		}
	}

	/**
	 * Creates particles globally applied on the result box
	 */
	public createParticles(
		size: number = 100,
		particleSprites: string[],
		properties: particles.ParticleContainerProperties
	): void {
		this.particleSystem = new particles.ParticleContainer(size, properties);
		this.particleSystem.docking = new Docking(Docking.centerAll);

		// Add particles
		for (let i = 0; i < size - 1; i++) {
			const index = (Math.random() * particleSprites.length) >> 0;
			const p = Sprite.fromFrame(particleSprites[index]);
			this.particleSystem.addChild(p);
		}

		this.particleSystemConfig = {
			count: size,
			sprites: particleSprites,
			alpha: properties.alpha,
			scale: properties.scale,
			position: properties.position,
		};
	}

	/**
	 * Starts the particle system
	 */
	public startParticleSystem(deltaTime: number = 37): void {
		this.particleSystem.visible = true;

		this.particleTweens.forEach((t) => t.kill());
		this.particleTweens = [];

		// Start animations
		this.particleSystem.children.forEach((p: Sprite, index: number) => {
			p.scaleXY = 0.5;
			p.x = p.y = p.alpha = 0;

			const cos = Math.cos(Math.PI * 2 * Math.random());
			const sin = Math.sin(Math.PI * 2 * Math.random());

			const tween = TweenMax.to(p, 1, {
				delay: (deltaTime * index) / 1000,
				x: screen.width * cos,
				y: screen.height * sin,
				scaleXY: 1 + Math.random(),
				onStart: () => {
					p.alpha = 1;

					tween.updateTo({
						alpha: 0,
					});
				},
			});

			this.particleTweens.push(tween);
		});
	}

	/**
	 * Returns a result box following the wizz guidelines for game giving coins
	 */
	public static createCoinWizz(
		config: IWizzResultBoxStyle,
		stake: IAmount = Amount.fromNumber(100)
	): ResultBox {
		const result = new ResultBox();

		result.addStage(
			new AmountCoinWizzStage({
				game: config.game,
				amountStyle: config.winAmountTextStyle,
				hasCoinsAnimation: config.hasCoinsAnimation || true,
				sounds: {
					loser: (config.sounds && config.sounds.loser) || null,
					winner: (config.sounds && config.sounds.winner) || null,
					incrementation: (config.sounds && config.sounds.incrementation) || null,
				},
				animationSpine: {
					loser: (config.animationSpine && config.animationSpine.assetIDLoser) || null,
					winner: (config.animationSpine && config.animationSpine.assetIDWinner) || null,
					coin: (config.animationSpine && config.animationSpine.assetIDCoins) || null,
				},
				loserDuration: config.loserDuration,
				winnerDuration: config.winnerDuration,
			})
		);

		return result;
	}

	/**
	 * Returns a result box following the wizz guidelines for game giving true currency
	 */
	public static createCurrencyWizz(
		config: IWizzCurrencyResultBoxStyle,
		stake: IAmount = Amount.fromNumber(100)
	): ResultBox {
		const result = new ResultBox();

		result.addStage(
			new AmountCurrencyWizzStage({
				game: config.game,
				amountStyle: config.winAmountTextStyle,
				amountStrokeStyle: config.winAmountStrokeTextStyle,
				hasCoinsAnimation: config.hasCoinsAnimation || true,
				sounds: {
					loser: (config.sounds && config.sounds.loser) || null,
					winner: (config.sounds && config.sounds.winner) || null,
				},
				animationSpine: {
					loser: (config.animationSpine && config.animationSpine.assetIDLoser) || null,
					rollingFX: (config.animationSpine && config.animationSpine.assetIDRollingFX) || null,
				},
				sprites: {
					winTextSprite: (config.sprites && config.sprites.winTextSprite) || null,
					currencyBackSprite: (config.sprites && config.sprites.currencyBackSprite) || null,
					currencyMovingSprite: (config.sprites && config.sprites.currencyMovingSprite) || null,
				},
				minimumFractionDigits: config.minimumFractionDigits,
			})
		);

		return result;
	}

	/**
	 * Returns a default result box following the Illiko guidelines
	 */
	public static createDefault(
		config: IDefaultResultBoxStyle,
		stake: IAmount = Amount.fromNumber(100)
	): ResultBox {
		const result = new ResultBox();

		// Particle systems
		config.particleSystems = config.particleSystems || {
			count: 200,
			alpha: true,
			position: true,
			scale: true,
			sprites: ["sp_particule_00.png", "sp_particule_01.png", "sp_particule_02.png"],
		};

		result.createParticles(
			config.particleSystems.count,
			config.particleSystems.sprites ?? [
				"sp_particule_00.png",
				"sp_particule_01.png",
				"sp_particule_02.png",
			],
			{
				alpha: config.particleSystems.alpha,
				position: config.particleSystems.position,
				scale: config.particleSystems.scale,
			}
		);

		const thresholdsTexts = config.thresholdsTexts ?? [];
		const looserTexts = config.looserTexts ?? {};

		const textResultConfig: ITextResultStyle = {
			mainTextWinner: config.textStyles.mainTextWinner,
			subText1Winner: config.textStyles.subText1Winner,
			mainTextLooser: config.textStyles.mainTextLooser,
			subText1Looser: config.textStyles.subText1Looser,
			subText2Looser: config.textStyles.subText2Looser,
			winningThresholds: [
				{
					amount: Number(stake.value),
					mainText: thresholdsTexts[0]?.mainText ?? i18n.t("result.winner1"),
					subText1: thresholdsTexts[0]?.subText ?? i18n.t("result.winnerSubText"),
				},
				{
					amount: 10 * stake.value,
					mainText: thresholdsTexts[1]?.mainText ?? i18n.t("result.winner2"),
					subText1: thresholdsTexts[1]?.subText ?? i18n.t("result.winnerSubText"),
				},
				{
					amount: 49 * stake.value,
					mainText: thresholdsTexts[2]?.mainText ?? i18n.t("result.winner3"),
					subText1: thresholdsTexts[2]?.subText ?? i18n.t("result.winnerSubText"),
				},
			],
			looserTexts: {
				disappears: config.amountDisappears ?? true,
				mainText: looserTexts.mainText ?? i18n.t("result.looser1"),
				subText1: looserTexts.subText1 ?? i18n.t("result.looser2"),
				subText2: looserTexts.subText2 ?? i18n.t("result.looser3"),
			},
			sounds: {
				loser: (config.sounds && config.sounds.loser) || null,
				winner: (config.sounds && config.sounds.winner) || null,
			},
			looserDuration: config.looserDuration ?? 3,
		};

		const amountResultConfig: IAmountResultStyle = {
			amountDisappears: config.amountDisappears ?? true,
			amountVerticalMargin: config.amountVerticalMargin,
			smallRaySprite: "sp_layon_s.png",
			bigRaySprite: "sp_layon_b.png",
			middleSprite: "sp_effect_win.png",
			amountStyle: config.winAmountTextStyle,
			raysPerThreshold: 6,
			thresholds: [Number(stake.value), 10 * stake.value, 50 * stake.value],
			sounds: {
				incrementation: (config.sounds && config.sounds.incrementation) || null,
			},
			minimumFractionDigits: config.minimumFractionDigits,
		};

		if (config.thresholds !== undefined) {
			textResultConfig.winningThresholds = config.thresholds.map((c, i) => ({
				amount: (c.multiplier ?? 0) * stake.value,
				mainText: thresholdsTexts[i]?.mainText ?? i18n.t(`result.winner${i}`),
				subText1: thresholdsTexts[i]?.subText ?? i18n.t(`result.winnerSubText`),
				animated: c.animated ?? true,
				duration: 2,
			}));
			amountResultConfig.thresholds = config.thresholds.map((c) => ({
				amount: c.multiplier * stake.value,
				animated: c.animated ?? true,
				duration: c.animated ? (c.duration ?? 3) - 2 : c.duration ?? 3,
			}));
		}

		// Stages
		result.addStage(new TextResultStage(textResultConfig));
		result.addStage(new AmountResultStage(amountResultConfig));

		return result;
	}

	/**
	 * Returns a result box containing a popup used for scratch games
	 */
	public static createDefaultScratch(
		config: IDefaultResultBoxStyle,
		stake: IAmount = Amount.fromNumber(100)
	): ResultBox {
		const result = new ResultBox();

		const thresholdsTexts = config.thresholdsTexts ?? [];
		const looserTexts = config.looserTexts ?? {};

		const scratchConfig = {
			disappears: config.disappears ?? true,
			mainTextWinner: config.textStyles.mainTextWinner,
			subText1Winner: config.textStyles.subText1Winner,
			mainTextLooser: config.textStyles.mainTextLooser,
			subText1Looser: config.textStyles.subText1Looser,
			subText2Looser: config.textStyles.subText2Looser,
			winningThresholds: [
				{
					amount: Number(stake.value),
					mainText: thresholdsTexts[0]?.mainText ?? i18n.t("result.winner1"),
					subText1: thresholdsTexts[0]?.subText ?? i18n.t("result.winnerSubText"),
				},
				{
					amount: 10 * stake.value,
					mainText: thresholdsTexts[1]?.mainText ?? i18n.t("result.winner2"),
					subText1: thresholdsTexts[1]?.subText ?? i18n.t("result.winnerSubText"),
				},
				{
					amount: 49 * stake.value,
					mainText: thresholdsTexts[2]?.mainText ?? i18n.t("result.winner3"),
					subText1: thresholdsTexts[2]?.subText ?? i18n.t("result.winnerSubText"),
				},
			],
			amountStyle: config.winAmountTextStyle,
			looserTexts: {
				mainText: looserTexts.mainText ?? i18n.t("result.looser1"),
				subText1: looserTexts.subText1 ?? i18n.t("result.looser2"),
				subText2: looserTexts.subText2 ?? i18n.t("result.looser3"),
			},
			sounds: {
				loser: (config.sounds && config.sounds.loser) || null,
				winner: (config.sounds && config.sounds.winner) || null,
			},
			backgroundTexture: config.backgroundScratch,
			minimumFractionDigits: config.minimumFractionDigits,
		};

		if (config.thresholds !== undefined) {
			scratchConfig.winningThresholds = config.thresholds.map((c, i) => ({
				amount: (c.multiplier ?? 0) * stake.value,
				mainText: thresholdsTexts[i]?.mainText ?? i18n.t(`result.winner${i}`),
				subText1: thresholdsTexts[i]?.subText ?? i18n.t(`result.winnerSubText`),
				duration: c.duration ?? 3.4,
			}));
		}

		result.addStage(new ScratchStage(scratchConfig));

		return result;
	}

	/**
	 * Returns a result box containing spines used for games using spines
	 */
	public static createDefaultSpine(
		config: IDefaultSpineResultBoxStyle,
		stake: IAmount = Amount.fromNumber(100)
	): ResultBox {
		const result = new ResultBox();

		result.addStage(
			new SpineStage({
				thresholdsWinAmount: [Number(stake.value), 10 * stake.value, 49 * stake.value],
				winnerDuration: 5,
				loserDuration: 5,
				winEnterAnimationName: "enter",
				loseEnterAnimationName: "enter",
				winExitAnimationName: "exit",
				loseExitAnimationName: "exit",
				winMainText: config.winMainText
					? config.winMainText
					: config.winSpineTimeline
							.getSlotContainerByName("label_title")
							.getChildByName<TextField>("label"),
				winSubText: config.winSubText
					? config.winSubText
					: config.winSpineTimeline
							.getSlotContainerByName("label_text")
							.getChildByName<TextField>("label"),
				winAmountText: config.winAmountText
					? config.winAmountText
					: config.winSpineTimeline
							.getSlotContainerByName("label_amount")
							.getChildByName<TextField>("label"),
				loseMainText: config.loseMainText
					? config.loseMainText
					: config.loseSpineTimeline
							.getSlotContainerByName("label_title")
							.getChildByName<TextField>("label"),
				loseSubText: config.loseSubText
					? config.loseSubText
					: config.loseSpineTimeline
							.getSlotContainerByName("label_text")
							.getChildByName<TextField>("label"),
				...config,
			})
		);

		return result;
	}

	public export(): IProjectResulBox {
		return Tools.assign(ContainerExporter.exportContainer(this, true), {
			type: "result.ResultBox",
			resultBox: {
				...(this.particleSystem !== null && { particleSystems: this.particleSystemConfig }),
				stages: this.stages.map((stage) => {
					let type: string | undefined;
					if (stage instanceof AmountCoinWizzStage) {
						type = AmountCoinWizzStage.name;
					} else if (stage instanceof AmountCurrencyWizzStage) {
						type = AmountCurrencyWizzStage.name;
					} else if (stage instanceof AmountResultStage) {
						type = AmountResultStage.name;
					} else if (stage instanceof ScratchStage) {
						type = ScratchStage.name;
					} else if (stage instanceof SpineStage) {
						type = SpineStage.name;
					} else if (stage instanceof TextResultStage) {
						type = TextResultStage.name;
					}
					return {
						type,
						config: stage.config,
					};
				}),
			},
		});
	}

	public static parse(config: IProjectResulBox, baseUrl: string): ResultBox {
		let resultBox = new ResultBox();
		const particleSystemConfig = config.resultBox.particleSystems;
		if (particleSystemConfig !== undefined) {
			resultBox.createParticles(particleSystemConfig.count, particleSystemConfig.sprites, {
				alpha: particleSystemConfig.alpha,
				scale: particleSystemConfig.scale,
				position: particleSystemConfig.position,
			});
		}
		for (const stage of config.resultBox.stages) {
			switch (stage.type) {
				case AmountCoinWizzStage.name:
					resultBox.addStage(new AmountCoinWizzStage(stage.config as IAmountCoinWizzStyle));
					break;
				case AmountCurrencyWizzStage.name:
					resultBox.addStage(new AmountCurrencyWizzStage(stage.config as IAmountCurrencyWizzStyle));
					break;
				case AmountResultStage.name:
					resultBox.addStage(new AmountResultStage(stage.config as IAmountResultStyle));
					break;
				case ScratchStage.name:
					resultBox.addStage(new ScratchStage(stage.config as IScratchResultStyle));
					break;
				case SpineStage.name:
					resultBox.addStage(new SpineStage(stage.config as ISpineStageStyle));
					break;
				case TextResultStage.name:
					resultBox.addStage(new TextResultStage(stage.config as ITextResultStyle));
					break;
			}
		}
		resultBox = ContainerImporter.importContainer<ResultBox>(config, baseUrl, resultBox);
		return resultBox;
	}
}
