import { Container } from "pixi.js";
import { Docking, TextArea, EAlign } from "@gdk/core-pixi";
import { Loader } from "@gdk/core";
import { Back, TweenMax } from "gsap";
import { Howl } from "howler";
import type { IStyles } from "@gdk/core-pixi";
import type { Sprite } from "pixi.js";

import { ResultStage } from "./resultStage";
import { EResultBoxEvents } from "./resultBox";
import type { ResultBox } from "./resultBox";

export interface ITextResultThreshold {
	amount: number;
	mainText: string;
	subText1: string;
	animated?: boolean;
	duration?: number;
}

export interface ITextResultLooser {
	disappears?: boolean;
	mainText: string;
	subText1: string;
	subText2: string;
}

export interface ITextResultSounds {
	loser?: Howl | string;
	winner?: Howl | string;
}

export interface ITextResultStyle {
	mainTextWinner: Container | IStyles;
	subText1Winner: Container | IStyles;

	mainTextLooser: Container | IStyles;
	subText1Looser: Container | IStyles;
	subText2Looser: Container | IStyles;

	winningThresholds: ITextResultThreshold[];
	looserTexts: ITextResultLooser;
	looserDuration: number;

	sounds?: ITextResultSounds;
}

export class TextResultStage extends ResultStage {
	// Public members
	public mainText: Container | TextArea;
	public subText1: Container | TextArea;
	public subText2: Container | TextArea;

	public config: ITextResultStyle;

	private _loseSound: Howl;
	private _winSound: Howl;

	/**
	 * Constructor
	 */
	public constructor(config: ITextResultStyle) {
		super();

		// Main text
		if (config.mainTextLooser instanceof Container) {
			this.mainText = config.mainTextLooser;
		} else {
			this.mainText = new TextArea("", config.mainTextLooser, { align: EAlign.Center });
			this.mainText.docking = new Docking(Docking.centerAll);
		}

		// Sub text 1
		if (config.subText1Looser instanceof Container) {
			this.subText1 = config.subText1Looser;
		} else {
			this.subText1 = new TextArea("", config.subText1Looser, { align: EAlign.Center });
			this.subText1.docking = new Docking(Docking.centerAll);
		}

		// Sub text 2
		if (config.subText2Looser instanceof Container) {
			this.subText2 = config.subText2Looser;
		} else {
			this.subText2 = new TextArea("", config.subText2Looser, { align: EAlign.Center });
			this.subText2.docking = new Docking(Docking.centerAll);
		}

		// Sounds
		config.sounds = config.sounds || {};

		if (typeof config.sounds.loser === "string") {
			this._loseSound = Loader.get<Howl>(config.sounds.loser);
		} else if (config.sounds.loser instanceof Howl) {
			this._loseSound = config.sounds.loser;
		}

		if (typeof config.sounds.winner === "string") {
			this._winSound = Loader.get<Howl>(config.sounds.winner);
		} else if (config.sounds.winner instanceof Howl) {
			this._winSound = config.sounds.winner;
		}

		// Misc.
		this.config = config;

		this.dockAsPoint = true;
		this.visible = false;
	}

	/**
	 * On show the result box
	 * @param box the result box reference
	 * @param done the callback to call when stage animation done
	 */
	public onShow(box: ResultBox, done: (stop: boolean, hide?: boolean) => void): void {
		let animated = true;
		let duration = 2;

		// Set texts
		if (box.amount.value === 0) {
			this.subText2.visible = true;

			// Looser
			if (this.mainText instanceof TextArea) {
				this.mainText.freeze();
				this.mainText.keepCanvasReference = true;
				this.mainText.text = this.config.looserTexts.mainText;
				this.mainText.styles = this.config.mainTextLooser as IStyles;
				this.mainText.unfreeze();
			}

			if (this.subText1 instanceof TextArea) {
				this.subText1.freeze();
				this.subText1.keepCanvasReference = true;
				this.subText1.text = this.config.looserTexts.subText1;
				this.subText1.styles = this.config.subText1Looser as IStyles;
				this.subText1.unfreeze();
			}

			if (this.subText2 instanceof TextArea) {
				this.subText2.freeze();
				this.subText2.keepCanvasReference = true;
				this.subText2.text = this.config.looserTexts.subText2;
				this.subText2.styles = this.config.subText2Looser as IStyles;
				this.subText2.unfreeze();
			}

			if (this._loseSound) {
				this._loseSound.play();
			}

			duration = this.config.looserDuration;
		} else {
			// Winner
			this.subText2.visible = false;

			// Set text
			if (this.mainText instanceof TextArea) {
				this.mainText.freeze();
				this.mainText.keepCanvasReference = true;
				this.mainText.text = this.config.winningThresholds[0].mainText;
			}

			if (this.subText1 instanceof TextArea) {
				this.subText1.freeze();
				this.subText1.text = this.config.winningThresholds[0].subText1;
			}

			this.config.winningThresholds.forEach((t) => {
				if (box.amount.value >= t.amount) {
					if (this.mainText instanceof TextArea) {
						this.mainText.text = t.mainText;
					}

					if (this.subText1 instanceof TextArea) {
						this.subText1.text = t.subText1;
					}

					duration = t.duration ?? 2;
					animated = t.animated ?? true;
				}
			});

			// Set styles
			if (this.mainText instanceof TextArea) {
				this.mainText.styles = this.config.mainTextWinner as IStyles;
				this.mainText.unfreeze();
			} else {
				this.mainText = this.config.mainTextWinner as Sprite;
			}

			if (this.subText1 instanceof TextArea) {
				this.subText1.styles = this.config.subText1Winner as IStyles;
				this.subText1.unfreeze();
			} else {
				this.subText1 = this.config.subText1Winner as Sprite;
			}

			if (this._winSound) {
				this._winSound.play();
			}
		}

		// Add children
		this.addChild(this.mainText);
		this.addChild(this.subText1);
		this.addChild(this.subText2);

		// Set texts scale to 1.0
		this.mainText.scaleXY = 1;
		this.subText2.scaleXY = 1;
		this.subText1.scaleXY = 1;

		// Set positions of texts
		this.mainText.docking.y = -this.subText1.height;
		this.subText1.docking.y = 0;
		this.subText2.docking.y = this.subText1.height;

		// Pivot of texts
		this.mainText.pivot.x = this.mainText.width / 2;
		this.mainText.pivot.y = this.mainText.height / 2;

		this.subText1.pivot.x = this.subText1.width / 2;
		this.subText1.pivot.y = this.subText1.height / 2;

		this.subText2.pivot.x = this.subText2.width / 2;
		this.subText2.pivot.y = this.subText2.height / 2;

		if (!animated) {
			this.mainText.docking.y -= 50;
			this.subText1.docking.y -= 50;
			this.subText2.docking.y -= 50;
			done(false, false);
			return;
		}

		this.animate(box, duration, done);
	}

	protected animate(
		box: ResultBox,
		duration: number,
		done: (stop: boolean, hide?: boolean) => void
	): void {
		// Reset texts scale
		this.mainText.scaleXY = 0;
		this.subText2.scaleXY = 0;
		this.subText1.scaleXY = 0;

		// ScaleXY from 0 to 1
		TweenMax.to(this.mainText, 0.25 * duration, {
			scaleXY: 1,
			onComplete: () => {
				if (this.mainText instanceof TextArea) {
					this.mainText.keepCanvasReference = false;
				}
			},
		});

		TweenMax.to(this.subText1, 0.25 * duration, {
			delay: 0.25 * duration,
			scaleXY: 1,
			onComplete: () => {
				if (this.subText1 instanceof TextArea) {
					this.subText1.keepCanvasReference = false;
				}
			},
		});

		TweenMax.to(box.particleSystem, duration, {
			scaleXY: 1,
		});

		TweenMax.to(this.subText2, 0.25 * duration, {
			delay: 0.25 * duration,
			scaleXY: 1,
			onComplete: () => {
				if (this.subText2 instanceof TextArea) {
					this.subText2.keepCanvasReference = false;
				}

				box.emit(EResultBoxEvents.StageHasEntered, this);

				TweenMax.to(box.particleSystem, 0.25 * duration, {
					delay: 0.25 * duration,
					scaleXY: 0,
				});

				if (box.amount.value === 0 && this.config.looserTexts.disappears === false) {
					done(true, false);
					return;
				}

				// ScaleXY from 1 to 0
				TweenMax.to(this.mainText, 0.25 * duration, {
					ease: Back.easeIn.config(1.7),
					delay: 0.25 * duration,
					scaleXY: 0,
				});

				TweenMax.to(this.subText1, 0.25 * duration, {
					ease: Back.easeIn.config(1.7),
					delay: 0.25 * duration,
					scaleXY: 0,
				});

				TweenMax.to(this.subText2, 0.25 * duration, {
					ease: Back.easeIn.config(1.7),
					delay: 0.25 * duration,
					scaleXY: 0,
					onStart: () => {
						box.emit(EResultBoxEvents.StageWillExit, this);
					},
					onComplete: () => {
						this.removeChild(box.particleSystem);
						done(box.amount.value === 0, true);
					},
				});
			},
		});
	}
}
