import type { PushChannel } from "@falcon/push-client";

import { Popin } from "../../com/popin/popin";
import { EPopinLevel, PopinButtonAction } from "../../com/popin/definitions";
import { EMessageType } from "../../com/messageManager/definitions";
import type { Multipass } from "./multipass";

export enum EMultipassGameState {
	PENDING = "PENDING",
	ONGOING = "ONGOING",
	FINISHED = "FINISHED",
}

export enum EMultipassNotificationType {
	SESSION_INITIALIZED = "SESSION_INITIALIZED",
	PLAYER_ENTER = "PLAYER_ENTER",
	PAYOUT = "PAYOUT",
	PLAYER_EXIT = "PLAYER_EXIT",
	SCRIPT = "SCRIPT",
	SESSION_CANCELLED = "SESSION_CANCELLED",
	SESSION_STOPPED = "SESSION_STOPPED",
	PLAYER_REMOVED = "PLAYER_REMOVED",
	TRANSACTION_EVENT = "FALCON_TRANSACTION_EVENT",
}

export interface IMultipassNotification<T> {
	broadcast: boolean;
	sequenceNumber?: number;
	gameState: EMultipassGameState;
	roomId: string;
	sessionId: string;
	time: number;
	type: EMultipassNotificationType;
	content: T;
}

export class MultipassNotifications {
	// Public members
	public game: Multipass;
	public channel: PushChannel = null;

	public lastMessageId: number = null;
	public lastMessageBroadcastId: number = null;

	public onMessage: (data: IMultipassNotification<any>) => void;

	// Private members
	private _muted: boolean = false;
	private _closed: boolean = true;

	private _timerId: number = null;
	private _timerBroadcastId: number = null;

	private _pendingNotifications: Array<IMultipassNotification<any>> = [];
	private _pendingNotificationsBroadcast: Array<IMultipassNotification<any>> = [];

	private _connectionPopin: Popin = null;
	private _connectionRetryCount: number = 0;

	// Static members
	public static tempo: number = 500;

	/**
	 * Constructor
	 * @param gameVariantName the game variant name to listen
	 * @param roomId the room id running the session with the given game variant name
	 */
	public constructor(game: Multipass, roomId?: string) {
		// Misc.
		this.game = game;

		// Connect?
		if (roomId) {
			this.createChannel(roomId);
		}

		// Connection popin
		this._connectionPopin = new Popin(
			{
				level: EPopinLevel.Info,
				text: this.game.com.t("sse_connection_try", "multiplayer"),
				closable: false,
				buttons: [],
			},
			null
		);
	}

	/**
	 * Creates the notifications channel
	 * @param roomId the room id running the session with the given game variant name
	 */
	public async createChannel(roomId: string): Promise<void> {
		// this.source = new EventSource(`mtp/games/${gameVariantName}/rooms/${roomId}/notifications`);
		/*this.source = new EventSource("https://mtp-qa-01.lotsys.corp:9991/mtp/" + roomId, {
            withCredentials: true
        });*/

		// Create channel
		this.channel = await this.game.com.getNotificationsChannel({
			transport: 2,
			topic: {
				type: "mtp",
				id: roomId,
			},
		});

		// Check disconnected
		this.channel.addEventListener("disconnected", async () => (this._closed = true));

		// Callback on receive a message
		this.channel.addEventListener("message", (e) => {
			// Parse message data
			if (!e.detail || !e.detail.content) {
				return;
			}

			const data = e.detail.content as IMultipassNotification<any>;
			this.dispatchNotification(data);
		});

		// Configure
		if (this._muted) {
			this.channel.mute();
		}

		this._closed = false;
	}

	/**
	 * Starts the notifications by connecting to the server
	 * @param retryCount the number of retries if connection failed
	 */
	public async start(retryCount: number = 3): Promise<void> {
		if (!this.channel) {
			return;
		}

		// Show connection popin?
		if (this._connectionRetryCount > 0) {
			this._connectionPopin.display();
		} else {
			this._connectionPopin.close();
		}

		try {
			// Connect
			await this.channel.start();

			this._connectionPopin.close();
		} catch (e) {
			retryCount--;
			this._connectionRetryCount++;

			if (retryCount > 0) {
				return await this.start(retryCount);
			}

			this._connectionPopin.close();
			await this.game.com.messageManager.display({
				code: "SSE.CONNECTION.FAILED",
				type: EMessageType.Immediate,
				popup: {
					level: EPopinLevel.Error,
					text: this.game.com.t("sse_connection_failed", "multiplayer"),
					buttons: [
						{
							label: this.game.com.t("close", "ui") || " Close",
							action: PopinButtonAction.CloseGame,
						},
						{
							label: this.game.com.t("retry", "ui"),
							action: PopinButtonAction.ClosePopin,
						},
					],
				},
			});

			await this.start();
		}
	}

	/**
	 * Closes the notifications by disconnecting from the server
	 */
	public async close(): Promise<void> {
		if (this._closed) {
			return;
		}
		if (this.channel) {
			await this.channel.stop();
		}
		this._closed = true;
	}

	/**
	 * Returns if the notifications are muted or not
	 */
	public get muted(): boolean {
		return this._muted;
	}

	/**
	 * Mutes the notifications
	 */
	public set muted(muted: boolean) {
		this._muted = muted;
		if (!this.channel) {
			return;
		}

		muted ? this.channel.mute() : this.channel.unmute();
	}

	/**
	 * Dispatches the given data by checking the last message id etc.
	 * @param data the data to dispatch
	 */
	public dispatchNotification(data: IMultipassNotification<any>): void {
		if (data.sequenceNumber === undefined) {
			return this.emitNotification(data);
		}

		// Setup first message Id
		if (this.lastMessageId === null && !data.broadcast) {
			this.lastMessageId = data.sequenceNumber;
		}
		if (this.lastMessageBroadcastId === null && data.broadcast) {
			this.lastMessageBroadcastId = data.sequenceNumber;
		}

		// If old notification, bypass notification
		if (data.broadcast && data.sequenceNumber <= this.lastMessageBroadcastId) {
			return;
		}

		if (!data.broadcast && data.sequenceNumber <= this.lastMessageId) {
			return;
		}

		// Determine if there is a hole in notifications
		const shouldWait =
			(data.broadcast && data.sequenceNumber - this.lastMessageBroadcastId >= 2) ||
			(!data.broadcast && data.sequenceNumber - this.lastMessageId >= 2);

		if (shouldWait) {
			this._setupNotification(data);

			// Broadcast notifications
			if (this._pendingNotificationsBroadcast.length > 0) {
				this._shiftNotifications(
					this._pendingNotificationsBroadcast,
					this.lastMessageBroadcastId,
					"_timerBroadcastId"
				);
			}

			// Private notifications
			if (this._pendingNotifications.length > 0) {
				this._shiftNotifications(
					this._pendingNotifications,
					this.lastMessageBroadcastId,
					"_timerId"
				);
			}

			return;
		}

		// Emit
		this.emitNotification(data);
	}

	/**
	 * Emits the given notification and updates the last message Id
	 * for private messages and broadcasts
	 * @param data the notification data
	 */
	public emitNotification(data: IMultipassNotification<any>): void {
		if (this.onMessage) {
			if (data.type === EMultipassNotificationType.TRANSACTION_EVENT) {
				this.game.com.populateNotifications(data.content);
				return;
			}

			if (data.sessionId !== undefined && this.game.session.id !== data.sessionId) {
				return;
			}

			if (data.sequenceNumber === undefined) {
				return this.onMessage(data);
			}

			// Update last message Id
			if (data.broadcast) {
				this.lastMessageBroadcastId = data.sequenceNumber;
			} else {
				this.lastMessageId = data.sequenceNumber;
			}

			this.onMessage(data);
		}
	}

	/**
	 * Reset the notifications datas
	 */
	public reset(): void {
		this.lastMessageId = null;
		this.lastMessageBroadcastId = null;
	}

	/**
	 * Setups pending notifications (reset tempo, create tempo etc.)
	 */
	private _setupNotification(notification: IMultipassNotification<any>): void {
		// Broadcast notification
		if (notification.broadcast) {
			return this._setupPendingNotifications(
				this._pendingNotificationsBroadcast,
				notification,
				"_timerBroadcastId"
			);
		}

		return this._setupPendingNotifications(this._pendingNotifications, notification, "_timerId");
	}

	/**
	 * Setups the pending notifications by adding the given notification to the pending
	 * list. If the timer wasn't created, the timer is created
	 */
	private _setupPendingNotifications(
		notifications: Array<IMultipassNotification<any>>,
		notifiation: IMultipassNotification<any>,
		timerKey: string
	): void {
		if (this[timerKey] === null) {
			// Create timer
			notifications.splice(0, notifications.length - 1);
			notifications.push(notifiation);

			this[timerKey] = setTimeout(async () => {
				// Update game session
				await this._updateSession();
				this[timerKey] = null;
			}, MultipassNotifications.tempo);
		} else {
			// Add notification + sort
			notifications.push(notifiation);
			notifications.sort((a, b) => a.sequenceNumber - b.sequenceNumber);
		}
	}

	/**
	 * Shifts all the pending notifications until we find a hole
	 * in sequence number. If the count of pending notifications is equal
	 * to 0, then the associated timer is cleared
	 */
	private _shiftNotifications(
		pendingNotifications: Array<IMultipassNotification<any>>,
		lastMessageId: number,
		timerKey: string
	): void {
		// Emit until we find a hole in sequence number
		while (
			pendingNotifications.length &&
			pendingNotifications[0].sequenceNumber === lastMessageId + 1
		) {
			const notification = pendingNotifications.shift();
			this.emitNotification(notification);
		}

		// If empty, clear timeout to update session
		if (pendingNotifications.length === 0) {
			clearTimeout(this[timerKey]);
		}
	}

	// Updates the game session. This will mute the notifications
	private async _updateSession(): Promise<void> {
		// Mute notifications in order to update game session
		const notificationsWasMuted = this.game.notifications.muted;
		this.game.notifications.muted = true;

		await this.game.updateSession();

		// Then, unmute notifications
		this.game.notifications.muted = notificationsWasMuted;
	}
}
