import { utils, ticker, Graphics, Rectangle } from "pixi.js";
import i18n from "i18next";
import { GameCore, Loader, State, Tools, HowlerLoader } from "@gdk/core";
import {
	FontLoader,
	Resize,
	StateImporter,
	ContainerImporter,
	ScratchSolver,
	AssetStore,
} from "@gdk/core-pixi";
import type { LoadedView, ContainerView, IProject } from "@gdk/core-pixi";
import type { Sprite } from "pixi.js";

import { ProjectLoader } from "../loading";
import { EVisualIdentityAction, EVisualIdentityEvent } from "./events";
import type { AbstractGame } from "../game/abstractGame";
import type {
	IIdentity,
	IAdaptativeLoadingView,
	IAdaptativeIntroductionView,
	IAdaptativeInGameView,
	IAdaptativeLockedView,
} from "./identity";

declare var __webpack_public_path__: string;

/**
 * Optionnal boolean parameters used by the adaptative visual identity.
 */
export interface IAdaptativeIdentityParameters {
	rulesEnabled?: boolean;
	soundButtonEnabled?: boolean;
	skipIntroduction?: boolean;
	backgroundRulesClickable?: boolean;
	[key: string]: any;
}

export interface IAdaptativeIdentityConfig {
	/**
	 * The lottery id to load identity
	 */
	lottery: string;
	/**
	 * The theme of the lottery identity
	 */
	theme: string;
	/**
	 * Parameters feeded to the identity
	 */
	parameters?: IAdaptativeIdentityParameters;
}

export class AdaptativeVisualIdentity extends GameCore {
	/**
	 * Defines the main emitter listened by the views
	 */
	public emitter: utils.EventEmitter = new utils.EventEmitter();
	/**
	 * Defines the background used as an overlay when player shows the settings.
	 * Anyway it can be used for other needs. The background is added into the stage
	 * and has a Cover resize method.
	 */
	public background: Sprite | Graphics = null;
	/**
	 * Defines the loading view instance created after importing the lottery identity
	 */
	public loadingView: (LoadedView | ContainerView) & IAdaptativeLoadingView = null;
	/**
	 * Defines the introduction view instance created after importing the lottery identity
	 */
	public introductionView: (LoadedView | ContainerView) & IAdaptativeIntroductionView = null;
	/**
	 * Defines the in-game view instance created after importing the lottery identity
	 */
	public inGameView: (LoadedView | ContainerView) & IAdaptativeInGameView = null;
	/**
	 * Defines the lock view instance created after importing the lottery identity
	 */
	public lockView: (LoadedView | ContainerView) & IAdaptativeLockedView = null;
	/**
	 * Defines the skin of the overall loaded identity
	 */
	public skin: any;

	/**
	 * Called once the VI has been initialized
	 */
	public onInitialized: () => void;
	/**
	 * For backward compatibility, keep show rules callback
	 * @deprecated
	 */
	public onShowRules: () => void;
	/**
	 * For backward compatibility, keep show main rules callback
	 * @deprecated
	 */
	public onShowMainRules: () => void;

	/**
	 * Reference of the game using the Adaptative Visual Identity
	 */
	public game: AbstractGame;

	/**
	 * Reference of the parameters used by the Adaptative Visual Identity
	 */
	public parameters: IAdaptativeIdentityParameters;

	private _config: IAdaptativeIdentityConfig;
	private _files: any = {};

	// false if sound was disabled from VI
	protected _soundEnabled: boolean = true;

	/**
	 * Constructor
	 * @param game the game reference containing the VI
	 */
	public constructor(game: AbstractGame, config: IAdaptativeIdentityConfig) {
		// Game Core
		super(null, null, false);

		// Misc.
		this.game = game;
		this._config = config;
		this.parameters = this._config.parameters || {};

		ticker.shared.autoStart = false;

		// Register needed loaders
		Loader.register(new FontLoader());

		// Background
		this.background = new Graphics();
		this.background.resize = new Resize(Resize.cover);
		this.background.interactive = true;

		// Events
		this._bindEvents();
	}

	/**
	 * Loads the given lottery identity exposing the required views
	 * @param lottery the lottery identifier to know what identity to load
	 */
	public async getIdentity(lottery: string): Promise<IIdentity> {
		__webpack_public_path__ = `${this.game.gameConfig.frameworkPath || "."}${Tools.getFolder(
			this.game.gameConfig.modules.paths["@gdk/gamekit"]
		)}/`;

		switch (lottery) {
			case "fdj":
				return await import(/* webpackChunkName: "fdj" */ "./identities/fdj");
			case "fdj_wizz":
				this.background.visible = false;
				return await import(/* webpackChunkName: "fdj_wizz" */ "./identities/fdj_wizz");
			case "ds":
				return await import(/* webpackChunkName: "ds" */ "./identities/danskeSpil");
			case "nt":
				return await import(/* webpackChunkName: "nt" */ "./identities/norskTipping");
			case "svs":
				return await import(/* webpackChunkName: "svs" */ "./identities/svenskaSpel");
			case "lor":
				return await import(/* webpackChunkName: "lor" */ "./identities/lor");
			case "fgs":
				return await import(/* webpackChunkName: "fgs" */ "./identities/fgs");
			case "fgs_nt":
				return await import(/* webpackChunkName: "fgs_nt" */ "./identities/fgs_nt");
			case "fgs_ds":
				return await import(/* webpackChunkName: "fgs_ds" */ "./identities/fgs_ds");
			case "fgs_szk":
				return await import(/* webpackChunkName: "fgs_szk" */ "./identities/fgs_szk");
			case "fgs_ees":
				return await import(/* webpackChunkName: "fgs_ees" */ "./identities/fgs_ees");
			case "vks":
				return await import(/* webpackChunkName: "vks" */ "./identities/veikkaus");
			default:
				return null;
		}
	}

	/**
	 * Inits the VI by loading the assets and creating the state
	 */
	public async init(done?: () => void): Promise<void> {
		// Load theme files
		await this.loader.configureFromTheme(
			"vi",
			this.game.settings.language,
			`${this._config.lottery}/${this._config.theme}/`
		);
		await this.loader.load();

		// i18n
		const locales = this.loader.get("vi-i18n-base");
		if (locales) {
			i18n.addResourceBundle(this.game.settings.language, "game", locales, true, true);
		}

		const overridenLocales = this.loader.get("vi-i18n");
		if (overridenLocales) {
			i18n.addResourceBundle(this.game.settings.language, "game", overridenLocales, true, true);
		}

		// Views
		const identity = await this.getIdentity(this._config.lottery);

		this.loadingView = new identity.LoadingView(this) as (LoadedView | ContainerView) &
			IAdaptativeLoadingView;
		this.introductionView = new identity.IntroductionView(this) as (LoadedView | ContainerView) &
			IAdaptativeIntroductionView;
		this.inGameView = new identity.InGameView(this) as (LoadedView | ContainerView) &
			IAdaptativeInGameView;
		this.lockView = new identity.LockView(this) as (LoadedView | ContainerView) &
			IAdaptativeLockedView;

		// State
		this.state = new State({
			loading: { view: this.loadingView },
			introduction: { view: this.introductionView },
			game: { view: this.inGameView },
			lock: { view: this.lockView },
		});

		// Load project (from editor)
		const project = this.loader.get<IProject>("project");
		this.skin = project.skin;
		this.loader.remove("project");

		ContainerImporter.customContainerCreator = (config, container) => {
			return ProjectLoader.customContainerLoader(config, this.loader.baseUrl, container, project);
		};

		AssetStore.parse(project.assets, this.loader.baseUrl);

		project.views.forEach((v) =>
			StateImporter.importView(
				this.state.states[v.stateName].view as LoadedView,
				v,
				this.loader.baseUrl,
				project
			)
		);

		// Start First as VI assets are still accessible.
		super.start();

		// Then Clear loader as VI assets are now useless.
		// excepted HowlerLoader that optionally loaded sound files than are often loaded
		// during the game
		if (this.game.gameConfig.gameParameters.overrideOnMultipleLoads) {
			this.game.pixiLoader.overrideOnMultipleLoads = true;
		} else {
			for (const loader of this.loader.loaders) {
				if (!(loader instanceof HowlerLoader)) {
					loader.clear();
				}
			}
		}

		// Configure the GamePixi lock view
		this.game.lockStage.addChild(this.lockView);
		this.lockView.orientation = this.game.settings.orientation;

		// Resize game
		this.game.resize();

		// VI has been initialized
		if (this.onInitialized) {
			this.onInitialized();
		}

		// Hide the environment "loading" image
		this.game.com.bridge.executeAction("GAME_LOADING");

		if (done) {
			done();
		}
	}

	/**
	 * Shows the loading view
	 */
	public showLoadingView(): void {
		// Add view in front of all other views
		this.game.stage.addChild(this.loadingView);

		// Configure loader
		this.game.loader.onProgress = (percentage) => this.loadingView.onProgress(percentage);
	}

	/**
	 * Shows the introduction
	 * @param done callback once the animation is done
	 */
	public async showIntroduction(done?: () => void): Promise<void> {
		// Always set on top
		this.game.stage.addChild(this.introductionView);

		// Go to introduction view
		await this.state.to("introduction");

		// Play introduction sprite
		return new Promise<void>((resolve) => {
			this.introductionView.animate(() => {
				this.emitter.emit(EVisualIdentityEvent.IntroductionShown);
				if (done) {
					done();
				}

				resolve();
			});
		});
	}

	/**
	 * Shows the VI in game (logo, settings, purse etc.)
	 */
	public showInGame(): void {
		// Always set on top
		this.game.stage.addChild(this.inGameView);
		this.game.stage.addChild(this.introductionView);

		// Go to game view
		this.state.to("game");
	}

	/**
	 * Hides the visual identity views
	 */
	public hide(): void {
		this.game.stage.removeChild(this.background);

		Object.keys(this.state.states).forEach((s) => {
			const view = this.state.states[s].view as ContainerView;
			view.visible = false;
		});
	}

	/**
	 * Emit a VisualIdentity action event
	 * @param event the action type to emit to intentities
	 * @param parameters the parameters to send to the identity on the given action
	 */
	public emit<T>(event: EVisualIdentityAction | string, parameters?: T): boolean {
		return this.emitter.emit(event, parameters);
	}

	// Fills the background according to the given color / alpha
	private _fillBackground(color: number, alpha?: number): void {
		if (!(this.background instanceof Graphics)) {
			return;
		}

		// set default values
		if (alpha === undefined) {
			alpha = 0.4;
		}

		this.background.beginFill(color, alpha);
		this.background.clear();
		this.background.drawRect(0, 0, 1, 1);
		this.background.endFill();

		// We have to manually set hit area on PIXI.Graphics
		this.background.hitArea = new Rectangle(
			0,
			0,
			this.game.renderer.width,
			this.game.renderer.height
		);
	}

	// Binds the events
	private _bindEvents(): void {
		// Resize
		window.addEventListener("resize", () => this.emitter.emit("resize"));

		// Settings
		this.emitter.on(EVisualIdentityEvent.ShowSettings, () => {
			// the loro lottery use a clickable transparent background for settings
			const behaviourParameter =
				this.game.gameConfig.behavioursParameters[this.game.gameConfig.selectedBehaviour];
			this._fillBackground(0x0, behaviourParameter.settingsBackgroundOpacity);

			this.game.stage.addChild(this.background);
			this.game.stage.addChild(this.inGameView);

			// Hack: need to render stage to update transforms according to
			// vertex datas of settings component and its children...
			// this.game.renderer.render(this.game.stage);
		});

		this.emitter.on(EVisualIdentityEvent.HideSettings, () => {
			this.game.stage.removeChild(this.background);
		});

		// Scratch
		this.emitter.on(EVisualIdentityAction.ShowScratchSettings, (visible: boolean) => {
			if (visible) {
				this._fillBackground(0x0, 0.4);
				this.game.stage.addChild(this.background);
			} else {
				this.game.stage.removeChild(this.background);
			}

			this.game.stage.addChild(this.inGameView);
		});

		this.emitter.on(EVisualIdentityEvent.SetScratchConfiguration, (params) => {
			ScratchSolver.scratchs.forEach((scratch) => {
				// TODO: change this addTarget interface for a scratchComponent.setTool(IScratchConfig)
				scratch.emitterDatas = params.emitterDatas;
				scratch.revealEmitterDatas = params.revealEmitterDatas;
				params.pointer.addTarget(scratch);
			});
		});

		this.emitter.on(EVisualIdentityAction.SoundEnabled, (enabled: boolean) => {
			this._soundEnabled = enabled;
		});

		// Backward compatibility
		this.emitter.on(EVisualIdentityEvent.ShowRules, () => this.onShowRules && this.onShowRules());
		this.emitter.on(
			EVisualIdentityEvent.ShowMainRules,
			() => this.onShowMainRules && this.onShowMainRules()
		);
	}

	/**
	 * For backward compatibility, keep setter to know if settings are enabled
	 * @deprecated
	 */
	public set settingsEnabled(enabled: boolean) {
		this.emitter.emit(EVisualIdentityAction.SettingsEnabled, enabled);
	}

	/**
	 * Enabled / disable sound
	 * @param enabled: boolean
	 */
	public set soundEnabled(enabled: boolean) {
		this._soundEnabled = enabled;
		this.emitter.emit(EVisualIdentityAction.SoundEnabled, enabled);
	}

	/**
	 * Return true if sound is enabled in VI, false otherwise.
	 * @returns boolean
	 */
	public get soundEnabled(): boolean {
		return this._soundEnabled;
	}
}
