import { Button } from "@gdk/core-pixi";
import { Sprite, Texture, Graphics } from "pixi.js";
import { TweenMax, Linear } from "gsap";
import type { IButtonStyle, IStyles } from "@gdk/core-pixi";

export interface IShineableButtonOptions {
	button: IButtonStyle;
	shine?: boolean;
}

const CTA = "buttons/primary/shine";

export class ShineableButton extends Button {
	private shine: Sprite;
	private _bump: number = 0;
	private shineScale: number;
	private shineMask: Graphics;

	public constructor(options: IShineableButtonOptions, textStyles: IStyles, text: string) {
		super(options.button, textStyles, text);

		if (options.shine) {
			this.shine = new Sprite(Texture.from(CTA));
			this.addChild(this.shine);
			this.shine.anchor.x = 0;
			this.shine.anchor.y = 0;
		}
	}

	/**
	 * It will update the button size to fit min width or the text
	 *
	 * @access public
	 */
	public updateSize(): void {
		if (this.shine) {
			if (!this.shineMask) {
				this.shineMask = new Graphics();
			} else {
				this.shineMask.clear();
			}

			this.shineMask.beginFill(0xffffff, 1);
			this.shineMask.drawRoundedRect(
				9,
				8,
				this.backgroundNineSlice.width - 18,
				this.backgroundNineSlice.height - 20,
				9
			);
			this.shineMask.endFill();
			this.addChild(this.shineMask);

			this.shine.mask = this.shineMask;
			this.shine.scaleXY = this.shineScale = this.backgroundNineSlice.height / this.shine.height;

			TweenMax.fromTo(
				this.shine,
				0.9,
				{
					x: -this.shine.width,
				},
				{
					x: this.backgroundNineSlice.width,
					repeat: -1,
					repeatDelay: 2,
					ease: Linear.easeNone,
				}
			);

			TweenMax.fromTo(
				this,
				0.9,
				{
					bump: 0,
				},
				{
					bump: Math.PI,
					repeat: -1,
					repeatDelay: 2,
					ease: Linear.easeNone,
				}
			);
		}
	}

	/**
	 * It will set the shine bump (scale bump)
	 *
	 * @access public
	 * @param {number} bump, the bump size
	 */
	public set bump(bump: number) {
		this._bump = this.shineScale * (1 + Math.sin(bump));
		this.shine.scaleXY = this._bump;
	}

	/**
	 * It will return the current bump state
	 *
	 * @access public
	 */
	public get bump(): number {
		return this._bump;
	}

	/**
	 * @Override, It will set the button with or without shine
	 *
	 * @access public
	 * @param {boolean} enabled, enable the button or not
	 */
	public set enabled(enabled: boolean) {
		if (this.shine) {
			this.shine.visible = enabled;
		}

		if (!enabled) {
			if (this.disabledTexture) {
				(this as any)._updateTexture(this.disabledTexture);
			}
		} else {
			(this as any)._updateTexture(this.idleTexture);
		}

		(this as any)._updateTextStyle(enabled ? this.textStates.idle : this.textStates.disabled);
		this._enabled = enabled;

		this.interactive = enabled;
		this.buttonMode = enabled;
	}

	/**
	 * Get the enabled state
	 *
	 * @access public
	 */
	public get enabled(): boolean {
		return this._enabled;
	}
}
