import { Container } from "pixi.js";
import type { Graphics } from "pixi.js";
import type { Button } from "@gdk/core-pixi";

import { Time } from "../components/time";
import type { AdaptativeVisualIdentity } from "../../../adaptativeVisualIdentity";
import type { InGameView } from "./inGameView";

export class BottomBar extends Container {
	public static height: number = 90;
	/**
	 * Bottom overlay used to draw time and menu button
	 */
	public bottomOverlay: Graphics = null;
	/**
	 * Quick button used to show DS Quick settings (from the editor)
	 */
	public settingsButton: Button = null;
	/**
	 * Textfield used to draw the current time (from the editor)
	 */
	public time: Time = new Time();

	private _buttons: Button[] = [];
	private _buttonsLeft: Button[] = [];
	private _buttonsRight: Button[] = [];

	/**
	 * Constructor
	 * @param vi the adaptative visual identity reference
	 */
	public constructor(protected vi: AdaptativeVisualIdentity, protected inGameView: InGameView) {
		super();
	}

	/**
	 * handle bottom bar resize
	 */
	public onResize(): void {
		const rendererWidth = this.vi.game.renderer.width / this.vi.game.renderer.resolution;
		const reverseParentScaleRatio = 1 / this.inGameView.scaleXY;
		this.dockingBounds.width = Math.max(
			this.inGameView.dockingBounds.width,
			rendererWidth * reverseParentScaleRatio
		);

		let maxButtonsWidth = 0;
		let maxLeftButtonsWidth = 0;
		let maxRightButtonsWidth = 0;

		this._buttonsLeft.forEach((leftButton) => {
			maxLeftButtonsWidth = Math.max(maxLeftButtonsWidth, leftButton.width);
		});

		this._buttonsRight.forEach((rightBUtton) => {
			maxRightButtonsWidth = Math.max(maxRightButtonsWidth, rightBUtton.width);
		});

		maxButtonsWidth = Math.max(maxLeftButtonsWidth, maxRightButtonsWidth);

		const spacing = -10;

		this._buttonsLeft.forEach((leftButton) => {
			leftButton.docking.x = (maxRightButtonsWidth + spacing) * -0.5;
		});

		this._buttonsRight.forEach((rightButton) => {
			rightButton.docking.x = (maxLeftButtonsWidth + spacing) * 0.5;
		});
	}

	/**
	 * adds a button to the buttom bar
	 * @param button
	 */
	public addButton(button: Button): void {
		this._buttons.push(button);
		if (button.docking.x < 0) {
			this._buttonsLeft.push(button);
		} else if (button.docking.x > 0) {
			this._buttonsRight.push(button);
		}
		this.addChild(button);
	}
}
