import { Tools } from "@gdk/core";
import { AssetStore } from "@gdk/core-pixi";
import type { Container } from "pixi.js";
import type { IProject, GamePixi } from "@gdk/core-pixi";

export interface IBehaviorScript {
	/**
	 * The name of the script
	 */
	name: string;
	/**
	 * The id of the script (automatically set)
	 */
	id: string;
	/**
	 * Compiled code (javascript) from the TypeScript
	 */
	compiledCode: string;
	/**
	 * The TypeScript source code
	 */
	baseCode: string;
}

export interface IBehaviorScriptReference {
	/**
	 * The id of the script to reference
	 */
	id: string;
	/**
	 * Sets wether or not the attached script is active or not
	 */
	active: boolean;
}

export interface IBehaviorCode {
	/**
	 * The collection of distinct scripts available
	 */
	scripts: IBehaviorScript[];
}

// Template
const template = `
var require = SystemJS.amdRequire;
var exports = {};
{{code}}
SystemJS.set("{{id}}", SystemJS.newModule(exports));
`;

interface IScriptExport {
	/**
	 * In case of a default export
	 */
	default?: any;
	/**
	 * Any other class exported as non-default
	 */
	[className: string]: any;
}

export class BehaviorCodeExtension {
	/**
	 * The id of the behavior code extension
	 */
	public static id: string = "behaviorCode";

	/**
	 * Applies the behavior code extension on the given container
	 * @param game the game reference containing the behavior scripts
	 * @param project the project JSON reference
	 * @param container the container having scripts attached
	 */
	public static apply(game: GamePixi, project: IProject, container: Container): void {
		const data = project.metadata[this.id] as IBehaviorCode;
		if (!data) {
			return;
		}

		const references = container.metadata[this.id] as IBehaviorScriptReference[];
		if (!references) {
			return;
		}

		// Apply all references
		const instances = [];

		references.forEach((r) => {
			if (!r.active) {
				return;
			}
			const script = data.scripts.find((s) => s.id === r.id);
			if (!script) {
				return;
			}

			// Inject script
			Tools.injectScript(
				template.replace("{{id}}", script.id).replace("{{code}}", script.compiledCode),
				script.name,
				"scripts"
			);

			// Get constructor then instantiate
			const exports = Tools.getScript<any>(script.id);
			instances.push(new exports.default(container, AssetStore));

			// The container is ready
			container.emit("ready");
		});

		// Register the update event
		game.application.emitter.on("beforeRender", () => container.emit("update"));
	}
}
