import { Container, extras, Texture, Sprite, Point } from "pixi.js";
import { Button, DockingBounds, Docking, Stackable } from "@gdk/core-pixi";

import { DSPopin } from "./popin";
import type { IDSPopinOptions } from "./popin";

const BUTTON_IDLE = "buttons/primary/idle";
const BUTTON_OVER = "buttons/primary/over";
const BUTTON_DISABLED = "buttons/primary/idle";
const LEFT_TOP_RIGHT = 30;
const BOTTOM = 40;
const BUTTONS_SPACE = 80;
const BULLETS_SPACE = 60;
const DEFAULT_COLOR = "#000000";
const BULLET_BUTTONS_HEIGHT = -150;
const BULLET_HEIGHT = -40;
const BULLET = "icons/color/bullet-";
const BULLET_ON = `${BULLET}on`;
const BULLET_OFF = `${BULLET}off`;
const CIRCLE = "backgrounds/white/circle";
const ARROW = "icons/color/arrow";

export interface IDSPaginatedPopinOptions extends IDSPopinOptions {
	slides: Container[];
	buttonColor?: string;
	leftButtonLabel?: string;
	rightButtonLabel?: string;
	onLeft?: () => Promise<void>;
	onRight?: () => Promise<void>;
}

export class DSPaginatedPopin extends DSPopin {
	private leftButton?: Button;
	private rightButton?: Button;
	private bullets: extras.AnimatedSprite[] = [];
	private bulletContainer?: Container;
	private slides: Container[] = [];
	private slideContainer?: Container;
	private previousButton?: Button;
	private nextButton?: Button;

	private _currentSlide?: number;

	public constructor(options: IDSPaginatedPopinOptions) {
		super(options);

		this.content.dockingBounds = new DockingBounds(DSPopin.minWidth, BUTTONS_SPACE);

		if (options.leftButtonLabel && options.rightButtonLabel) {
			this.addButtons(options);
		}

		if (options.slides.length > 1) {
			this.setSlides(options);
		} else {
			this.setContent(options);
		}
	}

	private setContent(options: IDSPaginatedPopinOptions): void {
		this.slideContainer = new Container();
		this.slideContainer.dockingBounds = new DockingBounds(0, 0);
		this.content.addChild(this.slideContainer);

		this.bulletContainer = new Container();

		const slide = options.slides[0];
		if (slide.parent) {
			slide.parent.removeChild(slide);
		}

		this.slides.push(slide);
		this.slideContainer.addChild(slide);

		if (slide.dockingBounds.width > this.slideContainer.dockingBounds.width) {
			this.slideContainer.dockingBounds.width = slide.dockingBounds.width;
		}

		if (slide.dockingBounds.height > this.slideContainer.dockingBounds.height) {
			this.slideContainer.dockingBounds.height = slide.dockingBounds.height;
		}
	}

	private setSlides(options: IDSPaginatedPopinOptions): void {
		this.slideContainer = new Container();
		this.slideContainer.dockingBounds = new DockingBounds(0, 0);
		this.content.addChild(this.slideContainer);

		this.bulletContainer = new Container();

		for (const slide of options.slides) {
			if (slide.parent) {
				slide.parent.removeChild(slide);
			}

			this.slides.push(slide);

			if (slide.dockingBounds.width > this.slideContainer.dockingBounds.width) {
				this.slideContainer.dockingBounds.width = slide.dockingBounds.width;
			}

			if (slide.dockingBounds.height > this.slideContainer.dockingBounds.height) {
				this.slideContainer.dockingBounds.height = slide.dockingBounds.height;
			}

			const bullet = new extras.AnimatedSprite([
				Texture.fromImage(BULLET_OFF),
				Texture.fromImage(BULLET_ON),
			]);

			bullet.anchor.x = 0;
			bullet.anchor.y = 0;
			bullet.x = this.bullets.length * bullet.width;
			this.bulletContainer.addChild(bullet);
			this.bullets.push(bullet);
		}

		this.bulletContainer.dockingBounds = new DockingBounds(
			this.bulletContainer.width,
			this.bulletContainer.height
		);
		this.bulletContainer.docking = new Docking(Docking.bottomCenterHorizontal);
		this.addChild(this.bulletContainer);

		this.setArrows();
	}

	/**
	 * Show the active buttons
	 *
	 * @access public
	 * @param {boolean} withButtons=false, Must the popup have buttons or only the close one
	 */
	public show(withButtons: boolean = false): void {
		if (this.leftButton && this.rightButton) {
			this.leftButton.visible = withButtons;
			this.rightButton.visible = withButtons;
		}

		this.closeButton.visible = !withButtons;

		this.currentSlide = 0;

		super.show();
	}

	/**
	 * Set the current slide, it will also set the correct dot in slide progression
	 *
	 * @access public
	 * @param {number} slide, The slide id for activation
	 */
	public set currentSlide(slide: number) {
		if (this.slides.length > 1) {
			if (slide >= this.slides.length) {
				slide = 0;
			} else if (slide < 0) {
				slide = this.slides.length - 1;
			}

			if (this.bullets.length > 1) {
				this.bullets[this._currentSlide].gotoAndStop(0);
				this.bullets[slide].gotoAndStop(1);
			}

			this._currentSlide = slide;

			if (this.slideContainer.children.length) {
				this.slideContainer.removeChildAt(0);
			}

			if (this.slides.length) {
				this.slideContainer.addChild(this.slides[this.currentSlide]);
			}
		}
	}

	/**
	 * Return the current slide id
	 *
	 * @access public
	 * @returns {number} The current slide id
	 */
	public get currentSlide(): number {
		return this._currentSlide;
	}

	/**
	 * Resize the popin according to content
	 *
	 * @access public
	 */
	public resizePopin(): void {
		this.content.dockingBounds.width = this.slideContainer.dockingBounds.width;
		if (this.leftButton && this.leftButton.visible) {
			this.content.dockingBounds.height = this.slideContainer.dockingBounds.height;

			if (this.bullets.length > 1) {
				this.bulletContainer.docking.y = BULLET_BUTTONS_HEIGHT;
			}
		} else {
			this.content.dockingBounds.height = this.slideContainer.dockingBounds.height;

			if (this.bullets.length > 1) {
				this.bulletContainer.docking.y = BULLET_HEIGHT;
			}
		}

		// this.buttonStack.y = this.content.dockingBounds.height - this.buttonStack.height * .5 + 10;

		if (this.bullets.length > 1) {
			this.content.dockingBounds.height += BULLETS_SPACE;
		}

		if (this.content.dockingBounds.width < DSPopin.minWidth) {
			this.content.dockingBounds.width = DSPopin.minWidth;
		}

		super.resizePopin();

		// this.buttonStack.x = (this.content.dockingBounds.width - this.buttonStack.width) * .5;
	}

	private setArrows(): void {
		for (const key of ["previousButton", "nextButton"]) {
			this[key] = new Button({
				idleTexture: CIRCLE,
				overTexture: CIRCLE,
				clickedTexture: CIRCLE,
				disabledTexture: CIRCLE,
			});
			this[key].isNineSlice = true;
			this[key].scaleXY = 1.4;
			this[key].dockingBounds = new DockingBounds(this[key].width, this[key].height);

			if (key === "previousButton") {
				this[key].docking = new Docking(Docking.centerVerticalLeft, 0, 20, false);
			} else {
				this[key].docking = new Docking(Docking.centerVerticalRight, -0, 20, false);
				this[key].scaleX *= -1;
			}
			this[key].pivot = new Point(50, 45);

			const arrow = Sprite.fromFrame(ARROW);
			arrow.x = 50;
			arrow.y = 45;
			this[key].addChild(arrow);
			this.addChild(this[key]);
		}

		this._currentSlide = 0;
		this.currentSlide = 0;

		this.previousButton.on("pointertap", () => {
			this.currentSlide--;
		});

		this.nextButton.on("pointertap", () => {
			this.currentSlide++;
		});
	}

	private addButtons(options: IDSPaginatedPopinOptions): void {
		for (const key of ["leftButton", "rightButton"]) {
			this[key] = new Button(
				{
					idleTexture: BUTTON_IDLE,
					overTexture: BUTTON_OVER,
					clickedTexture: BUTTON_IDLE,
					disabledTexture: BUTTON_DISABLED,
				},
				{
					default: {
						fontName: "OpenSans-SemiBold",
						fontSize: DSPopin.buttonFontSize,
						fill: options.buttonColor ? options.buttonColor : DEFAULT_COLOR,
					},
				},
				options[`${key}Label`]
			);
			// this[key].docking = new Docking(key === "leftButton" ? Docking.bottomLeft : Docking.bottomRight);

			this[key].isNineSlice = true;
			this[key].backgroundNineSlice.leftWidth = LEFT_TOP_RIGHT;
			this[key].backgroundNineSlice.topHeight = LEFT_TOP_RIGHT;
			this[key].backgroundNineSlice.rightWidth = LEFT_TOP_RIGHT;
			this[key].backgroundNineSlice.bottomHeight = BOTTOM;

			this[key].nineSliceMinSize.x = 320;
			this[key].nineSliceMinSize.y = 70;

			this[key].nineSlicePadding.x = 20;
			this[key].nineSlicePadding.y = 21;
			this[key].nineSlicePadding.width = 20;
			this[key].nineSlicePadding.height = 22;

			this[key].on("pointertap", () => {
				this.executeButtonCallback(key === "leftButton" ? options.onLeft : options.onRight);
			});

			this.buttonStack.push(this[key], false);
			// this.content.addChild(this[key]);
			// Useful for resizing a niceSlice Button
			this[key].label = this[key].label;
		}
	}

	protected activateButtons(activate: boolean): void {
		for (const key of ["leftButton", "rightButton", "closeButton"]) {
			if (this[key]) {
				this[key].enabled = activate;
			}
		}
	}
}
