import { Container, Sprite, Texture } from "pixi.js";
import {
	isLeftPointerTap,
	Docking,
	ContainerExporter,
	TextureExporter,
	TextureImporter,
	Popup,
	Button,
	ISwipeEventData,
	DockingBounds,
	ContainerImporter,
} from "@gdk/core-pixi";
import { Tools } from "@gdk/core";
import type { IPopupStyle, IButtonStyle } from "@gdk/core-pixi";
import type { interaction, Point } from "pixi.js";

import type { Slide } from "./slide";
import type { BaseSlide } from "./baseSlide";

export interface IRulesStyle {
	popup: IPopupStyle | Popup;
	nextButton: IButtonStyle | Button;
	previousButton: IButtonStyle | Button;
	bulletPoint: {
		idle: string | Texture;
		active: string | Texture;
	};
}

export class Rules extends Container {
	// Public members
	public popup: Popup;
	public nextButton: Button;
	public previousButton: Button;

	public bullet: Container;
	public loop: boolean = false;

	public idleBulletTexture: Texture;
	public activeBulletTexture: Texture;

	public onClose: () => void;
	public onNext: () => void;
	public onPrevious: () => void;
	public onChangeSlide: () => void;

	// Protected members
	protected _slides: Slide[] = [];
	protected _currentSlide: Slide = null;

	protected config: IRulesStyle;

	protected currentBullet: Sprite = null;

	protected touchStartTime: number;
	protected touchStartPosition: Point;
	protected touchEndPosition: Point;
	protected containerToListen: Container = null;

	/**
	 * Constructor
	 */
	public constructor(config: IRulesStyle) {
		// PIXI.Container
		super();

		// Popup
		if (config.popup instanceof Popup) {
			this.popup = config.popup;
		} else {
			this.popup = new Popup(config.popup);
		}

		// tracking: closeButton must always be named the same way
		this.popup.closeButton.setName("rules:close");

		this.popup.interactive = true;
		this.popup.doNotExport = true;
		this.popup.docking = new Docking(Docking.centerAll);

		// Next button
		if (config.nextButton instanceof Button) {
			this.nextButton = config.nextButton;
		} else {
			this.nextButton = new Button(config.nextButton);
		}

		this.nextButton.doNotExport = true;
		this.nextButton.docking = new Docking(Docking.bottomRight);

		// tracking: nextButon must always be named the same way
		this.nextButton.setName("rules:next");

		// Previous button
		if (config.previousButton instanceof Button) {
			this.previousButton = config.previousButton;
		} else {
			this.previousButton = new Button(config.previousButton);
		}

		this.previousButton.doNotExport = true;
		this.previousButton.docking = new Docking(Docking.bottomLeft);
		this.previousButton.scaleX = -1;

		// tracking: nextButon must always be named the same way
		this.previousButton.setName("rules:previous");

		// Bullet
		this.idleBulletTexture =
			typeof config.bulletPoint.idle === "string"
				? Texture.fromFrame(config.bulletPoint.idle)
				: config.bulletPoint.idle;
		this.activeBulletTexture =
			typeof config.bulletPoint.active === "string"
				? Texture.fromFrame(config.bulletPoint.active)
				: config.bulletPoint.active;

		this.bullet = new Container();
		this.bullet.name = "Bullets";
		this.bullet.doNotExport = true;
		this.bullet.docking = new Docking(Docking.bottomCenterHorizontal);
		this.bullet.dockingBounds = new DockingBounds(0, this.idleBulletTexture.height);
		this.bullet.docking.y = -48; // Default vertical position according to the guidelines. Can be overrided

		// Children
		this.addChild(this.popup);
		this.addChild(this.nextButton);
		this.addChild(this.previousButton);
		this.addChild(this.bullet);

		// Misc.
		this.config = config;
		this.dockingBounds = new DockingBounds(
			this.popup.background.width,
			this.popup.background.height
		);

		// Finish
		this._bindEvents();
	}

	/**
	 * Returns the slides
	 */
	public get slides(): Slide[] {
		return this._slides;
	}

	/**
	 * Returns the current slide
	 */
	public get currentSlide(): Slide {
		return this._currentSlide;
	}

	/**
	 * Sets the current slide
	 */
	public set currentSlide(slide: Slide) {
		if (slide === this._currentSlide) {
			return;
		}

		if (this._currentSlide) {
			this._currentSlide.onHide(this);
		}

		this._currentSlide = slide;
		this._currentSlide.onShow(this);

		// Update bullet
		this.currentBullet.texture = this.idleBulletTexture;

		const index = this._slides.indexOf(this._currentSlide);

		this.currentBullet = this.bullet.children[index] as Sprite;
		this.currentBullet.texture = this.activeBulletTexture;

		// Next and previous buttons
		this.previousButton.visible = index > 0 && this.slides.length > 1;
		this.nextButton.visible = index < this.slides.length - 1 && this.slides.length > 1;
	}

	/**
	 * Adds a slide to the rules popup
	 * @param slide the slide to add (can be a custom slide extending Slide)
	 */
	public addSlide(slide: Slide): void {
		if (slide.configure) {
			slide.configure(this);
		}

		this.addChild(slide);
		this._slides.push(slide);

		if (!this._currentSlide) {
			this._currentSlide = slide;
		}

		if (this._slides.length >= 1) {
			slide.visible = false;
		}

		// Add a bullet when precised
		if (slide.hasBullet) {
			this._addBullet();
		}

		// Always add close and next buttons in front of all slides.
		this.addChild(this.previousButton);
		this.addChild(this.nextButton);
	}

	/**
	 * Removes the given slide from the rules popup
	 * @param slide the slide to remove
	 */
	public removeSlide(slide: Slide): void {
		// Destroy
		if (slide.onDestroy) {
			slide.onDestroy(this);
		}

		this.removeChild(slide);

		const index = this._slides.indexOf(slide);
		if (index !== -1) {
			this._slides.splice(index, 1);
		}

		if (slide === this._currentSlide) {
			this._currentSlide = this._slides[index > this._slides.length ? this._slides.length : index];

			if (this._currentSlide) {
				this._currentSlide.onShow(this);
			}
		}

		// Remove bullet
		if (slide.hasBullet && this.bullet.children.length > 0) {
			this.bullet.children[0].destroy();
			this.bullet.children.forEach((c: Sprite, i) => (c.x = c.texture.width * i));
		}
	}

	/**
	 * Returns a slide identified by the given name
	 * @param name the name of the slide to find
	 */
	public getSlideByName(name: string): Slide {
		for (const s of this.slides) {
			if (s.name === name) {
				return s;
			}
		}

		return null;
	}

	/**
	 * Removes all the slides from the rules component
	 */
	public removeAllSlides(): void {
		while (this._slides.length > 0) {
			this.removeSlide(this._slides[0]);
		}

		this._currentSlide = null;

		// Some slides can hide the close button. Force is to visible
		this.popup.closeButton.visible = true;
	}

	/**
	 * Resets the rules component (rollback to first slide)
	 */
	public reset(): void {
		// Current slide
		if (this._currentSlide) {
			this._currentSlide.onHide(this);
		}

		this._currentSlide = this._slides[0];
		this._currentSlide.onShow(this);

		// Buttons visibility
		if (this._slides.length > 1) {
			this.previousButton.visible = this.loop;
			this.nextButton.visible = true;
		} else {
			this.previousButton.visible = false;
			this.nextButton.visible = false;
		}

		// Bullets
		if (this.bullet.children.length > 0) {
			this.bullet.children.forEach((c: Sprite) => (c.texture = this.idleBulletTexture));
			this.currentBullet = this.bullet.children[0] as Sprite;
			this.currentBullet.texture = this.activeBulletTexture;
		}

		// Reset closeButton to initial texture
		// (on mobile, button keeps hoverTexture when multiple opening/closing of the popup)
		this.popup.closeButton.enabled = true;

		// Always add close and next buttons in front of all slides.
		this.addChild(this.previousButton);
		this.addChild(this.nextButton);
	}

	/**
	 * Show the popup with the added slides
	 * @param loopMode a tag to set the loop mode through slides
	 */
	public show(loopMode: boolean = false): void {
		this.alpha = 1;
		this.loop = loopMode;
		this.reset();
	}

	/**
	 * Switch to the specified slide by its index and returns the instance of the next slide
	 * @param index The index slide
	 * @return the specified slide
	 */
	public changeSlideByIndex(index: number): Slide {
		const currentIndex = this._slides.indexOf(this._currentSlide);
		const isNext = currentIndex + 1 === index;
		const isPrevious = currentIndex - 1 === index;

		if (index < 0) {
			if (!this.loop) {
				return;
			}

			index = this._slides.length - ((Math.abs(index) - 1) % this._slides.length);
		}

		if (index >= this._slides.length) {
			if (!this.loop) {
				return;
			}

			index = index % this._slides.length;
		}

		this._currentSlide.onHide(this);
		this.currentBullet.texture = this.idleBulletTexture;
		this._currentSlide = this._slides[index];

		// Update bullet, next and pervious buttons visibility according to slide index and loop state
		this.previousButton.visible = index > 0 || this.loop;
		this.nextButton.visible = index < this._slides.length - 1 || this.loop;

		if (this._currentSlide.hasBullet) {
			this._updateBulletsTexture(index);
		} else {
			this.bullet.visible = false;
		}

		this._currentSlide.onShow(this);
		if (this.onChangeSlide) {
			this.onChangeSlide();
		}

		if (isNext && this.onNext) {
			this.onNext();
		}

		if (isPrevious && this.onPrevious) {
			this.onPrevious();
		}

		return this._currentSlide;
	}

	/**
	 * Switches to the next slide and returns the instance of the next slide
	 * @return: the next slide
	 */
	public next(): Slide {
		const index = this._slides.indexOf(this._currentSlide) + 1;

		return this.changeSlideByIndex(index);
	}

	/**
	 * Switches to the previous slides and returns the instance of the previous slide
	 * @return: the next slide
	 */
	public previous(): Slide {
		const index = this._slides.indexOf(this._currentSlide) - 1;

		return this.changeSlideByIndex(index);
	}

	/**
	 * Closes the rules popup
	 */
	public close(): void {
		if (this.onClose) {
			this.onClose();
		}
	}

	/**
	 * Add a bullet point
	 */
	private _addBullet(): void {
		const bulletChild = new Sprite(this.idleBulletTexture);
		this.bullet.addChild(bulletChild);
		this.bullet.children.forEach((c: Sprite, index) => (c.x = c.texture.width * index));
		this.bullet.dockingBounds.width = this.bullet.children.length * this.idleBulletTexture.width;

		if (!this.currentBullet) {
			this.currentBullet = bulletChild;
		}

		// Configure bullet visibility when showing one slide
		this.bullet.visible = this._slides.length > 1;
		this.bullet.pivot.set(0, bulletChild.height / 2);
	}

	/**
	 * show bullets while updating its texture
	 * @param currentIndex the index of the bullet to be activated
	 */
	private _updateBulletsTexture(currentIndex: number): void {
		if (this.bullet.children.length > 1) {
			this.bullet.visible = true;
			this.currentBullet = this.bullet.children[currentIndex] as Sprite;
			this.currentBullet.texture = this.activeBulletTexture;
		}
	}

	// Binds the events
	private _bindEvents(): void {
		// Close popup
		this.popup.closeButton.on("pointertap", (e: interaction.InteractionEvent) => {
			if (isLeftPointerTap(e)) {
				this.close();
			}
		});

		// Next & previous buttons
		this.nextButton.on("pointertap", (e: interaction.InteractionEvent) => {
			if (isLeftPointerTap(e)) {
				this.next();
			}
		});
		this.previousButton.on("pointertap", (e: interaction.InteractionEvent) => {
			if (isLeftPointerTap(e)) {
				this.previous();
			}
		});

		this.addSwipeListenerToContainer(this.popup);
	}

	/**
	 * Listen to touchstart event
	 * @param ev: The interaction event data
	 */
	private _onTouchStart = (ev: interaction.InteractionEvent): void => {
		this.touchStartPosition = ev.data.getLocalPosition(this);
		this.touchStartTime = Date.now();
	};

	/**
	 * Listen to touchend event
	 * @param ev: The interaction event data
	 */
	private _onTouchEnd = (ev: interaction.InteractionEvent): void => {
		if (this.touchStartPosition) {
			// DraggEvent disturb "touchstart" propagation, sometimes start is null
			this.touchEndPosition = ev.data.getLocalPosition(this);

			const dx = Math.abs(this.touchStartPosition.x - this.touchEndPosition.x);
			const dt = Date.now() - this.touchStartTime;
			const pixelsPerSecond = (1000 * dx) / dt;

			if (pixelsPerSecond > 500) {
				if (this.touchStartPosition.x > this.touchEndPosition.x) {
					this.next();
				} else {
					this.previous();
				}
			}
		}
	};

	/**
	 * Add a container to listen for swipe event
	 * @param nextContainerToListen: The container to listen
	 */
	public addSwipeListenerToContainer(nextContainerToListen: Container = null): void {
		if (this.containerToListen) {
			this.containerToListen.removeListener("touchstart", this._onTouchStart);
			this.containerToListen.removeListener("touchend", this._onTouchEnd);
		}

		this.containerToListen = nextContainerToListen || this.popup;
		this.containerToListen.addListener("touchstart", this._onTouchStart);
		this.containerToListen.addListener("touchend", this._onTouchEnd);
	}

	/**
	 * Export the rules component
	 */
	public export(): any {
		// Configure slides
		const slides = [];
		this.slides.forEach((s) => {
			// Export slide if supported
			if (s.export) {
				s.doNotExport = false;
				slides.push(s.export());
			}

			// Stop export
			s.doNotExport = true;
		});

		// Configure bullets
		this.bullet.children.forEach((c) => (c.doNotExport = true));

		return Tools.assign(ContainerExporter.exportContainer(this), {
			type: "rules.Rules",

			popup: this.popup.export(),
			nextButton: this.nextButton.export(),
			previousButton: this.previousButton.export(),
			bulletPoint: {
				config: ContainerExporter.exportContainer(this.bullet, true),
				idle: TextureExporter.exportTexture(this.idleBulletTexture),
				active: TextureExporter.exportTexture(this.activeBulletTexture),
			},

			slides,
		});
	}

	/**
	 * Parses the rules component
	 * @param config the serialized data
	 * @param baseUrl the base url of the loader
	 */
	public static parse(config: any, baseUrl: string, rules?: Rules): Rules {
		// Rules
		if (!rules) {
			rules = new Rules({
				popup: new Popup(),
				nextButton: new Button({}, config.nextButton.textStyle, config.nextButton.text),
				previousButton: new Button({}, config.previousButton.textStyle, config.previousButton.text),
				bulletPoint: {
					idle: Texture.WHITE,
					active: Texture.WHITE,
				},
			});
		}

		Popup.parse(config.popup, baseUrl, rules.popup);

		Button.parse(config.nextButton, baseUrl, rules.nextButton);
		Button.parse(config.previousButton, baseUrl, rules.previousButton);

		rules.idleBulletTexture = TextureImporter.importTexture(
			config.bulletPoint.idle,
			baseUrl,
			rules,
			"idleBulletTexture"
		);
		rules.activeBulletTexture = TextureImporter.importTexture(
			config.bulletPoint.active,
			baseUrl,
			rules,
			"activeBulletTexture"
		);

		if (config.bulletPoint.config) {
			ContainerImporter.importContainer(config.bulletPoint.config, baseUrl, rules.bullet);
		}

		// Docking bounds
		rules.dockingBounds = rules.popup.dockingBounds.clone();

		// Slides
		config.slides.forEach((s) => {
			const slide = ContainerImporter.import(s, baseUrl) as BaseSlide;
			rules.addSlide(slide);
		});

		// Reset
		if (rules.slides.length > 0) {
			rules.reset();
		}

		return rules;
	}
}
