import { ContainerUtils, Docking } from "@gdk/core-pixi";
import { EOrientation } from "@gdk/core";
import { TimelineMax, TweenMax } from "gsap";
import { Point, utils } from "pixi.js";
import { EventEmitter } from "events";
import type { Container, interaction } from "pixi.js";
import type { Button } from "@gdk/core-pixi";

import { TweenUtils } from "./tweenUtils";
import type { AbstractGame } from "./abstractGame";

export interface ICarouselData {
	/**
	 * The game
	 */
	game: AbstractGame;

	/**
	 * The parent container of the carousel
	 */
	container: Container;

	/**
	 * The previous arrow button
	 */
	previousArrow?: Button;

	/**
	 * The next arrow button
	 */
	nextArrow?: Button;
}

export interface ICarouselSettings {
	/**
	 * Duration of animation
	 */
	animationDuration?: number;

	/**
	 * The offset between cards in px
	 */
	offset?: number;

	/**
	 * The orientation of the carousel
	 */
	orientation?: EOrientation;

	/**
	 * The list of scales for cards
	 */
	scales?: number[];

	/**
	 * The fixed docking position for all cards (x axis for Orientation Landscape and y axis for Portrait orientation)
	 */
	fixedDocking?: Point[];

	/**
	 * The fixed docking if there is only 2 cards
	 */
	fixedDocking2Cards?: Point[];

	/**
	 * The max time during drag event can be active
	 */
	maxDragTime?: number;

	/**
	 * The number of pixel required to be considered has a swipe
	 */
	dragSensibility?: number;

	/**
	 * If true, arrows won't be positionned automaticaly
	 */
	useCustomArrowsPositions?: boolean;
}

const defaultCarouselSettings: ICarouselSettings = {
	offset: 0,
	maxDragTime: 400,
	dragSensibility: 10,
	animationDuration: 0.5,
	scales: [1, 0.75, 0.6, 0.5],
	fixedDocking: [new Point(0, 0), new Point(176, 128), new Point(300, 226), new Point(500, 324)],
	fixedDocking2Cards: [new Point(232, 232), new Point(-232, -232)],
};

export class Carousel extends utils.EventEmitter {
	/* ATTRIBUTES */

	/**
	 * The game
	 */
	protected game: AbstractGame;

	/**
	 * The parent container
	 */
	protected container: Container;

	/**
	 * List of cards
	 */
	protected cards: Container[];

	/**
	 * Button for previous arrow
	 */
	public previousArrow: Button;

	/**
	 * Button for next arrow
	 */
	public nextArrow: Button;

	/**
	 * The active card
	 */
	protected activeCard: Container;

	/**
	 * The previous card
	 */
	protected previousActiveCard: Container;

	/**
	 * The settings of the carousel
	 */
	protected settings: ICarouselSettings;

	/**
	 * The timestamp of the drag start
	 */
	protected dragStartTime: number = 0;

	/**
	 * The mouse position when the drag has started
	 */
	protected dragStartPosition: Point = new Point();

	/**
	 * Check if the player is currently dragging the carousel
	 */
	public isDragging: boolean = false;

	/**
	 * card sequence of z-order where you cycle from center to right, then left to center without wrong depth
	 */
	public correspondingIndex: number[];

	/**
	 * Check if the carousel is active
	 */
	protected isActive: boolean = false;

	/* LIFECYCLE */

	/**
	 * @constructor
	 * @param cards: List of cards containers
	 * @param leftArrow: Button for Left Arrow
	 * @param rightArrow: Button for Right Arrow
	 */
	public constructor(data: ICarouselData, settings: ICarouselSettings = {}) {
		super();

		this.game = data.game;
		this.container = data.container;
		this.previousArrow = data.previousArrow;
		this.nextArrow = data.nextArrow;

		// cards ordered by childIndex
		const visibleChildren: Container[] = (data.container.children as Container[]).filter(
			(card: Container) => card.visible
		);

		// cards childIndex, ordered by position from center to right then left to center
		this.correspondingIndex = [];
		for (let i = 0; i < visibleChildren.length; i++) {
			const value = visibleChildren.length - 1 - i;
			i % 2 === 0 ? this.correspondingIndex.push(value) : this.correspondingIndex.unshift(value);
		}
		const splicingNeeded = Math.ceil(visibleChildren.length / 2);
		for (let i = 0; i < splicingNeeded; i++) {
			const temp = this.correspondingIndex.pop();
			this.correspondingIndex.splice(0, 0, temp);
		}

		this.cards = [];
		for (let i = 0; i < visibleChildren.length; i++) {
			this.cards[i] = visibleChildren[this.correspondingIndex[i]];
		}

		this.settings = {
			...defaultCarouselSettings,
			orientation: this.game.settings.orientation,
			...settings,
		};

		if (this.previousArrow) {
			this.previousArrow.on("pointertap", () => {
				this.previousCard();
			});
		}

		if (this.nextArrow) {
			this.nextArrow.on("pointertap", () => {
				this.nextCard();
			});
		}

		// Init carousel
		this.setButtonActive(this.isCarousel());
		this.previousActiveCard = this.cards[0];
		this.updateCardPositions(false);

		// Init drag
		this.container.interactive = true;
		this.container.on("pointerdown", this.onDragStart.bind(this));
		this.container.on("pointerup", this.onDragStop.bind(this));
		this.container.on("pointerupoutside", this.onDragStop.bind(this));

		// Init scroll
		if (typeof window !== "undefined") {
			window.addEventListener("wheel", this.onScroll.bind(this));
			window.addEventListener("mousewheel", this.onScroll.bind(this));
			window.addEventListener("DOMMouseScroll", this.onScroll.bind(this));
		}
	}

	/* METHODS */

	/**
	 * Get the maximum dimension depending of cards
	 */
	protected getCardDimension(): Point {
		let width = 0;
		let height = 0;

		for (const card of this.cards) {
			width = Math.max(width, card.width * (1 / card.scaleX));
			height = Math.max(height, card.height * (1 / card.scaleY));
		}

		return new Point(width, height);
	}

	/**
	 * Get the offset value between each cards relative to the container dimension
	 */
	protected getCardsOffset(): number {
		const cardDimenion: Point = this.getCardDimension();
		const containerDimension: Point = ContainerUtils.getContainerDimensions(
			this.game.renderer,
			this.container
		);
		const cardOrientationSize: number =
			(this.settings.orientation === EOrientation.Landscape && cardDimenion.x) || cardDimenion.y;
		const containerOrientationSize: number =
			(this.settings.orientation === EOrientation.Landscape && containerDimension.x) ||
			containerDimension.y;

		if (this.cards.length === 2) {
			return Math.max(20, (containerOrientationSize - cardOrientationSize * 2) / 2);
		}

		return Math.max(1, (containerOrientationSize - cardOrientationSize) / this.cards.length);
	}

	/**
	 * Sort cards with children to prevent card to nlap other
	 * @param reverseLastCard: True if you want that the right card will be the first drawed card
	 */
	protected sortCards(reverseLastCard: boolean = false): void {
		if (this.cards.length <= 2) {
			return;
		}

		const activeCardIndex: number = this.cards.findIndex(
			(card: Container) => card === this.activeCard
		);
		const nextCards = this.cards
			.slice(activeCardIndex)
			.concat(this.cards.slice(0, activeCardIndex));
		this.cards = nextCards;

		const odds: Container[] = [];
		const evens: Container[] = [];
		const separationIndex: number = Math.round(this.cards.length / 2);
		const computedCards: Container[] = [
			...this.cards.slice(0, separationIndex),
			...this.cards.slice(separationIndex).reverse(),
		];

		for (let i = 0; i < computedCards.length; i++) {
			if (i === 0) {
				continue;
			} else if (i % 2 === 0) {
				evens.push(computedCards[i]);
			} else {
				odds.push(computedCards[i]);
			}
		}

		const orders = [...evens, ...odds, computedCards[0]];

		if (reverseLastCard) {
			orders.splice(0, 2, ...orders.slice(0, 2).reverse());
		}

		orders.forEach((card: Container) => this.container.addChild(card));
	}

	/**
	 * Convert an array of indices to the full array for each cards (useful to sort cards by peer)
	 * @param array: The array to compute
	 * @param isNegative: If true, the left part of the array will be negative
	 * @returns The computed array
	 */
	public computeArray(ar: number[], isNegative: boolean = false): number[] {
		const computedArray = ar.slice();

		// shorten the base array size if needed
		const sizeDiff = computedArray.length * 2 - 1 - this.cards.length;
		const nbrValueToDelete = Math.floor(sizeDiff / 2);
		computedArray.splice(computedArray.length - nbrValueToDelete, nbrValueToDelete);

		// make the second part of the array with mirrored values
		let reversedArray = computedArray.slice().reverse();
		reversedArray.pop();
		if (isNegative) {
			reversedArray = reversedArray.map((r) => (r = -r));
		}

		// even number of card mean the left side is shorter
		if (this.cards.length % 2 === 0) {
			computedArray.pop();
		}

		return computedArray.concat(reversedArray);
	}

	/**
	 * Get all card positions based on fixed docking positions
	 * @returns
	 */
	protected getCardPositions(): number[] {
		if (this.cards.length <= 2 && Array.isArray(this.settings.fixedDocking2Cards)) {
			return this.settings.fixedDocking2Cards.map((point: Point) =>
				this.settings.orientation === EOrientation.Landscape ? point.x : point.y
			);
		}

		if (!Array.isArray(this.settings.fixedDocking)) {
			throw new Error("[carousel] fixedDocking must not be an array of Point and not be empty");
		}

		return this.computeArray(
			this.settings.fixedDocking.map((fixedDocking: Point) => {
				return this.settings.orientation === EOrientation.Landscape
					? fixedDocking.x
					: fixedDocking.y;
			}),
			true
		);
	}

	/**
	 * When scroll has been detected
	 * @param e: The window event
	 */
	protected onScroll(e: Event): void {
		if (!this.container.visible) {
			return;
		}

		let delta: number = 0;

		if (!e) {
			e = window.event;
		}

		if (event["wheelDelta"]) {
			// IE/opera
			delta = event["wheelDelta"] / 120;
		} else if (event["detail"]) {
			// mozilla
			delta -= event["detail"] / 3;
		}

		if (delta) {
			if (delta > 0) {
				this.nextCard();
			} else {
				this.previousCard();
			}
		}
	}

	/**
	 * @event
	 * When the drag has started
	 * @param e: The interaction event
	 * @returns
	 */
	protected onDragStart(e: interaction.InteractionEvent): void {
		this.isDragging = true;
		this.dragStartTime = Date.now();
		this.dragStartPosition = new Point(e.data.global.x, e.data.global.y);
	}

	/**
	 * @event
	 * When drag has stopped
	 * @param e: The interaction event
	 * @returns
	 */
	protected onDragStop(e: interaction.InteractionEvent): void {
		if (!this.isDragging) {
			return;
		}

		const point: Point = new Point(e.data.global.x, e.data.global.y);
		const diff: Point = new Point(
			point.x - this.dragStartPosition.x,
			point.y - this.dragStartPosition.y
		);

		if (
			this.settings.orientation === EOrientation.Landscape &&
			Math.abs(diff.x) >= this.settings.dragSensibility
		) {
			if (point.x > this.dragStartPosition.x) {
				this.previousCard();
			} else {
				this.nextCard();
			}
		} else if (
			this.settings.orientation === EOrientation.Portrait &&
			Math.abs(diff.y) >= this.settings.dragSensibility
		) {
			if (point.y > this.dragStartPosition.y) {
				this.previousCard();
			} else {
				this.nextCard();
			}
		}

		this.isDragging = false;
	}

	/**
	 * Update positions of arrows relative to the screen and orientation
	 */
	protected updateArrowsPositions(): void {
		const isLandscape: boolean = this.settings.orientation === EOrientation.Landscape;
		const rotation: number = (isLandscape ? 0 : 90) * (Math.PI / 180);

		if (this.previousArrow) {
			this.previousArrow.rotation = rotation;
			this.previousArrow.docking = isLandscape
				? new Docking(Docking.bottomLeft, 0, -40)
				: new Docking(Docking.bottomRight, 0, -102);
		}

		if (this.nextArrow) {
			this.nextArrow.rotation = rotation;
			this.nextArrow.docking = isLandscape
				? new Docking(Docking.bottomRight, 0, -40)
				: new Docking(Docking.bottomRight, 0, -40);
		}
	}

	/* PUBLIC */

	/**
	 * Set a new card as active
	 * @param nextCard: The next card to active
	 * @param disableAnimation: True if animation should be disabled
	 */
	public async setActiveCard(
		nextCard: Container,
		disableAnimation: boolean = false
	): Promise<void> {
		if (!this.cards.find((card) => card === nextCard) || this.activeCard === nextCard) {
			return;
		}

		this.previousActiveCard = this.cards[0];

		const index = this.cards.findIndex((c) => c === nextCard);
		for (let i = 0; i < index; i++) {
			const temp = this.cards.shift();
			this.cards[this.cards.length] = temp;
		}

		const isReverseRotation = (this.cards.length - index) % 2 === 1;
		await this.updateCardPositions(disableAnimation, isReverseRotation);
	}

	/**
	 * Update positions of cards
	 * @param disableAnimation: If true, cards will be updated without animation
	 * @param reverseRotation: True if the default rotation should be reversed for better animation
	 */
	public async updateCardPositions(
		disableAnimation: boolean = false,
		reverseRotation: boolean = false
	): Promise<void> {
		const timeline: TimelineMax = new TimelineMax();
		const positions: number[] = this.getCardPositions();
		const scales: number[] =
			this.cards.length > 2
				? this.computeArray(this.settings.scales)
				: new Array(this.cards.length).fill(0.8);

		this.activeCard = this.cards[0];
		this.setButtonActive(false);

		if (this.container.worldVisible) {
			this.emit("beforeChangeCard", this.activeCard, this.previousActiveCard);
		}

		for (let i = 0; i < this.cards.length; i++) {
			const card = this.cards[i];
			const scaleXY: number = scales[i];
			const dockingX: number =
				this.settings.orientation === EOrientation.Landscape ? positions[i] : card.docking.x;
			const dockingY: number =
				this.settings.orientation === EOrientation.Portrait ? positions[i] : card.docking.y;

			// reorder cards in the container hierarchy (without animation)
			this.container.addChild(this.cards[this.correspondingIndex.findIndex((c) => c === i)]);

			if (disableAnimation) {
				card.scaleXY = scaleXY;
				card.docking.x = dockingX;
				card.docking.y = dockingY;
			} else {
				timeline.add(
					TweenMax.to(card.docking, this.settings.animationDuration, { x: dockingX, y: dockingY }),
					0
				);
				timeline.add(TweenMax.to(card, this.settings.animationDuration, { scaleXY }), 0);
			}
		}

		// put the card that is moving the most behind the others
		if (this.cards.length % 2 === 1) {
			if (reverseRotation) {
				this.container.setChildIndex(
					this.cards[this.correspondingIndex.findIndex((c) => c === 1)],
					0
				);
			}
		} else if (!reverseRotation) {
			const farRightBackCard = Math.floor((this.cards.length - 1) / 2);
			this.container.setChildIndex(this.cards[farRightBackCard], 0);
		}

		if (!disableAnimation) {
			await TweenUtils.playTween(timeline);
		}

		this.setButtonActive(this.isCarousel());

		if (this.container.worldVisible) {
			this.emit("changeCard", this.activeCard, this.previousActiveCard);
		}

		if (!this.settings.useCustomArrowsPositions) {
			this.updateArrowsPositions();
		}

		this.emit("cardPositionsUpdated");
	}

	/**
	 * Active or deactive buttons Left/Right arrow
	 * @param active: True if buttons must be active
	 */
	public setButtonActive(active: boolean = true): void {
		if (this.getCards().length <= 2) {
			return;
		}

		if (this.previousArrow) {
			this.previousArrow.enabled = this.previousArrow.visible = active;
		}

		if (this.nextArrow) {
			this.nextArrow.enabled = this.nextArrow.visible = active;
		}
	}

	/**
	 * Switch to previous card
	 * @param disableAnimation: True if the changement should be instant
	 */
	public previousCard(disableAnimation: boolean = false): Promise<void> {
		if (!this.isCarousel()) {
			return;
		}

		this.previousActiveCard = this.cards[0];
		const temp = this.cards.pop();
		this.cards.splice(0, 0, temp);
		this.updateCardPositions(disableAnimation, true);
	}

	/**
	 * Switch to next card
	 * @param disableAnimation: True if the changement shoud be instant
	 */
	public nextCard(disableAnimation: boolean = false): Promise<void> {
		if (!this.isCarousel()) {
			return;
		}

		this.previousActiveCard = this.cards[0];
		const temp = this.cards.shift();
		this.cards[this.cards.length] = temp;
		this.updateCardPositions(disableAnimation);
	}

	/**
	 * Kill the carousel and remove all events
	 */
	public kill(): void {
		if (typeof window !== "undefined") {
			window.removeEventListener("wheel", this.onScroll.bind(this));
			window.removeEventListener("mousewheel", this.onScroll.bind(this));
			window.removeEventListener("DOMMouseScroll", this.onScroll.bind(this));
		}
	}

	/**
	 * Sets wether or not the carousel is active.
	 * @param isActive defines wether or not the carousel is active.
	 */
	public setActive(isActive: boolean): boolean {
		return (this.isActive = isActive);
	}

	/* GETTERS */

	/**
	 * Know if arrows should be active, in relation with the number of cards present in carousel
	 */
	public isCarousel(): boolean {
		return this.cards.length > 2;
	}

	/**
	 * Get the active card
	 * @returns
	 */
	public getActiveCard(): Container {
		return this.activeCard;
	}

	/**
	 * Get list of cards
	 * @returns
	 */
	public getCards(): Container[] {
		return this.cards;
	}

	/**
	 * Check if the carousel is in simple mode (without wheel)
	 * @returns True if the carousel is in simple mode
	 */
	public isSimpleMode(): boolean {
		return this.cards.length <= 2;
	}
}
