import { TextField, EAlign, DockingBounds } from "@gdk/core-pixi";
import * as i18n from "i18next";

import { DSTwoButtonsPopin } from "./twoButtonsPopin";
import { DSPopin } from "./popin";
import type { DSPopinsContainer } from "./popinsContainer";
import type { IDSTwoButtonsPopinOptions } from "./twoButtonsPopin";

const REPLAY_CONTENT = "replay.content";

export class DSReplayPoptin extends DSTwoButtonsPopin {
	private popinsContainer: DSPopinsContainer;
	private replaySentence: TextField;

	public constructor(options: IDSTwoButtonsPopinOptions, popinsContainer: DSPopinsContainer) {
		const replaySentence = new TextField(i18n.t(REPLAY_CONTENT), {
			default: {
				fontName: "OpenSans-SemiBold",
				fontSize: DSPopin.textFontSize,
				fill: options.base.titleColor ? options.base.titleColor : "#000000",
			},
		});
		replaySentence.freeze();
		replaySentence.wordWrap = true;
		replaySentence.align = EAlign.Center;
		replaySentence.width = DSPopin.minWidth;
		replaySentence.dockingBounds = new DockingBounds(DSPopin.minWidth, 120);
		replaySentence.unfreeze({ noUpdate: true });

		options.base.content = replaySentence;

		super(options);

		this.popinsContainer = popinsContainer;

		this.replaySentence = replaySentence;
	}

	/**
	 * Show the popin with or without close button
	 *
	 * @access public
	 * @param {boolean} withClose, should add the close button
	 */
	public show(withClose: boolean): void {
		this.replaySentence.text = i18n.t(REPLAY_CONTENT, {
			ticketId: this.popinsContainer.ticket.ticketId,
		});

		super.show(withClose);
	}
}
