/* eslint-disable import/no-duplicates */
import { ContainerImporter, ProjectImporter, AssetStore } from "@gdk/core-pixi";
import * as GDKSpine from "@gdk/core-pixi";
import * as GDKUi from "@gdk/core-pixi";
import * as GDKProton from "@gdk/core-pixi";
import type { GamePixi, IProject, IProjectContainer, IViewMap } from "@gdk/core-pixi";
import type { Container } from "pixi.js";

import * as GDKRules from "../";
import { BehaviorCodeExtension } from ".";

export class ProjectLoader {
	/**
	 * Load the given project
	 * @param game the game reference, typically a GamePixi
	 * @param project the project's configuration
	 * @param baseUrl the base url of the loader
	 * @param viewMap the viewmap to map views with states
	 */
	public static load(
		game: GamePixi,
		project: IProject,
		baseUrl: string,
		viewMap: IViewMap = {}
	): void {
		// Assets
		AssetStore.parse(project.assets, baseUrl);

		// Customize container creator
		ContainerImporter.customContainerCreator = (config, container) => {
			return this.customContainerLoader(config, baseUrl, container, project);
		};

		// Custom customizer is now commented as it is not used for instance
		ContainerImporter.customContainerCustomizer = (config, container) => {
			this.customContainerCustomizer(game, config, container, baseUrl, project);
		};

		// Import project
		ProjectImporter.importProject(game, project, baseUrl, viewMap);
	}

	/**
	 * Function that customizes the given container according to its metadatas
	 * @param config the container's configuration
	 * @param container the currently customized container
	 * @param baseUrl the base url of the loader
	 * @param project the project JSON data (source data)
	 */
	public static customContainerCustomizer(
		game: GamePixi,
		config: IProjectContainer,
		container: Container,
		baseUrl: string,
		project: IProject
	): void {
		if (!container.metadata) {
			return;
		}

		if (project.metadata) {
			// Behavior code extension requires metadatas on game (scripts collection)
			BehaviorCodeExtension.apply(game, project, container);
		}
	}

	/**
	 * Custom loader for loading custom containers like ui.TextField etc.
	 * @param config the container's configuration
	 * @param baseUrl the base url of the loader
	 * @param container the currently created container
	 * @param project the project JSON data (source data)
	 */
	public static customContainerLoader(
		config: IProjectContainer,
		baseUrl: string,
		container?: Container,
		project?: IProject
	): Container {
		const split = config.type.split(".");
		if (split.length === 1) {
			return null;
		}

		let c: Container = null;

		// Create
		switch (split[0]) {
			case "ui":
				c = GDKUi[split[1]].parse(config, baseUrl, container, project);
				break;
			case "result":
				c = GDKRules[split[1]].parse(config, baseUrl);
				break;
			case "rules":
				c = GDKRules[split[1]].parse(config, baseUrl, container, project);
				break;
			case "spine":
				c = GDKSpine[split[1]].parse(config, baseUrl);
				break;
			case "proton":
				c = GDKProton[split[1]].parse(config, baseUrl);
				break;
			default:
				break;
		}

		// Return
		return c;
	}
}
