import { Container } from "pixi.js";
import { tracker, SlideEvent, ESlideEvent } from "@gdk/core";

import type { Rules } from "./rules";

export abstract class Slide extends Container {
	// Public members
	public hasBullet: boolean = true;

	/**
	 * Constructor
	 */
	public constructor() {
		// PIXI.Container
		super();
	}

	/**
	 * Configures the slide in the given popup
	 * @param rules the popup containing the slide
	 */
	public configure(rules: Rules): void {
		// Empty. Can be overriden
	}

	/**
	 * Called when showing the slide in the rules popup
	 */
	public onShow(rules: Rules): void {
		if (this.visible) {
			return;
		}
		this.visible = true;
		tracker.track(new SlideEvent(this.getName(), ESlideEvent.Show));
	}

	/**
	 * Called when hiding the slide in the rules popup
	 */
	public onHide(rules: Rules): void {
		if (!this.visible) {
			return;
		}
		tracker.track(new SlideEvent(this.getName(), ESlideEvent.Hide));
		this.visible = false;
	}

	/**
	 * On destroy the slide
	 */
	public onDestroy(rules: Rules): void {
		return;
	}
}
