import { EMessageType } from "./definitions";
import { Popin } from "../popin/index";
import type { IMessage } from "./definitions";
import type { IPopinActions } from "../popin/index";
import type { Com } from "..";

/**
 * InGame message manager. Displays popins.
 */
export class MessageManager {
	private _messages: IMessage[] = [];
	private _com: Com;
	private _messageDisplayStack: Promise<void> = Promise.resolve();

	public constructor(com: Com) {
		this._com = com;
	}

	/**
	 * Push one or multiples messages to be displayed immediatly or at the end of the game
	 * regarding the provided configuration
	 * @param message message.s to display
	 */
	public async push(message: IMessage | IMessage[]): Promise<void> {
		if (Array.isArray(message)) {
			await Promise.all(message.map((each) => this._pushOne(each)));
		} else {
			await this._pushOne(message);
		}
	}

	/**
	 * Remove all messages waiting to be displayed.
	 */
	public reset(): void {
		this._messages.length = 0;
	}

	/**
	 * Display all messages waiting for GameEnd event
	 */
	public async onGameEnd(): Promise<void> {
		await Promise.all(this._messages.map((message) => this.display(message)));
		this.reset();
	}

	/**
	 * Display a popin message
	 * We first ask the lottery frontsite to display the message
	 * without positive response we handle the popin ourself
	 * @param message message to display
	 */
	public async display(message: IMessage): Promise<void> {
		this._messageDisplayStack = this._messageDisplayStack.then(async () => {
			this._com.game.pause("messagemanager");

			try {
				await new Promise((resolve, reject) => {
					// bridge promises are shitty, so we have to wrap them to be able to await them
					(this._com.bridge.executeAction("OPEN_POPUP", message) as any)
						.then(resolve)
						.catch(reject);
				});
			} catch (up) {
				this._com.game.showInteractivityOverlay();
				await Popin.display(message.popup, {
					closeGame: () => {
						this._com.bridge.executeAction("CLOSE_GAME");
					},
					refreshGame: () => {
						window.location.reload();
					},
				} as IPopinActions);
				this._com.game.removeInteractivityOverlay();
			}

			this._com.game.unpause("messagemanager");
		});

		await this._messageDisplayStack;
	}

	private async _pushOne(message: IMessage): Promise<void> {
		if (message.type === EMessageType.Immediate) {
			await this.display(message);
			return;
		}

		this._messages.push(message);
	}
}
