import { pDelayedCall } from "@gdk/core-pixi";
import { Numeral } from "@gdk/core";
import type { SpineTimeline, TextField } from "@gdk/core-pixi";

import { ResultStage } from "./resultStage";
import type { ResultBox } from "./resultBox";

export interface ISpineStageStyle {
	thresholdsTexts: Array<{
		mainText: string;
		subText: string;
	}>;

	looserTexts: {
		mainText: string;
		subText: string;
	};

	winMainText: TextField;
	winSubText: TextField;
	winAmountText: TextField;

	loseMainText: TextField;
	loseSubText: TextField;

	thresholdsWinAmount: number[];

	winSpineTimeline: SpineTimeline;
	loseSpineTimeline: SpineTimeline;

	winEnterAnimationName: string;
	winExitAnimationName: string;

	loseEnterAnimationName: string;
	loseExitAnimationName: string;

	winnerDuration: number;
	loserDuration: number;
}

export class SpineStage extends ResultStage {
	/* ATTRIBUTES */

	public config: ISpineStageStyle;

	/* LIFECYCLE */

	/**
	 * @constructor
	 * @param config Configuration of the stage
	 */
	public constructor(config: ISpineStageStyle) {
		super();

		this.config = config;
		this.reset();
	}

	/**
	 * onShow
	 * @param box
	 * @param done
	 */
	public async onShow(box: ResultBox, done: (stop: boolean) => void): Promise<void> {
		const isWin: boolean = box.amount.value > 0;

		if (isWin) {
			this.config.winAmountText.text = Numeral.currency(box.amount, {
				minimumFractionDigits: 2,
			});

			for (let i = this.config.thresholdsWinAmount.length - 1; i >= 0; i--) {
				const thresholdWinAmount: number = this.config.thresholdsWinAmount[i];
				const thresholdText: { mainText: string; subText: string } = this.config.thresholdsTexts[i];

				if (thresholdText && box.amount.value >= thresholdWinAmount) {
					this.config.winMainText.text = thresholdText.mainText;
					this.config.winSubText.text = thresholdText.subText;
					break;
				}
			}
		} else {
			this.config.loseMainText.text = this.config.looserTexts.mainText;
			this.config.loseSubText.text = this.config.looserTexts.subText;
		}

		const enterAnimation = isWin
			? this.config.winSpineTimeline.playDirect(this.config.winEnterAnimationName)
			: this.config.loseSpineTimeline.playDirect(this.config.loseEnterAnimationName);

		enterAnimation
			.then(() => pDelayedCall(isWin ? this.config.winnerDuration : this.config.loserDuration))
			.then(() =>
				isWin
					? this.config.winSpineTimeline.playDirect(this.config.winExitAnimationName)
					: this.config.loseSpineTimeline.playDirect(this.config.loseExitAnimationName)
			)
			.then(() => {
				done(true);
			});

		this.config.winSpineTimeline.visible = isWin;
		this.config.loseSpineTimeline.visible = !isWin;
	}

	/* PUBLIC METHODS */

	/**
	 * Reset the stage
	 */
	public reset(): void {
		this.config.winSpineTimeline.visible = false;
		this.config.loseSpineTimeline.visible = false;
	}
}
