import { Container } from "pixi.js";
import { TweenMax } from "gsap";

import type { ResultBox } from "./resultBox";

export abstract class ResultStage extends Container {
	/**
	 * Show the stage
	 * @param done The callback to call when animation done
	 */
	public abstract onShow(box: ResultBox, done: (stop: boolean, hide?: boolean) => void): void;

	/**
	 * Hide the stage
	 * @param progressive: If true, stage is hidden in 0.3s, otherwise in 0s.
	 */
	public onHide(progressive: boolean): Promise<void> {
		if (!progressive) {
			this.hide();
			return Promise.resolve();
		}

		return new Promise((resolve) => {
			TweenMax.to(this, 0.3, {
				alpha: 0,
				onComplete: () => {
					this.hide();
					resolve();
				},
			});
		});
	}

	/**
	 * Hide the stage immediatly
	 */
	public hide(): void {
		this.visible = false;
		this.alpha = 1;
	}
}
