import { Container, Point } from "pixi.js";
import {
	TextArea,
	Docking,
	SpineTimeline,
	DockingBounds,
	EAlign,
	ESpineTimelineEvent,
} from "@gdk/core-pixi";
import { Loader } from "@gdk/core";
import { TweenMax, TimelineMax, Cubic, Linear } from "gsap";
import { Howl } from "howler";
import type { IStyles, TextField } from "@gdk/core-pixi";

import { ResultStage } from "./resultStage";
import { EResultBoxEvents } from "./resultBox";
import { EVisualIdentityAction } from "../../../adaptativeVisualIdentity";
import { IPurse } from "../../wizz";
import type { ResultBox } from "./resultBox";
import type { ICoinPosition, IPurses } from "../../wizz";
import type { AbstractGame } from "../../abstractGame";

export interface IAmountCoinWizzThreshold {
	amount: number;
}

export interface IAmountCoinWizzSounds {
	loser?: Howl | string;
	winner?: Howl | string;
	incrementation?: Howl | string;
}

export interface IAmountCoinWizzAnimations {
	loser?: SpineTimeline | string;
	winner?: SpineTimeline | string;
	coin?: SpineTimeline | string;
}

export interface ITextResultEndGame {
	mainText: string;
	subText1: string;
}

export interface IAmountCoinWizzStyle {
	game: AbstractGame;
	amountStyle: IStyles;
	hasCoinsAnimation: boolean;
	animationSpine?: IAmountCoinWizzAnimations;
	sounds?: IAmountCoinWizzSounds;
	loserDuration?: number;
	winnerDuration?: number;
}

export class AmountCoinWizzStage extends ResultStage {
	// Public members
	public mainText: Container | TextArea;
	public subText1: Container | TextArea;

	public config: IAmountCoinWizzStyle;

	private _loseSound: Howl;
	private _winSound: Howl;
	private _incrementationSound: Howl;
	private _winAnimation: SpineTimeline;
	private _coinAnimation: SpineTimeline;
	private _coins: SpineTimeline[];
	private _lostAnimation: SpineTimeline;
	private _amount: TextField;
	private coinsContainer: Container;
	private purse: Container;

	private static coinsPosition: ICoinPosition[] = [
		{ x: 67, y: -223, startTime: 0.01, rotation: 0.6 },
		{ x: 180, y: -128, startTime: 0.2, rotation: 2.3 },
		{ x: 199, y: -3, startTime: 0.8, rotation: 3.25 },
		{ x: 195, y: 181, startTime: 0.4, rotation: 0.35 },
		{ x: 89, y: 247, startTime: 1.4, rotation: 1.8 },
		{ x: -77, y: 254, startTime: 1, rotation: 0.45 },
		{ x: -185, y: 162, startTime: 1.6, rotation: -2 },
		{ x: -208, y: 26, startTime: 1.2, rotation: -3.6 },
		{ x: -187, y: -109, startTime: 0.6, rotation: 1.4 },
		{ x: -102, y: -215, startTime: 1.4, rotation: 0.8 },
	];

	/**
	 * Constructor
	 */
	public constructor(config: IAmountCoinWizzStyle) {
		super();

		this._amount = new TextArea("+100", config.amountStyle);
		this._amount.keepCanvasReference = true; // Performances, keep canvas reference to avoid re-creating a canvas each update.
		this._amount.docking = new Docking(Docking.centerAll);
		this._amount.wordWrap = true;
		this._amount.align = EAlign.Center;
		this._amount.width = 240;
		this._amount.docking.x = -15;
		this._amount.docking.y = 292;
		this._amount.scaleXY = 1;

		// Sounds
		config.sounds = config.sounds || {};

		if (typeof config.sounds.loser === "string") {
			this._loseSound = Loader.get<Howl>(config.sounds.loser);
		} else if (config.sounds.loser instanceof Howl) {
			this._loseSound = config.sounds.loser;
		}

		if (typeof config.sounds.winner === "string") {
			this._winSound = Loader.get<Howl>(config.sounds.winner);
		} else if (config.sounds.winner instanceof Howl) {
			this._winSound = config.sounds.winner;
		}

		if (typeof config.sounds.incrementation === "string") {
			this._incrementationSound = Loader.get<Howl>(config.sounds.incrementation);
		} else if (config.sounds.incrementation instanceof Howl) {
			this._incrementationSound = config.sounds.incrementation;
		}

		config.winnerDuration = config.winnerDuration || 6;
		config.loserDuration = config.loserDuration || 4;

		// animations
		config.animationSpine = config.animationSpine || {};

		// winning Animation
		if (config.animationSpine.winner instanceof SpineTimeline) {
			this._winAnimation = config.animationSpine.winner as SpineTimeline;
		} else {
			// instanciate a new spineTimeline
			if (typeof config.animationSpine.winner === "string") {
				this._winAnimation = new SpineTimeline(config.animationSpine.winner);
			} else {
				this._winAnimation = new SpineTimeline("result_win.json");
			}

			this._winAnimation.docking = new Docking(Docking.centerAll);
			this._winAnimation.docking.x = -38;
			this._winAnimation.docking.y = -140;
			this._winAnimation.pivot.x = 651;
			this._winAnimation.pivot.y = 614;
		}
		this._winAnimation.visible = false;
		this.addChild(this._winAnimation);

		// lost animation
		if (config.animationSpine.loser instanceof SpineTimeline) {
			this._lostAnimation = config.animationSpine.loser as SpineTimeline;
		} else {
			// instanciate a new spineTimeline
			if (typeof config.animationSpine.loser === "string") {
				this._lostAnimation = new SpineTimeline(config.animationSpine.loser);
			} else {
				this._lostAnimation = new SpineTimeline("result_lost.json");
			}

			this._lostAnimation.docking = new Docking(Docking.centerAll);
			this._lostAnimation.docking.x = 0;
			this._lostAnimation.docking.y = 0;
			this._lostAnimation.pivot.x = 288;
			this._lostAnimation.pivot.y = 288;
			this._lostAnimation.scaleXY = 1.1;
		}
		this._lostAnimation.visible = false;
		this.addChild(this._lostAnimation);

		this.addChild(this._amount);

		// coin animation
		if (config.hasCoinsAnimation) {
			let mustSetCoins = true;

			if (config.animationSpine.coin instanceof SpineTimeline) {
				this._coinAnimation = config.animationSpine.coin as SpineTimeline;
				mustSetCoins = false;
			} else if (typeof config.animationSpine.coin === "string") {
				this._coinAnimation = new SpineTimeline(config.animationSpine.coin);
			} else {
				// standard animation
				this._coinAnimation = new SpineTimeline("result_win_coin.json");
			}

			if (mustSetCoins) {
				// a new SpineTimeline was created, must set it
				this._coinAnimation.docking = new Docking(Docking.centerAll);
				this._coinAnimation.pivot.x = this._coinAnimation.width / 2;
				this._coinAnimation.pivot.y = this._coinAnimation.height / 2;
				this._coinAnimation.docking.x = 0;
				this._coinAnimation.docking.y = 0;
			}

			this.coinsContainer = new Container();
			this.coinsContainer.dockingBounds = new DockingBounds(576, 1024);
			this.coinsContainer.docking = new Docking(Docking.centerAll);
			this.coinsContainer.docking.x = 0;
			this.coinsContainer.docking.y = 0;

			this._coins = [];
			for (let i = 0; i < 10; i++) {
				this._coins[i] = this._coinAnimation.clone() as SpineTimeline;
				this.coinsContainer.addChild(this._coins[i]);
			}

			this.addChild(this.coinsContainer);
		}

		// Misc.
		config.game.vi.emitter.emit(
			EVisualIdentityAction.WizzGetPursesContainers,
			(containers: IPurses) => {
				this.purse = containers.standard;
			}
		);
		this.config = config;
		this.dockAsPoint = true;
		this.visible = false;
	}

	/**
	 * On show the result box
	 * @param box the result box reference
	 * @param done the callback to call when stage animation done
	 */
	public onShow(box: ResultBox, done: (stop: boolean) => void): void {
		if (box.amount.value === 0) {
			// Looser

			this._winAnimation.visible = false;
			this._lostAnimation.visible = false;
			this.coinsContainer.visible = false;
			this._amount.visible = false;
			this._amount.docking.x = -13;
			this._amount.docking.y = 209;

			// texts
			this._amount.text = "0";

			// sounds
			if (this._loseSound) {
				this._loseSound.play();
			}

			// animations
			(async () => {
				this._lostAnimation.on(ESpineTimelineEvent.Start, () => {
					this._lostAnimation.visible = true;
					this._amount.visible = true;
				});
				await this._lostAnimation.playDirect("enter");
				await this._lostAnimation.playDirect("idle");
				this._amount.visible = false;
				this._lostAnimation.visible = false;
			})();
		} else {
			// Winner

			// @TODO
			this._winAnimation.visible = true;
			this._lostAnimation.visible = false;
			this.coinsContainer.visible = true;
			this._amount.docking.x = -15;
			this._amount.docking.y = 292;

			// texts
			this._amount.text = `+ ${box.amount.value}`;

			// sounds
			if (this._winSound) {
				this._winSound.play();
			}

			// animations
			// coins animations
			if (this.config.hasCoinsAnimation) {
				// set coins to default
				for (let i = 0; i < this._coins.length; i++) {
					const element = this._coins[i];
					element.docking.x = AmountCoinWizzStage.coinsPosition[i].x;
					element.docking.y = AmountCoinWizzStage.coinsPosition[i].y;
					element.playDirect("coin", true);
					element.play();
					element.timeScale = 0;
					element.rotation = AmountCoinWizzStage.coinsPosition[i].rotation;
					element.seek(AmountCoinWizzStage.coinsPosition[i].startTime);
					element.seek(AmountCoinWizzStage.coinsPosition[i].startTime);
					element.seek(AmountCoinWizzStage.coinsPosition[i].startTime);
					element.seek(AmountCoinWizzStage.coinsPosition[i].startTime);
					element.scaleXY = 1;
					element.visible = true;
				}
			}

			// phase one
			this._winAnimation.visible = true;
			this._winAnimation.playDirect("enter");
			this.scaleXY = 0;
			const firstTween: TimelineMax = new TimelineMax();
			firstTween.to(this, 0.5, { scaleXY: 1.1 }, 0);
			firstTween.to(
				this,
				0.1,
				{
					scaleXY: 1,
					onComplete: () => {
						box.emit(EResultBoxEvents.StageHasEntered, this);
						// phase two
						const secondTween: TimelineMax = new TimelineMax();
						secondTween.to(this, 3.5, {}, 0);
						if (this.config.hasCoinsAnimation) {
							const coinFinalPos: Point = this.coinsContainer.toLocal(
								new Point(this.purse.dockingBounds.width + 15, 50),
								this.purse,
								new Point(0, 0),
								true
							);
							for (let i = 0; i < this._coins.length; i++) {
								const element = this._coins[i];
								// turning coin animation
								const timeCoin = { float: AmountCoinWizzStage.coinsPosition[i].startTime };
								const endTime =
									Math.ceil(AmountCoinWizzStage.coinsPosition[i].startTime / (1.605 + 0.48)) *
										1.605 +
									0.48;
								secondTween.to(
									timeCoin,
									1.75,
									{
										float: endTime,
										onUpdate: () => {
											element.seek(timeCoin.float);
										},
										onComplete: () => {
											element.seek(1.605 + 0.48);
										},
										ease: Linear.easeNone,
									},
									0
								);
								// movement/rotation coin animation
								secondTween.to(
									element.docking,
									1.65 + (i % 3) * 0.1,
									{
										x: coinFinalPos.x - element.x + element.docking.x,
										delay: (i % 3) * 0.1,
										ease: Cubic.easeIn,
									},
									0
								);
								secondTween.to(
									element.docking,
									1.7 + (i % 3) * 0.1,
									{
										y: coinFinalPos.y - element.y + element.docking.y,
										delay: (i % 3) * 0.1,
										ease: Cubic.easeIn,
									},
									0
								);
								secondTween.to(
									element,
									1.7 + (i % 3) * 0.1,
									{
										rotation: 0,
										scaleXY: 0.5,
										delay: (i % 3) * 0.1,
										ease: Linear.easeNone,
										onComplete: () => {
											element.visible = false;
											if (this._incrementationSound) {
												this._incrementationSound.play();
											}
										},
									},
									0
								);
							}
						}
						secondTween.play();
					},
				},
				0.5
			);
			firstTween.play();
		}

		// set the overall time of the animation
		const duration: number =
			box.amount.value === 0 ? this.config.loserDuration : this.config.winnerDuration;
		TweenMax.to(this, duration, {
			onComplete: () => {
				box.emit(EResultBoxEvents.StageWillExit, this);
				done(true);
			},
		});
	}
}
