import { Button, Docking } from "@gdk/core-pixi";
import * as i18n from "i18next";

import { ShineableButton } from "./shineableButton";
import { EVisualIdentityAction } from "../adaptativeVisualIdentity";
import { DSPopin } from "./popin";
import type { Express } from "../game";

const BASE = "buttons/autoskrab/";
const IDLE = `${BASE}idle`;
const OVER = `${BASE}over`;
const CLICKED = `${BASE}down`;
const DISABLED = `${BASE}disabled`;
const PADDING = 15;
const PADDING_WIDTH = 30;
const COLOR = "#000000";

const KEYS = {
	auto: "reveal.auto",
	stop: "reveal.pause",
	playAgain: "reveal.playAgain",
	tryAgain: "reveal.tryAgain",
};

export interface IDSVIButtonsOptions {
	onAuto: () => void;
	onStop: () => void;
	onPlayAgain: () => void;
	onTryAgain: () => void;
}

export class DSVIButtons {
	private auto: Button;
	private stop: Button;
	private playAgain: ShineableButton;
	private tryAgain: ShineableButton;

	private onAuto: () => void;
	private onStop: () => void;
	private onPlayAgain: () => void;
	private onTryAgain: () => void;

	private buttons: Button[] = [];

	private game: Express;

	public constructor(game: Express, callbacks: IDSVIButtonsOptions) {
		this.game = game;
		this.onAuto = callbacks.onAuto;
		this.onStop = callbacks.onStop;
		this.onPlayAgain = callbacks.onPlayAgain;
		this.onTryAgain = callbacks.onTryAgain;

		for (const key of ["auto", "stop", "playAgain", "tryAgain"]) {
			if (key === "playAgain" || key === "tryAgain") {
				this[key] = new ShineableButton(
					{
						button: {
							idleTexture: IDLE,
							overTexture: OVER,
							clickedTexture: CLICKED,
							disabledTexture: DISABLED,
						},
						shine: true,
					},
					{
						default: {
							fontName: "OpenSans-SemiBold",
							fontSize: DSPopin.menuButtonFontSize,
							fill: COLOR,
						},
					},
					""
				);
			} else {
				this[key] = new Button(
					{
						idleTexture: IDLE,
						overTexture: OVER,
						clickedTexture: CLICKED,
						disabledTexture: DISABLED,
					},
					{
						default: {
							fontName: "OpenSans-SemiBold",
							fontSize: DSPopin.menuButtonFontSize,
							fill: COLOR,
						},
					},
					""
				);
			}
			this[key].docking = new Docking(Docking.centerAll);
			this[key].text.docking.y = -4;
			this[key].isNineSlice = true;
			this[key].backgroundNineSlice.leftWidth = PADDING_WIDTH;
			this[key].backgroundNineSlice.topHeight = PADDING;
			this[key].backgroundNineSlice.rightWidth = PADDING_WIDTH;
			this[key].backgroundNineSlice.bottomHeight = PADDING;

			this[key].nineSliceMinSize.x = 170;
			this[key].nineSliceMinSize.y = 70;

			this[key].nineSlicePadding.x = PADDING;
			this[key].nineSlicePadding.y = PADDING;
			this[key].nineSlicePadding.width = PADDING;
			this[key].nineSlicePadding.height = PADDING;
			this[key].label = i18n.t(KEYS[key]);
			this[key].trackingName = key;

			this.buttons.push(this[key]);
		}

		this.deactivateButtons();

		this.auto.on("pointertap", this.onAuto);
		this.stop.on("pointertap", this.onStop);
		this.playAgain.on("pointertap", this.onPlayAgain);
		this.tryAgain.on("pointertap", this.onTryAgain);

		this.game.vi.emit(EVisualIdentityAction.AddControlButton, this.buttons);
	}

	/**
	 * Activate the play again button
	 *
	 * @access public
	 */
	public activatePlayAgain(): void {
		this.activateButtons([this.playAgain]);
		this.playAgain.updateSize();
	}

	/**
	 * Activate the try again button
	 *
	 * @access public
	 */
	public activateTryAgain(): void {
		this.activateButtons([this.tryAgain]);
		this.tryAgain.updateSize();
	}

	/**
	 * Activate the auto button
	 *
	 * @access public
	 */
	public activateAuto(): void {
		this.activateButtons([this.auto]);
	}

	/**
	 * Activate the pause button
	 *
	 * @access public
	 */
	public activatePause(): void {
		this.activateButtons([this.stop]);
	}

	/**
	 * It will hide all the vi buttons.
	 *
	 * @access public
	 * @param {boolean} deactivateAll=false, if it must disable all buttons
	 */
	public hideButtons(deactivateAll: boolean = false): void {
		for (const button of this.buttons) {
			button.visible = false;
		}

		if (deactivateAll) {
			this.deactivateButtons();
		}
	}

	/**
	 * Show all the active buttons
	 *
	 * @access public
	 */
	public showActiveButtons(): void {
		this.hideButtons();

		for (const button of this.buttons) {
			if (button.enabled) {
				button.visible = true;
			}
		}
	}

	private activateButtons(buttons: Button[]): void {
		this.deactivateButtons();

		for (const button of buttons) {
			button.enabled = true;
		}

		this.showActiveButtons();
	}

	private deactivateButtons(): void {
		for (const button of this.buttons) {
			button.enabled = false;
		}

		this.showActiveButtons();
	}
}
