import { TimelineMax, TweenMax } from "gsap";
import { Container } from "pixi.js";
import { Docking, SpineTimelineButton } from "@gdk/core-pixi";
import type { ICrossSell } from "@gdk/core";
import type { Button, SpineTimeline } from "@gdk/core-pixi";

import { Carousel } from "../../carousel";
import { TweenUtils } from "../../tweenUtils";
import type { ICarouselSettings } from "../../carousel";
import type { AbstractGame } from "../../abstractGame";

export interface ICrossSellOptions {
	// Animations names
	upAnimation?: string;
	outAnimation?: string;
	overAnimation?: string;
	downAnimation?: string;
	enterAnimation?: string;
	idleLoopAnimation?: string;
	enterBackAnimation?: string;
	frontToBackAnimation?: string;
	backToFrontAnimation?: string;
	idleLoopBackAnimation?: string;

	mainCardName?: string;
	defaultSpineTimelineButtonId?: string;
}

export interface ICrossSellCarouselOptions {
	nextArrow?: Button;
	previousArrow?: Button;

	settings?: ICarouselSettings;
}

export class CrossSell extends Container {
	/* ATTRIBUTES */

	/**
	 * List of all visible cards
	 */
	public cards: SpineTimeline[];

	/**
	 * The main card, corresponding to the current game
	 */
	public mainCard: SpineTimeline;

	/**
	 * The carousel
	 */
	public carousel: Carousel;

	/**
	 * The play button from the active container (used for carousel)
	 */
	protected playButton: Container;

	/**
	 * The tweenMax used for the IdleWave animation
	 */
	public tweenIdleWave: TweenMax;

	/**
	 * Options applied to the cross sell instance
	 */
	public options: ICrossSellOptions;

	/**
	 * Force animation to be stopped
	 */
	private _animationStopped: boolean = false;

	/**
	 * Disable the carousel interactivity
	 */
	private _carouselDisabled: boolean = false;

	/* LIFECYCLE */

	/**
	 * @constructor
	 * @param game - The reference to the game
	 */
	public constructor(private game: AbstractGame, options: ICrossSellOptions = {}) {
		super();

		this.options = {
			...options,
			mainCardName: options.mainCardName || "mainCard",
			defaultSpineTimelineButtonId: options.defaultSpineTimelineButtonId || "99_crosssell.json",

			upAnimation: options.upAnimation || "up",
			outAnimation: options.outAnimation || "out",
			overAnimation: options.overAnimation || "over",
			downAnimation: options.downAnimation || "down",
			enterAnimation: options.enterAnimation || "enter",
			idleLoopAnimation: options.idleLoopAnimation || "idleLoop",
			enterBackAnimation: options.enterBackAnimation || "back_enter",
			frontToBackAnimation: options.frontToBackAnimation || "front_to_back",
			backToFrontAnimation: options.backToFrontAnimation || "back_to_front",
			idleLoopBackAnimation: options.idleLoopBackAnimation || "idleLoop_back",
		};
	}

	/**
	 * On Loaded
	 */
	public onLoaded(): void {
		this.cards = this.children.filter((child) => child.visible) as SpineTimeline[];
		this.mainCard = this.getChildByName<SpineTimeline>(this.options.mainCardName);
		this.playButton = this.mainCard.getSlotContainerByName("button").getChildByName("playButton");
	}

	/* METHODS */

	/**
	 * Create a card in the card container
	 * @param options - Options to pass to the card container
	 */
	public createCard(option: ICrossSell): SpineTimelineButton {
		const card: SpineTimelineButton = new SpineTimelineButton(
			option.spineTimelineButtonId || this.options.defaultSpineTimelineButtonId,
			{ default: {} }
		);

		if (option.skinName) {
			card.setSkinByName(option.skinName);
		}

		card.animationsNames.up = this.options.upAnimation;
		card.animationsNames.out = this.options.outAnimation;
		card.animationsNames.over = this.options.overAnimation;
		card.animationsNames.down = this.options.downAnimation;
		card.animationsNames.enter = this.options.enterAnimation;
		card.animationsNames.idleLoop = this.options.idleLoopAnimation;

		card.docking = new Docking(Docking.centerAll, 0, 0);
		this.cards.push(card);
		this.addChildAt(card, 0);

		card.on("pointerover", () => this.stopIdleWaveAnimation());
		card.on("pointerout", () => this.startTimeoutIdleWave());

		return card;
	}

	/**
	 * Initialize the carousel
	 * @param options - Options of the carousel
	 */
	public initializeCarousel(options: ICrossSellCarouselOptions = {}): void {
		this.carousel = new Carousel(
			{
				game: this.game,
				container: this,
				nextArrow: options.nextArrow,
				previousArrow: options.previousArrow,
			},
			options.settings
		);

		const isSimpleMode: boolean = this.carousel.isSimpleMode();

		this.carousel
			.getCards()
			.forEach((card: Container) => (card.interactive = isSimpleMode && card !== this.mainCard));

		// Events
		this.carousel.on("beforeChangeCard", (nextCard: SpineTimeline, previousCard: SpineTimeline) => {
			if (previousCard) {
				previousCard.interactive = false;
				if (previousCard === this.mainCard) {
					this.playButton.interactive = false;
				}
				previousCard.playDirect(this.options.frontToBackAnimation);
			}

			this.stopIdleWaveAnimation();
			nextCard.playDirect(this.options.backToFrontAnimation);
		});

		this.carousel.on("changeCard", (nextCard: SpineTimeline) => {
			nextCard.interactive = nextCard instanceof SpineTimelineButton && nextCard !== this.mainCard;
			this.playButton.interactive = nextCard === this.mainCard;
			this.startTimeoutIdleWave();
		});

		this.carousel.on("cardPositionsUpdate", () => {
			if (this.isCarouselDisabled()) {
				this.disableCarousel();
			}
		});
	}

	/**
	 * Appear the cross sell
	 */
	public async appear(): Promise<void> {
		const arrows: Container[] = [this.carousel.previousArrow, this.carousel.nextArrow].filter(
			(container) => Boolean(container)
		);
		const timeline: TimelineMax = new TimelineMax({ paused: true });

		this.carousel.setActiveCard(this.mainCard, true);

		arrows.forEach((arrow: Container) => {
			arrow.alpha = 0;
			arrow.visible = false;
			timeline.add(
				TweenMax.fromTo(
					arrow,
					0.7,
					{
						alpha: 0,
					},
					{
						alpha: 1,
						onStart: () => (arrow.visible = true),
					}
				),
				0
			);
		});

		await TweenUtils.wait(0.5);

		const promises: Array<Promise<any>> = this.cards.map((card: SpineTimeline) => {
			const isActive: boolean = this.carousel.isSimpleMode()
				? true
				: card === this.carousel.getActiveCard();
			return card.playDirect(isActive ? "enter" : "back_enter");
		});

		this.visible = true;

		if (this.carousel.getCards().length > 2) {
			promises.push(TweenUtils.playTween(timeline));
			await Promise.all(promises);
		}

		this.interactive = true;
		this.enableCarousel();
		this.startTimeoutIdleWave();
	}

	/**
	 * Disappear animation of the cross sell
	 */
	public disappear(): void {
		this.visible = false;
		this.stopIdleWaveAnimation();
	}

	/**
	 * Disable carousel interactivity
	 */
	public disableCarousel(): void {
		this._carouselDisabled = true;
		this.interactive = false;
		this.carousel.isDragging = false;
		this.carousel.setButtonActive(false);
		this.carousel.getCards().forEach((card) => {
			card.interactive = false;
		});

		this.stopIdleWaveAnimation();
	}

	/**
	 * Enable the carousel
	 */
	public enableCarousel(): void {
		this._carouselDisabled = false;
		this.interactive = true;
		this.carousel.setButtonActive(true);

		if (this.carousel.getCards().length === 2) {
			this.carousel.getCards().forEach((card) => {
				card.interactive = true;
			});
		}
	}

	/**
	 * Stop the IdleWave animation of the main card
	 */
	public stopIdleWaveAnimation(): void {
		if (this.tweenIdleWave) {
			this.tweenIdleWave.kill();
		}

		this._animationStopped = true;
	}

	/**
	 * Create a timer to restart the idle wave animation
	 */
	public startTimeoutIdleWave(): void {
		this.stopIdleWaveAnimation();

		this.tweenIdleWave = TweenMax.fromTo(
			this,
			2,
			{ idleWaveTiming: 0 },
			{
				repeat: -1,
				idleWaveTiming: 2,
				onRepeat: this.playIdleWaveAnimation,
			}
		);
	}

	/**
	 * Create a wave animation base on the "idleLoop" animation of all cards
	 */
	public playIdleWaveAnimation = async (): Promise<void> => {
		let animationIsStopped: boolean = (this._animationStopped = false);
		const isSimpleMode: boolean = this.carousel.isSimpleMode();
		const waitTimings: number[] = this.carousel.computeArray([0, 0.5, 1]);
		const activeCard: Container = this.carousel.getActiveCard();
		const orderCards: SpineTimeline[] = this.carousel.getCards() as SpineTimeline[];
		const cardAnimation = async (card: SpineTimeline, animation: string, wait: number = 0) => {
			if (wait > 0) {
				await TweenUtils.wait(wait);
			}

			animationIsStopped = this._animationStopped || animationIsStopped;

			return !animationIsStopped && card.playDirect(animation);
		};

		await Promise.all(
			orderCards.map((card: SpineTimeline, i: number) => {
				cardAnimation(
					card,
					card === activeCard || isSimpleMode
						? this.options.idleLoopAnimation
						: this.options.idleLoopBackAnimation,
					waitTimings[i]
				);
			})
		);
	};

	/* GETTERS */

	/**
	 * Returns true if the carousel should be disabled
	 */
	public isCarouselDisabled(): boolean {
		return this._carouselDisabled;
	}
}
