import { TextField, DockingBounds, EAlign } from "@gdk/core-pixi";
import { Numeral } from "@gdk/core";
import * as i18n from "i18next";
import { EItgPrizeType } from "@falcon/bridge";

import { EBetResponseAction } from "../game";
import { DSTwoButtonsPopin } from "./twoButtonsPopin";
import { DSPopin } from "./popin";
import type { Ticket } from "../game";
import type { IDSTwoButtonsPopinOptions } from "./twoButtonsPopin";

const DEFAULT_COLOR = "#000000";
const BOTTOM_SPACE = 30;

export interface IDSWinLosePopinOptions extends IDSTwoButtonsPopinOptions {
	loseTitle: string;
	winTitle: string;
	exceptionnalTitle?: string;
	loseContent: string;
	winContentKey: string;
	exceptionnalContentKey?: string;
}

export class DSWinLosePopin extends DSTwoButtonsPopin {
	private loseTitle: string;
	private winTitle: string;
	private exceptionnalTitle: string;

	private loseContent: string;
	private winContentKey: string;
	private exceptionnalContentKey: string;

	private contentText: TextField;

	private _ranks: string[];

	public constructor(options: IDSWinLosePopinOptions) {
		const content = new TextField("", {
			default: {
				fontName: "OpenSans-SemiBold",
				fontSize: DSPopin.textFontSize,
				fill: options.base.titleColor ? options.base.titleColor : DEFAULT_COLOR,
			},
		});
		content.wordWrap = true;
		content.width = DSPopin.minWidth;
		content.align = EAlign.Center;
		content.dockingBounds = new DockingBounds(0, 0);
		options.base.content = content;

		super(options);

		this.contentText = content;
		this.loseTitle = options.loseTitle;
		this.winTitle = options.winTitle;
		this.exceptionnalTitle = options.exceptionnalTitle;
		this.loseContent = options.loseContent;
		this.winContentKey = options.winContentKey;
		this.exceptionnalContentKey = options.exceptionnalContentKey;
	}

	/**
	 * Show with or without close button, could use a Ticket to specify amount
	 *
	 * @access public
	 * @param {boolean} withClose=false, Should show the close button
	 * @param {Ticket} ticket?, the game ticket
	 */
	public show(withClose: boolean = false, ticket?: Ticket): void {
		if (
			Boolean(this.exceptionnalTitle) &&
			Boolean(ticket.prize) &&
			ticket.prize.type === EItgPrizeType.periodicWinning
		) {
			// periodic winning
			this.title.text = this.exceptionnalTitle;
			this.contentText.text = i18n
				.t(this.exceptionnalContentKey, { amount: this._formatAmount(ticket, true) })
				.replace(/\.\./g, ".");
		} else if (
			Boolean(this.exceptionnalTitle) &&
			ticket.action === EBetResponseAction.SUSPENSION_TRIGGERED
		) {
			// big win
			this.title.text = this.exceptionnalTitle;
			this.contentText.text = i18n
				.t(this.exceptionnalContentKey, { amount: this._formatAmount(ticket) })
				.replace(/\.\./g, ".");
		} else if (ticket.wonAmount.value > 0) {
			// normal win
			this.title.text = this.winTitle;
			this.contentText.text = i18n
				.t(this.winContentKey, { amount: this._formatAmount(ticket) })
				.replace(/\.\./g, ".");
		} else {
			// lose
			this.title.text = this.loseTitle;
			this.contentText.text = this.loseContent;
		}

		this.contentText.dockingBounds.width = this.contentText.width;
		this.contentText.dockingBounds.height = this.contentText.height + BOTTOM_SPACE;

		super.show(withClose);
	}

	/**
	 * Set the ranks of the popin, it contains the descriptions of the game ranks.
	 * They are used to display infomations on specials wins
	 */
	public set ranks(ranks: string[]) {
		this._ranks = ranks;
	}

	private _formatAmount(ticket: Ticket, description: boolean = false): string {
		let amount: string;
		if (
			description &&
			Boolean(ticket.prize) &&
			ticket.prize.rank &&
			Array.isArray(this._ranks) &&
			Boolean(this._ranks[ticket.prize.rank])
		) {
			amount = this._ranks[ticket.prize.rank - 1];
		} else {
			amount = Numeral.currency(ticket.wonAmount, {
				minimumFractionDigits: ticket.wonAmount.value % 100 === 0 ? 0 : 2,
			});
		}
		return amount;
	}
}
