import { Sprite, Container, Point } from "pixi.js";
import {
	TextArea,
	Docking,
	SpineTimeline,
	DockingBounds,
	EAlign,
	ESpineTimelineEvent,
} from "@gdk/core-pixi";
import { Loader, Numeral } from "@gdk/core";
import { TweenMax, TimelineMax, Cubic, Power0, Linear, Back, Expo } from "gsap";
import { Howl } from "howler";
import type { IStyles, TextField } from "@gdk/core-pixi";

import { ResultStage } from "./resultStage";
import { EResultBoxEvents } from "./resultBox";
import { EVisualIdentityAction } from "../../../adaptativeVisualIdentity";
import type { ResultBox } from "./resultBox";
import type { AbstractGame } from "../../abstractGame";
import type { IPurses } from "../../wizz";

export interface IAmountCurrencyWizzThreshold {
	amount: number;
}

export interface IAmountCurrencyWizzSounds {
	loser?: Howl | string;
	winner?: Howl | string;
	increment?: Howl | string;
}

export interface IAmountCurrencyWizzAnimations {
	loser?: SpineTimeline | string;
	rollingFX?: SpineTimeline | string;
}

export interface IAmountCurrencyWizzSprites {
	winTextSprite?: Sprite | string;
	currencyBackSprite?: Sprite | string;
	currencyMovingSprite?: Sprite | string;
}

export interface ITextResultEndGame {
	mainText: string;
	subText1: string;
}

export interface IAmountCurrencyWizzStyle {
	game: AbstractGame;
	amountStyle: IStyles;
	amountStrokeStyle: IStyles;
	hasCoinsAnimation: boolean;
	animationSpine?: IAmountCurrencyWizzAnimations;
	sounds?: IAmountCurrencyWizzSounds;
	sprites?: IAmountCurrencyWizzSprites;
	minimumFractionDigits: number;
}

export class AmountCurrencyWizzStage extends ResultStage {
	// Public members
	public mainText: Container | TextArea;
	public subText1: Container | TextArea;

	public config: IAmountCurrencyWizzStyle;

	// sounds
	private _loseSound: Howl;
	private _winSound: Howl;
	private _incrementSound: Howl;
	// spineTimelines
	private _rollingFX: SpineTimeline;
	private _lostAnimation: SpineTimeline;
	// sprites
	private _referenceCoin: Sprite;
	private _movingCoins: Sprite[];
	private _referenceBackgroundCoin: Sprite;
	private _backgroundCoins: Sprite[];
	private _sparkles: Sprite[];
	private _fxRayon: Sprite;
	private _winText: Sprite;
	private _amount: TextField;
	private _amountStroke: TextField;
	private _coinsContainer: Container;
	// containers
	private sparkles: Container;
	private resultWin: Container;
	private purse: Container;

	private static coinsPosition: Array<{
		x: number;
		y: number;
		startTime: number;
		rotation: number;
	}> = [
		{ x: 67, y: -223, startTime: 0.01, rotation: 0.6 },
		{ x: 180, y: -128, startTime: 0.2, rotation: 2.3 },
		{ x: 199, y: -3, startTime: 0.8, rotation: 3.25 },
		{ x: 195, y: 181, startTime: 0.4, rotation: 0.35 },
		{ x: 89, y: 247, startTime: 1.4, rotation: 1.8 },
		{ x: -77, y: 254, startTime: 1, rotation: 0.45 },
		{ x: -185, y: 162, startTime: 1.6, rotation: -2 },
		{ x: -208, y: 26, startTime: 1.2, rotation: -3.6 },
		{ x: -187, y: -109, startTime: 0.6, rotation: 1.4 },
		{ x: -102, y: -215, startTime: 1.4, rotation: 0.8 },
	];

	private static sparkleRotation: number[] = [4, 2.9, 2, 0.64, 0, -0.93, 1.2, 4.5];

	private static backgroundCoinsPosition: Array<{
		x: number;
		y: number;
		angle: number;
		force: number;
		startRotation: number;
		rotationForce: number;
		startScale: number;
		scaleForce: number;
	}> = [
		{
			x: -95,
			y: -10,
			angle: 1.74,
			force: 9,
			startRotation: -0.7,
			rotationForce: -0.08,
			startScale: 0.4,
			scaleForce: 0.1,
		},
		{
			x: -30,
			y: -40,
			angle: 2.62,
			force: 14,
			startRotation: 0,
			rotationForce: -0.2,
			startScale: 0.4,
			scaleForce: 0.1,
		},
		{
			x: 38,
			y: -45,
			angle: 3.48,
			force: 14,
			startRotation: 0.5,
			rotationForce: -0.1,
			startScale: 0.4,
			scaleForce: 0.1,
		},
		{
			x: 80,
			y: 0,
			angle: 4.36,
			force: 14,
			startRotation: 0.3,
			rotationForce: 0.05,
			startScale: 0.4,
			scaleForce: 0.1,
		},
	];
	private static numberPerRayon: number = 6;

	/**
	 * Constructor
	 */
	public constructor(config: IAmountCurrencyWizzStyle) {
		super();

		// texts
		this._amount = new TextArea("", config.amountStyle);
		this._amount.keepCanvasReference = true; // Performances, keep canvas reference to avoid re-creating a canvas each update.
		this._amount.docking = new Docking(Docking.centerAll);
		this._amount.wordWrap = true;
		this._amount.docking.cancelPivot = true;
		this._amount.align = EAlign.Center;
		this._amount.width = 500;
		this._amount.docking.x = 0;
		this._amount.docking.y = 0;
		this._amount.scaleXY = 1;

		this._amountStroke = new TextArea("", config.amountStrokeStyle);
		this._amountStroke.keepCanvasReference = true; // Performances, keep canvas reference to avoid re-creating a canvas each update.
		this._amountStroke.docking = new Docking(Docking.centerAll);
		this._amountStroke.wordWrap = true;
		this._amountStroke.docking.cancelPivot = true;
		this._amountStroke.align = EAlign.Center;
		this._amountStroke.width = 500;
		this._amountStroke.docking.x = 0;
		this._amountStroke.docking.y = 0;
		this._amountStroke.scaleXY = 1;

		// Sounds
		config.sounds = config.sounds || {};

		if (typeof config.sounds.loser === "string") {
			this._loseSound = Loader.get<Howl>(config.sounds.loser);
		} else if (config.sounds.loser instanceof Howl) {
			this._loseSound = config.sounds.loser;
		}

		if (typeof config.sounds.winner === "string") {
			this._winSound = Loader.get<Howl>(config.sounds.winner);
		} else if (config.sounds.winner instanceof Howl) {
			this._winSound = config.sounds.winner;
		}

		if (typeof config.sounds.increment === "string") {
			this._incrementSound = Loader.get<Howl>(config.sounds.increment);
		} else if (config.sounds.increment instanceof Howl) {
			this._incrementSound = config.sounds.increment;
		}

		// animations
		config.animationSpine = config.animationSpine || {};

		// rollingFX Animation
		if (config.animationSpine.rollingFX instanceof SpineTimeline) {
			this._rollingFX = config.animationSpine.rollingFX as SpineTimeline;
		} else {
			if (typeof config.animationSpine.rollingFX === "string") {
				this._rollingFX = new SpineTimeline(config.animationSpine.rollingFX);
			} else {
				this._rollingFX = new SpineTimeline("rollingFX.json");
			}
			this._rollingFX.docking = new Docking(Docking.centerAll);
			this._rollingFX.docking.x = 0;
			this._rollingFX.docking.y = 100;
			this._rollingFX.pivot.x = 620;
			this._rollingFX.pivot.y = 580;
			this._rollingFX.scaleXY = 0.5;
		}
		this._rollingFX.timeScale = 3;
		this._rollingFX.visible = false;

		// lost animation
		if (config.animationSpine.loser instanceof SpineTimeline) {
			this._lostAnimation = config.animationSpine.loser as SpineTimeline;
		} else {
			if (typeof config.animationSpine.loser === "string") {
				this._lostAnimation = new SpineTimeline(config.animationSpine.loser);
			} else {
				this._lostAnimation = new SpineTimeline("result_lost.json");
			}
			this._lostAnimation.docking = new Docking(Docking.centerAll);
			this._lostAnimation.docking.x = 0;
			this._lostAnimation.docking.y = 100;
			this._lostAnimation.pivot.x = 280;
			this._lostAnimation.pivot.y = 260;
			this._lostAnimation.scaleXY = 0.95;
		}
		this._lostAnimation.visible = false;

		// sprites
		config.sprites = config.sprites || {};

		// backgroundCoin sprite
		if (config.sprites.currencyBackSprite instanceof Sprite) {
			this._referenceBackgroundCoin = config.sprites.currencyBackSprite as Sprite;
		} else {
			if (typeof config.sprites.currencyBackSprite === "string") {
				this._referenceBackgroundCoin = Sprite.fromImage(config.sprites.currencyBackSprite);
			} else {
				this._referenceBackgroundCoin = Sprite.fromImage("fx_euro_big.png");
			}
			this._referenceBackgroundCoin.docking = new Docking(Docking.centerAll);
			this._referenceBackgroundCoin.docking.x = 0;
			this._referenceBackgroundCoin.docking.y = 0;
			this._referenceBackgroundCoin.pivot.x = 18;
			this._referenceBackgroundCoin.pivot.y = 46;
			this._referenceBackgroundCoin.scaleXY = 0.38;
		}

		// moving coin sprite
		if (config.sprites.currencyMovingSprite instanceof Sprite) {
			this._referenceCoin = config.sprites.currencyMovingSprite as Sprite;
		} else {
			if (typeof config.sprites.currencyMovingSprite === "string") {
				this._referenceCoin = Sprite.fromImage(config.sprites.currencyMovingSprite);
			} else {
				this._referenceCoin = Sprite.fromImage("fx_euro_00.png");
			}
			this._referenceCoin.docking = new Docking(Docking.centerAll);
			this._referenceCoin.docking.x = 0;
			this._referenceCoin.docking.y = 0;
			this._referenceCoin.pivot.x = 40;
			this._referenceCoin.pivot.y = 40;
			this._referenceCoin.scaleXY = 1.3;
			this._referenceCoin.visible = false;
		}

		// sparkles sprite
		const sparkleReference = Sprite.fromImage("result_fx_sparkles.png");
		sparkleReference.docking = new Docking(Docking.centerAll);
		sparkleReference.docking.x = 0;
		sparkleReference.docking.y = 0;
		sparkleReference.pivot.x = 24;
		sparkleReference.pivot.y = 76;

		// sparkles container
		this.sparkles = new Container();
		this.sparkles.dockingBounds = new DockingBounds(256, 512);
		this.sparkles.docking = new Docking(Docking.centerAll);
		this.sparkles.docking.cancelPivot = true;
		this.sparkles.docking.x = 0;
		this.sparkles.docking.y = 32;
		this.sparkles.pivot.x = 128;
		this.sparkles.pivot.y = 256;
		this._sparkles = [];
		for (let i = 0; i < 8; i++) {
			this._sparkles[i] = sparkleReference.clone() as Sprite;
			this._sparkles[i].rotation = AmountCurrencyWizzStage.sparkleRotation[i];
			this.sparkles.addChild(this._sparkles[i]);
		}

		// resultWin container
		this.resultWin = new Container();
		this.resultWin.dockingBounds = new DockingBounds(256, 512);
		this.resultWin.docking = new Docking(Docking.centerAll);
		this.resultWin.docking.cancelPivot = true;
		this.resultWin.docking.x = 0;
		this.resultWin.docking.y = 50;
		this.resultWin.pivot.x = 128;
		this.resultWin.pivot.y = 256;
		this._fxRayon = Sprite.fromFrame("result_fx_rayons.png");
		this._fxRayon.docking = new Docking(Docking.centerAll);
		this._fxRayon.width = 512;
		this._fxRayon.height = 512;
		this._fxRayon.pivot.x = 256;
		this._fxRayon.pivot.y = 256;
		this.resultWin.addChild(this._fxRayon);
		const resultTXT = Sprite.fromFrame("result_txt_win.png");
		resultTXT.docking = new Docking(Docking.centerAll);
		resultTXT.docking.cancelPivot = true;
		resultTXT.width = 461;
		resultTXT.height = 122;
		resultTXT.docking.y = -300;
		resultTXT.scaleXY = 0.8;
		resultTXT.pivot.x = 94;
		resultTXT.pivot.y = 58;
		this.resultWin.addChild(resultTXT);
		// background coins
		this._backgroundCoins = [];
		const totalBackgroundCoins =
			AmountCurrencyWizzStage.numberPerRayon *
			AmountCurrencyWizzStage.backgroundCoinsPosition.length;
		for (let i = 0; i < totalBackgroundCoins; i++) {
			const elem = (this._backgroundCoins[i] = this._referenceBackgroundCoin.clone() as Sprite);
			const rayonNumber = Math.floor(i / AmountCurrencyWizzStage.numberPerRayon);
			const rayon = AmountCurrencyWizzStage.backgroundCoinsPosition[rayonNumber];
			const placeInRayon = i - rayonNumber * AmountCurrencyWizzStage.numberPerRayon;

			elem.docking.x = rayon.x + Math.cos(rayon.angle + Math.PI / 2) * rayon.force * placeInRayon;
			elem.docking.y = rayon.y + Math.sin(rayon.angle + Math.PI / 2) * rayon.force * placeInRayon;
			elem.rotation = rayon.startRotation + rayon.rotationForce * placeInRayon;
			elem.scaleXY = rayon.startScale + rayon.scaleForce * placeInRayon;
			this.resultWin.addChild(this._backgroundCoins[i]);
		}

		// coins container
		this._coinsContainer = new Container();
		this._coinsContainer.dockingBounds = new DockingBounds(576, 1024);
		this._coinsContainer.docking = new Docking(Docking.centerAll);
		this._coinsContainer.docking.x = 0;
		this._coinsContainer.docking.y = 50;
		this._movingCoins = [];
		for (let i = 0; i < 10; i++) {
			this._movingCoins[i] = this._referenceCoin.clone() as Sprite;
			this._coinsContainer.addChild(this._movingCoins[i]);
		}

		config.game.vi.emitter.emit(
			EVisualIdentityAction.WizzGetPursesContainers,
			(containers: IPurses) => {
				this.purse = containers.standard;
			}
		);

		this.addChild(this._rollingFX);
		this.addChild(this.sparkles);
		this.addChild(this.resultWin);
		this.addChild(this._lostAnimation);
		this.addChild(this._coinsContainer);
		this.resultWin.addChild(this._amountStroke);
		this.resultWin.addChild(this._amount);

		// Misc.
		this.config = config;
		this.dockAsPoint = true;
		this.visible = false;
	}

	/**
	 * On show the result box
	 * @param box the result box reference
	 * @param done the callback to call when stage animation done
	 */
	public onShow(box: ResultBox, done: (stop: boolean) => void): void {
		if (box.amount.value === 0) {
			// Looser
			this.scaleXY = 1;
			this._rollingFX.visible = false;
			this.sparkles.visible = false;
			this.resultWin.visible = false;
			this._amount.visible = false;
			this._amountStroke.visible = false;
			this._lostAnimation.visible = false;

			// sounds
			if (this._loseSound) {
				this._loseSound.play();
			}

			// animations
			(async () => {
				this._lostAnimation.on(ESpineTimelineEvent.Start, () => {
					this._lostAnimation.visible = true;
				});
				await this._lostAnimation.playDirect("enter");
				await this._lostAnimation.playDirect("idle", true);
			})();
		} else {
			// Winner
			this.scaleXY = 0;
			this._rollingFX.visible = true;
			this.sparkles.visible = true;
			this.resultWin.visible = true;
			this._amount.visible = true;
			this._amountStroke.visible = true;
			this._lostAnimation.visible = false;

			// texts
			this._amount.text = Numeral.currency(box.amount, {
				minimumFractionDigits: this.config.minimumFractionDigits,
			});
			this._amountStroke.text = Numeral.currency(box.amount, {
				minimumFractionDigits: this.config.minimumFractionDigits,
			});

			// sounds
			if (this._winSound) {
				this._winSound.play();
			}

			// reset animations
			// reset coins animations
			if (this.config.hasCoinsAnimation) {
				// set coins to default
				for (let i = 0; i < this._movingCoins.length; i++) {
					const element = this._movingCoins[i];
					element.docking.x = AmountCurrencyWizzStage.coinsPosition[i].x;
					element.docking.y = AmountCurrencyWizzStage.coinsPosition[i].y;
					element.rotation = AmountCurrencyWizzStage.coinsPosition[i].rotation;
					element.scaleXY = 1;
					element.visible = true;
				}
			}

			// reset background coins animation
			const totalBackgroundCoins =
				AmountCurrencyWizzStage.numberPerRayon *
				AmountCurrencyWizzStage.backgroundCoinsPosition.length;
			for (let i = 0; i < totalBackgroundCoins; i++) {
				const elem = this._backgroundCoins[i];
				const rayonNumber = Math.floor(i / AmountCurrencyWizzStage.numberPerRayon);
				const rayon = AmountCurrencyWizzStage.backgroundCoinsPosition[rayonNumber];

				elem.docking.x = rayon.x;
				elem.docking.y = rayon.y;
				elem.rotation = rayon.startRotation;
			}

			// animations
			// phase one
			this._rollingFX.playDirect("enter", true);
			const firstTween: TimelineMax = new TimelineMax();
			// size up
			firstTween.to(this, 0.5, { scaleXY: 1.1, ease: Back.easeOut }, 0);
			firstTween.to(
				this,
				0.1,
				{
					scaleXY: 1,
					ease: Back.easeOut,
					onComplete: () => {
						box.emit(EResultBoxEvents.StageHasEntered, this);
						// phase two
						if (this.config.hasCoinsAnimation) {
							const movingCoinTween: TimelineMax = new TimelineMax();
							// find the coins destination
							const coinFinalPos: Point = this._coinsContainer.toLocal(
								new Point(this.purse.dockingBounds.width - 20, 0),
								this.purse,
								new Point(0, 0),
								true
							);
							for (let i = 0; i < this._movingCoins.length; i++) {
								const element = this._movingCoins[i];
								// movement/rotation coin animation
								movingCoinTween.to(
									element.docking,
									1.65 + (i % 3) * 0.1,
									{
										x: coinFinalPos.x - element.x + element.docking.x,
										delay: (i % 3) * 0.1,
										ease: Cubic.easeIn,
									},
									0
								);
								movingCoinTween.to(
									element.docking,
									1.7 + (i % 3) * 0.1,
									{
										y: coinFinalPos.y - element.y + element.docking.y,
										delay: (i % 3) * 0.1,
										ease: Cubic.easeIn,
									},
									0
								);
								movingCoinTween.to(
									element,
									1.65 + (i % 3) * 0.1,
									{ scaleXY: 0.5, delay: (i % 3) * 0.1, ease: Expo.easeIn },
									0
								);
								movingCoinTween.to(
									movingCoinTween,
									1.65 + (i % 3) * 0.1,
									{ timeScale: 0.8, delay: (i % 3) * 0.1, ease: Expo.easeIn },
									0
								);
								movingCoinTween.to(
									element,
									1.7 + (i % 3) * 0.1,
									{
										rotation: 0,
										scaleXY: 0.5,
										delay: (i % 3) * 0.1,
										ease: Linear.easeNone,
										onComplete: () => {
											element.visible = false;
										},
									},
									0
								);
							}
							movingCoinTween.play();
						}
					},
				},
				0.5
			);

			// fxRayons rotating
			firstTween.to(this._fxRayon, 20, { rotation: Math.PI * 2 });

			// background coins
			for (let i = 0; i < totalBackgroundCoins; i++) {
				const elem = this._backgroundCoins[i];
				const rayonNumber = Math.floor(i / AmountCurrencyWizzStage.numberPerRayon);
				const rayon = AmountCurrencyWizzStage.backgroundCoinsPosition[rayonNumber];
				const placeInRayon = i - rayonNumber * AmountCurrencyWizzStage.numberPerRayon;

				const delayNumber = AmountCurrencyWizzStage.numberPerRayon - placeInRayon;
				const finalX = rayon.x + Math.cos(rayon.angle + Math.PI / 2) * rayon.force * placeInRayon;
				const finalY = rayon.y + Math.sin(rayon.angle + Math.PI / 2) * rayon.force * placeInRayon;
				firstTween.to(
					elem.docking,
					0.6,
					{ delay: delayNumber * 0.1, x: finalX, y: finalY, ease: Back.easeOut },
					0
				);
				elem.rotation = rayon.startRotation + rayon.rotationForce * placeInRayon;
			}
			firstTween.play();

			this.sparkles.visible = true;
			this._sparkles.forEach((elem) => {
				elem.scaleXY = 1;
				elem.alpha = 1;
				elem.docking.x = elem.docking.y = 0;
				TweenMax.to(elem, 1, { scaleX: 2, scaleY: 8, delay: 0.1, ease: Power0.easeNone });
				TweenMax.to(elem.docking, 1, {
					delay: 0.1,
					ease: Power0.easeNone,
					x: 1000 * Math.cos(elem.rotation + Math.PI / 2),
					y: 1000 * Math.sin(elem.rotation + Math.PI / 2),
				});
				TweenMax.to(elem, 0.2, { alpha: 0, delay: 0.9 });
			});
		}

		// set the overall time of the animation
		TweenMax.to(this, 6, {
			onComplete: () => {
				box.emit(EResultBoxEvents.StageWillExit, this);
				done(true);
			},
		});
	}
}
