import { Container } from "pixi.js";
import { Linear, TweenMax } from "gsap";
import * as i18n from "i18next";
import type { TextField } from "@gdk/core-pixi";

export class Timer extends Container {
	/**
	 * timer text with the countdown
	 */
	public timerText: TextField = null;

	/**
	 * "game canceled after" text
	 */
	public txt: TextField = null;

	/**
	 * "let's go!" text
	 */
	public txtGo: TextField = null;
	/**
	 * current left time
	 */
	protected time: number;

	/**
	 * the time tween
	 */
	protected tween: TweenMax;

	/**
	 * the time promise
	 */
	protected tweenPromise: Promise<void>;

	/**
	 * The i18n key
	 */
	private _key: string;

	/**
	 * the timer start time
	 */
	private _startTime: number;

	/**
	 * Set the starting time in seconds
	 * @param time in seconds
	 */
	public set startTime(time: number) {
		this._startTime = time;
		this.timerText.visible = true;
		this.timerText.text = Math.floor(time).toString();
	}

	/**
	 * Set the formating string key (count var used for time display)
	 * @param key the i18n key
	 */
	public set key(key: string) {
		this._key = key;
	}

	/**
	 * Start the timer
	 * @param time, optional, set the start time in seconds
	 */
	public async start(time?: number): Promise<void> {
		if (time) {
			this.startTime = time;
		}

		this.reset();
		this.timerText.visible = true;

		this.time = this._startTime;

		this.tweenPromise = new Promise<void>((solve) => {
			this.tween = TweenMax.to(this, time, {
				time: 0,
				ease: Linear.easeNone,
				onUpdate: () => {
					if (this._key) {
						this.timerText.text = i18n.t(this._key, { count: Math.floor(this.time) });
					} else {
						this.timerText.text = Math.floor(this.time).toString(10);
					}
				},
				onComplete: solve,
			});
		});
	}

	/**
	 * Reset the timer to the startTime without start
	 */
	public reset(): void {
		this.startTime = this._startTime;

		if (this.tween) {
			this.tween.kill();
		}
	}

	/**
	 * Pause the timer
	 */
	public pause(): void {
		this.tween.pause();
	}

	/**
	 * Unpause the timer
	 */
	public async unpause(): Promise<void> {
		this.tween.play();
		await this.tweenPromise;
	}
}
