import { Container } from "pixi.js";
import type { WebGLRenderer, CanvasRenderer, Sprite } from "pixi.js";
import type { TextField } from "@gdk/core-pixi";

export class Time extends Container {
	/**
	 * Label textfield used to draw the purse value (from the editor)
	 */
	public label: TextField = null;
	private iconBlack: Sprite = null;
	private iconWhite: Sprite = null;

	private _hours: number = -1;
	private _minutes: number = -1;

	/**
	 * Constructor
	 */
	public constructor() {
		super();
	}

	/**
	 * sets the clock color to black or white
	 */
	public set color(value: string) {
		this.iconBlack.visible = value === "black" || value === "#000000" || value === "#000";
		this.iconWhite.visible = !this.iconBlack.visible;
		this.label.styles.default.fill = value;
		this.label.text = this.label.text;
	}

	/**
	 * Sets the current time
	 * @param ms ellapsed milliseconds
	 */
	public setTime(ms: number): void {
		// apply time text
		const d = new Date(ms);
		const hours = d.getHours();
		const minutes = d.getMinutes();

		if (hours === this._hours && minutes === this._minutes) {
			return;
		}

		// Update text
		this.label.text = `${(hours < 10 ? "0" : "") + hours}:${(minutes < 10 ? "0" : "") + minutes}`;

		// Update cache
		this._hours = hours;
		this._minutes = minutes;
	}

	/**
	 * Render text with WebGL
	 * @param renderer the pixi.js renderer (WebGLRenderer)
	 * @override PIXI.Sprite.renderWebGL
	 */
	public renderWebGL(renderer: WebGLRenderer): void {
		this.setTime(Date.now());
		super.renderWebGL(renderer);
	}

	/**
	 * Render text with Canvas renderer
	 * @param renderer the pixi.js renderer (CanvasRenderer)
	 * @override PIXI.Sprite._renderCanvas
	 */
	public _renderCanvas(renderer: CanvasRenderer): void {
		this.setTime(Date.now());
		super._renderCanvas(renderer);
	}
}
