import { Sprite, Container } from "pixi.js";
import { TextArea, Docking } from "@gdk/core-pixi";
import { Loader, Numeral, Amount } from "@gdk/core";
import { TweenMax, Back } from "gsap";
import { Howl } from "howler";
import type { IStyles } from "@gdk/core-pixi";

import { ResultStage } from "./resultStage";
import { EResultBoxEvents } from "./resultBox";
import { TweenUtils } from "../../";
import type { ResultBox } from "./resultBox";

export interface IAmountResultThreshold {
	raysCount: number;
	amount: number;
}

export interface IThresholdConfig {
	amount: number;
	duration?: number;
	animated?: boolean;
}

export interface IAmountResultStyle {
	smallRaySprite: string;
	bigRaySprite: string;
	middleSprite: string;
	amountStyle: IStyles;
	amountDisappears?: boolean;
	amountVerticalMargin?: number;
	raysPerThreshold: number;
	thresholds: Array<number | IThresholdConfig>;

	appearTime?: number;
	stayTime?: number;
	disappearTime?: number;

	sounds?: {
		incrementation?: Howl | string;
	};
	minimumFractionDigits?: number;
}

export class AmountResultStage extends ResultStage {
	// Public members
	public rays: Sprite[] = [];
	public raysContainer: Container;
	public fastRaysContainer: Container;
	public middleSprite: Container;
	public amount: TextArea;

	public config: IAmountResultStyle;

	// Protected members
	protected currentAmount: number = 0;

	// Private members
	private _totalRaysCount: number = 0;
	private _averageRaysCount: number = 0;
	private _incrementationSound: Howl;
	private _animatedThresholds: Array<number | IThresholdConfig>;
	/**
	 * Constructor
	 */
	public constructor(config: IAmountResultStyle) {
		super();

		// Create rays
		this._animatedThresholds = config.thresholds.filter(
			(t) => typeof t === "number" || t.animated !== false
		);
		this._totalRaysCount = config.raysPerThreshold * this._animatedThresholds.length;
		this._averageRaysCount = config.raysPerThreshold * (this._animatedThresholds.length - 1);

		for (let i = 0; i < this._totalRaysCount; i++) {
			// Add bigger rays when exceeding the average thresholds
			const big = i >= this._averageRaysCount;
			const rayToUse = big ? config.bigRaySprite : config.smallRaySprite;

			let ray: Sprite = null;
			ray = Sprite.fromFrame(rayToUse);
			ray.pivot.y = ray.texture.height;
			ray.pivot.x = ray.texture.width / 2;

			this.rays.push(ray);
		}

		this.raysContainer = new Container();
		this.raysContainer.dockAsPoint = true;
		this.raysContainer.docking = new Docking(Docking.centerAll);
		this.raysContainer.pivot.x = this.raysContainer.width / 2;
		this.raysContainer.pivot.y = this.raysContainer.height / 2;
		this.addChild(this.raysContainer);

		this.fastRaysContainer = new Container();
		this.fastRaysContainer.dockAsPoint = true;
		this.fastRaysContainer.docking = new Docking(Docking.centerAll);
		this.fastRaysContainer.pivot.x = this.fastRaysContainer.width / 2;
		this.fastRaysContainer.pivot.y = this.fastRaysContainer.height / 2;
		this.addChild(this.fastRaysContainer);

		// Middle sprite
		this.middleSprite = Sprite.fromFrame(config.middleSprite);
		this.middleSprite.docking = new Docking(Docking.centerAll);
		this.middleSprite.pivot.x = (this.middleSprite as Sprite).texture.width / 2;
		this.middleSprite.pivot.y = (this.middleSprite as Sprite).texture.height / 2;

		// Main text
		this.amount = new TextArea("", config.amountStyle);
		this.amount.docking = new Docking(Docking.centerAll);

		// Sound
		config.sounds = config.sounds || {};

		if (typeof config.sounds.incrementation === "string") {
			this._incrementationSound = Loader.get<Howl>(config.sounds.incrementation);
		} else if (config.sounds.incrementation instanceof Howl) {
			this._incrementationSound = config.sounds.incrementation;
		}

		// Misc.
		this.config = config;

		this.dockAsPoint = true;
		this.visible = false;
	}

	/**
	 * On show the result box
	 * @param box the result box reference
	 * @param done the callback to call when stage animation done
	 */
	public onShow(box: ResultBox, done: (stop: boolean) => void): void {
		// find matching threshold
		let threshold: IThresholdConfig = { amount: 0 };
		for (const _threshold of this.config.thresholds) {
			const amount = _threshold instanceof Object ? _threshold.amount : _threshold;
			if (box.amount.value > amount) {
				threshold = _threshold instanceof Object ? _threshold : { amount: _threshold };
			}
		}

		if (threshold.animated !== false) {
			this.showAnimated(box, done);
		} else {
			this.showStatic(box, threshold, done);
		}
	}

	protected showStatic(
		box: ResultBox,
		threshold: IThresholdConfig,
		done: (stop: boolean) => void
	): void {
		this.amount.visible = false;
		this.amount.text = Numeral.currency(box.amount, {
			minimumFractionDigits: this.config.minimumFractionDigits ?? 2,
		});

		this.amount.scaleXY = 1;
		this.amount.docking.y = 75 + (this.config.amountVerticalMargin ?? 0);
		this.amount.visible = true;
		this.scaleXY = 1;

		this.removeChild(this.middleSprite);
		this.addChild(this.amount);

		const duration = threshold.duration ?? 3;

		TweenUtils.wait(duration).then(() => {
			done(false);
		});
	}

	protected showAnimated(box: ResultBox, done: (stop: boolean) => void): void {
		if (this._incrementationSound) {
			this._incrementationSound.play();
		}

		// Get threshold
		let thresholdIndex = 0;
		let currentThresholdIndex = thresholdIndex;
		let currentThreshold = this._animatedThresholds[currentThresholdIndex];

		// Get indexes of rays per thersholds
		const raysPerThreshold = this.config.raysPerThreshold;
		let startThresholdIndex = currentThresholdIndex * raysPerThreshold;
		let endThresholdIndex = (currentThresholdIndex + 1) * raysPerThreshold;

		// Set animations's duration
		let appearTime = this.config.appearTime || 2;
		let stayTime = this.config.stayTime || 1;
		let disappearTime = this.config.disappearTime || 1;

		let totalTime = -1;
		this._animatedThresholds.forEach((t, index) => {
			const amount = t instanceof Object ? t.amount : t;
			if (box.amount.value > amount) {
				thresholdIndex = index;
				appearTime = 2 + 2 * index;
				if (t instanceof Object && "duration" in t) {
					totalTime = t.duration;
				}
			}
		});

		if (totalTime > 0) {
			appearTime = totalTime - disappearTime - stayTime;
			if (appearTime < 3) {
				appearTime = 3;
				const actualTotalTime = appearTime + stayTime + disappearTime;
				const c = totalTime / actualTotalTime;
				stayTime *= c;
				disappearTime *= c;
				appearTime *= c;
			}
		} else {
			const actualTotalTime = appearTime + stayTime + disappearTime;
			totalTime = actualTotalTime;
		}

		this.amount.docking.y = this.config.amountVerticalMargin ?? 0;

		// Add children
		this.middleSprite.scaleXY = 1;
		this.amount.scaleXY = 1;
		this.raysContainer.scaleXY = 0;
		this.fastRaysContainer.scaleXY = 0;
		this.raysContainer.rotation = 0;
		this.fastRaysContainer.rotation = 0;

		this.addChild(this.raysContainer);
		this.addChild(this.fastRaysContainer);
		this.addChild(this.middleSprite);
		this.addChild(box.particleSystem);
		this.addChild(this.amount);

		// Animate current amount (this)
		this.currentAmount = 0;

		TweenMax.to(this, appearTime, {
			currentAmount: box.amount.value,
			onStart: () => {
				// Performances, keep canvas reference to avoid re-creating a canvas each update.
				this.amount.keepCanvasReference = true;
			},
			onComplete: () => {
				this.amount.keepCanvasReference = false;
			},
			onUpdate: () => {
				this.amount.text = Numeral.currency(
					new Amount({ value: this.currentAmount, currency: box.amount.currency }),
					{ minimumFractionDigits: this.config.minimumFractionDigits ?? 2 }
				);

				this.amount.pivot.x = this.amount.width / 2;
				this.amount.pivot.y = this.amount.height / 2;

				const currentThresholdAmount =
					currentThreshold instanceof Object ? currentThreshold.amount : currentThreshold;

				// Add rays if current amount superior to curren threshold
				if (
					(currentThreshold && this.currentAmount >= currentThresholdAmount) ||
					currentThresholdIndex === 0
				) {
					for (let i = startThresholdIndex; i < endThresholdIndex; i++) {
						i < this._averageRaysCount
							? this.fastRaysContainer.addChild(this.rays[i])
							: this.raysContainer.addChild(this.rays[i]);
					}

					for (let i = 0; i < endThresholdIndex; i++) {
						if (
							(startThresholdIndex < this._averageRaysCount &&
								i < this._averageRaysCount &&
								i % (endThresholdIndex / startThresholdIndex) > 0) ||
							i >= this._averageRaysCount
						) {
							this.rays[i].scaleXY = 0;
							TweenMax.to(this.rays[i], 1, {
								scaleXY: 1,
							});
						}
					}

					currentThreshold = this._animatedThresholds[++currentThresholdIndex];
					startThresholdIndex = currentThresholdIndex * raysPerThreshold;
					endThresholdIndex = (currentThresholdIndex + 1) * raysPerThreshold;

					let raysRotationDelta = (Math.PI * 2) / this.fastRaysContainer.children.length;
					let rotation = 0;
					this.fastRaysContainer.children.forEach((child) => {
						child.rotation = rotation;
						rotation += raysRotationDelta;
					});

					raysRotationDelta = (Math.PI * 2) / this.raysContainer.children.length;
					rotation = 0;
					this.raysContainer.children.forEach((child) => {
						child.rotation = rotation;
						rotation += raysRotationDelta;
					});
				}
			},
		});

		// Animate "this" scale
		this.scaleXY = 0;

		TweenMax.to(this, appearTime, {
			scaleXY: 1,
			onComplete: () => {
				box.emit(EResultBoxEvents.StageHasEntered, this);
				box.startParticleSystem();
				TweenMax.to(box.particleSystem, 0.5, {
					scaleXY: 1,
				});
			},
		});

		TweenMax.to(this.raysContainer, appearTime, {
			scaleXY: 1,
		});

		TweenMax.to(this.fastRaysContainer, appearTime, {
			scaleXY: 1,
		});

		TweenMax.to(this.raysContainer, totalTime, {
			rotation: Math.PI * 3,
		});

		TweenMax.to(this.fastRaysContainer, totalTime, {
			rotation: Math.PI * 6,
		});

		// Animate middle sprite
		this.middleSprite.rotation = 0;

		TweenMax.to(this.middleSprite, totalTime, {
			ease: Back.easeInOut.config(1.7),
			rotation: -Math.PI * 6,
		});

		const disappearingComponents = [this.raysContainer, this.fastRaysContainer, this.middleSprite];

		if (this.config.amountDisappears) {
			disappearingComponents.push(this.amount);
		}

		// Once done, animate ScaleXY from 1 to 0 of "this"
		TweenMax.to(disappearingComponents, disappearTime, {
			ease: Back.easeIn.config(1.7),
			scaleXY: 0,
			delay: appearTime + stayTime,
			onStart: () => {
				box.emit(EResultBoxEvents.StageWillExit, this);
			},
			onComplete: () => {
				this.removeChild(box.particleSystem);

				for (let i = this.raysContainer.children.length - 1; i >= 0; i--) {
					this.raysContainer.removeChild(this.raysContainer.children[i]);
				}
				for (let i = this.fastRaysContainer.children.length - 1; i >= 0; i--) {
					this.fastRaysContainer.removeChild(this.fastRaysContainer.children[i]);
				}

				done(box.amount.value <= 0);
			},
		});
	}

	/**
	 * Hide the stage
	 * @param progressive: If true, stage is hidden in 0.3s, otherwise in 0s.
	 */
	public onHide(progressive: boolean): Promise<void> {
		if (this.config.amountDisappears === false) {
			return Promise.resolve();
		}
		return super.onHide(progressive);
	}

	/**
	 * Hide the stage immediatly
	 */
	public hide(): void {
		super.hide();
		this.amount.text = "";
	}
}
