import { Amount, Logger } from "@gdk/core";
import type { IItgTicket, IItgPrize } from "@falcon/bridge";

import { TweenUtils } from "../tweenUtils";
import type { Express } from "./express";

export enum ETicketStatus {
	CLAIMED = "CLAIMED",
	OPENED = "OPENED",
}

export enum ETicketMode {
	DEFAULT,
	RESUME,
	REPLAY,
}

export enum EBetResponseAction {
	NO_ACTION = "NO_ACTION",
	SUSPENSION_TRIGGERED = "SUSPENSION_TRIGGERED",
	AUTOWITHDRAWAL_TRIGGERED = "AUTOWITHDRAWAL_TRIGGERED",
}

export abstract class Ticket<T = {}, U extends Express = Express> {
	public readonly demo: boolean;
	public readonly stake: Amount;
	public readonly symbol: string;
	public readonly receiptId: string;
	public readonly ticketId: number;
	public readonly mode: ETicketMode;
	public readonly serviceName: string;
	public readonly platformBetId: string;
	public status: ETicketStatus;
	public wonAmount: Amount;

	/**
	 * Flag from the lottery as SUSPENSION_TRIGGERED, AUTOWITHDRAWAL_TRIGGERED...
	 */
	public action: EBetResponseAction;
	/**
	 * RevelationData, has to be set to last data before updating the ticket
	 */
	public revelationData: T;
	/**
	 * The bet response object keeped reference.
	 */
	public readonly ticket: IItgTicket;

	private _updateInProgress: boolean = false;

	private _updatePromise: Promise<void>;

	/**
	 * Constructor
	 * @param ticket an IItgTicket
	 * @param demo wether or not the ticket is in demo mode.
	 */
	public constructor(
		ticket: IItgTicket,
		mode: ETicketMode = ETicketMode.DEFAULT,
		protected _game: U
	) {
		this.ticket = ticket;
		this.serviceName = ticket.serviceName;
		this.stake = new Amount(ticket.betAmount);
		this.demo = ticket.demo;
		this.wonAmount = ticket.wonAmount ? new Amount(ticket.wonAmount) : null;
		this.symbol = ticket.symbol;
		this.receiptId = ticket.receiptId;
		this.platformBetId = ticket.platformBetId;
		this.ticketId = ticket.id;
		this.mode = mode;

		// parse symbol
		this.parse();

		// check revelationDataValidity so that resume don't get stuck
		let revelationData = ticket.revelationData;
		if (revelationData !== null) {
			let invalidReason = "areRevelationDataValid returned false";
			let revelationDataValid: boolean;
			try {
				revelationDataValid = this.areRevelationDataValid(revelationData);
			} catch (e) {
				invalidReason = `areRevelationDataValid error: ${e.message}`;
				revelationDataValid = false;
			}

			if (!revelationDataValid) {
				Logger.getLogger().error(
					`${invalidReason} with data: \n\n${JSON.stringify(revelationData)}`
				);
				revelationData = null;
			}
		}
		this.revelationData = revelationData;

		if (ticket.action) {
			this.action = ticket.action as EBetResponseAction;
		}
		this.status = ticket.status as ETicketStatus;
	}

	/**
	 * Claim ITG ticket
	 */
	public async claim(): Promise<IItgTicket> {
		// if ticket is already claimed, don't call claim
		if (
			this.status === ETicketStatus.CLAIMED &&
			(this._game.betMode === "BET_MANUAL_CLAIM" || this.mode === ETicketMode.REPLAY)
		) {
			return this.ticket;
		}

		const response = await this._game.claim(
			{
				applicationId: "ITG",
				serviceName: this._game.gameConfig.lotteryGameCode,
				stake: {
					currency: this.stake.currency,
					value: this.stake.value,
				},
				demo: this.demo,
			},
			this.ticketId
		);

		this.status = response.status as ETicketStatus;
		this.wonAmount = response.wonAmount ? new Amount(response.wonAmount) : null;
		if (response.action) {
			this.action = response.action as EBetResponseAction;
		}

		return response;
	}

	/**
	 * Update ticket by sending revelation data to the server
	 * @param showError display an error message on request failure (default: true)
	 */
	public async update(showError: boolean = true): Promise<void> {
		if (this._updateInProgress || this.mode === ETicketMode.REPLAY) {
			return this._updatePromise;
		}
		this._updateInProgress = true;
		await TweenUtils.wait(0.5);
		await this._updateTicketNow(showError);
	}

	private async _updateTicketNow(showError: boolean = true): Promise<void> {
		this._updateInProgress = false;
		// never update a claimed ticket
		if (this.status === ETicketStatus.CLAIMED) {
			return;
		}
		await this._game.updateTicket(
			{
				applicationId: "ITG",
				demo: this.ticket.demo,
				serviceName: this._game.gameConfig.lotteryGameCode,
				stake: {
					currency: this.stake.currency,
					value: this.stake.value,
				},
				revelationData: this.revelationData,
			},
			this.ticketId,
			showError
		);
	}

	/**
	 * Check revalationData validity
	 * Must be implemented in Ticket class in the game
	 * Must return true if revelationData format is valid and handled, must return false otherwise
	 * Parsed symbol data are available at this time
	 * If not implemented in the game it will return false hence revelation data will be skipped
	 * @param revelationData
	 */
	protected abstract areRevelationDataValid(revelationData: T): boolean;

	/**
	 * Parse symbol
	 * To be implemented in the game Ticket class
	 */
	protected abstract parse(): void;

	/**
	 * Return the prize object of the ticket
	 */
	public get prize(): IItgPrize {
		return this.ticket.prize;
	}
}
