import { Container, mesh, Texture, Sprite, Point } from "pixi.js";
import {
	Button,
	Docking,
	TextField,
	DockingBounds,
	EAlign,
	Stackable,
	Resize,
} from "@gdk/core-pixi";

import type { ShineableButton } from "./shineableButton";

const FILL = "card/fill";
const BORDER = "card/outline";
const CLOSE = "backgrounds/white/circle";
const CROSS = "icons/color/cross";
const RADIUS = 30;
const MARGIN = 30;
const DEFAULT_COLOR = "#000000";

export interface IDSPopinOptions {
	content?: Container;
	title: string;
	titleColor?: string;
	onClose?: () => Promise<void>;
}

export abstract class DSPopin extends Container {
	public static minWidth: number = 768;
	public static textFontSize: number = 36;
	public static titleFontSize: number = 50;
	public static buttonFontSize: number = 36;
	public static menuButtonFontSize: number = 32;

	private fill: mesh.NineSlicePlane;
	private border: mesh.NineSlicePlane;
	private onClose?: () => Promise<void>;

	protected title: TextField;
	protected content: Container;
	protected closeButton: Button;
	protected buttonStack: Stackable<Button | ShineableButton>;

	public constructor(options: IDSPopinOptions) {
		super();

		this.content = options.content || new Container();
		if (!this.content.dockingBounds) {
			this.content.dockingBounds = new DockingBounds(0, 0);
		}
		this.onClose = options.onClose;

		this.buttonStack = new Stackable({ direction: "right", spacing: 20 });
		this.buttonStack.width = 0;
		this.buttonStack.height = 0;
		this.buttonStack.dockingBounds = new DockingBounds();
		this.buttonStack.docking = new Docking(Docking.bottomCenterHorizontal);
		this.content.addChild(this.buttonStack);

		this.fill = new mesh.NineSlicePlane(Texture.fromFrame(FILL), RADIUS, RADIUS, RADIUS, RADIUS);
		this.addChild(this.fill);

		this.border = new mesh.NineSlicePlane(
			Texture.fromFrame(BORDER),
			RADIUS,
			RADIUS,
			RADIUS,
			RADIUS
		);
		this.addChild(this.border);

		this.closeButton = new Button({
			idleTexture: CLOSE,
			overTexture: CLOSE,
			clickedTexture: CLOSE,
			disabledTexture: CLOSE,
		});
		this.closeButton.isNineSlice = true;
		this.closeButton.scaleXY = 1.1;
		this.closeButton.dockingBounds = new DockingBounds(1, 1);
		this.closeButton.docking = new Docking(Docking.topRight, -20, 20, false);
		this.closeButton.pivot = new Point(50, 45);
		const cross = Sprite.fromFrame(CROSS);
		cross.x = 50;
		cross.y = 45;
		this.closeButton.addChild(cross);
		this.addChild(this.closeButton);

		this.closeButton.on("pointertap", () => {
			this.executeButtonCallback(this.onClose);
		});

		const titleColor = options.titleColor ? options.titleColor : DEFAULT_COLOR;
		this.title = new TextField(
			options.title,
			{
				default: {
					fontName: "OpenSans-SemiBold",
					fontSize: DSPopin.titleFontSize,
					fill: titleColor,
					stroke: titleColor,
					strokeOffsetX: 0,
					strokeOffsetY: 0,
					strokeWidth: 1,
				},
			},
			{
				noUpdate: true,
			}
		);
		this.title.freeze();
		this.title.wordWrap = true;
		this.title.width = DSPopin.minWidth;
		this.title.align = EAlign.Center;
		this.title.docking = new Docking(Docking.topCenterHorizontal, 0, 20);
		this.title.unfreeze();
		this.addChild(this.title);

		this.addChild(this.content);

		this.dockingBounds = new DockingBounds();
	}

	/**
	 * Resize the popin to fit content
	 *
	 * @access public
	 */
	public resizePopin(): void {
		const maxSize = Math.max(768, this.dockingBounds.width) - MARGIN * 2;
		const remainingSpace = maxSize - this.buttonStack.width - this.buttonStack.spacing;
		const spacing = Math.min(remainingSpace / 3, 30);

		this.buttonStack.spacing = spacing;
		this.buttonStack.dockingBounds.width = this.buttonStack.width;
		this.buttonStack.dockingBounds.height = this.buttonStack.docking.y = this.buttonStack.height;

		let height = this.title.y + this.title.height + MARGIN;
		let width = this.title.width + 2 * MARGIN;

		this.content.y = height;
		height += this.content.dockingBounds.height + MARGIN + this.buttonStack.height;
		if (this.content.dockingBounds.width + 2 * MARGIN > width) {
			width = this.content.dockingBounds.width + 2 * MARGIN;
		}

		this.fill.height = height;
		this.fill.width = width;
		this.border.height = height;
		this.border.width = width;
		this.dockingBounds.height = height;
		this.dockingBounds.width = width;

		this.updateTransform();

		this.content.x = (this.dockingBounds.width - this.content.dockingBounds.width) * 0.5;

		// this.buttonStack.x = (this.dockingBounds.width - this.buttonStack.width) * 0.5;
		// this.buttonStack.y = this.dockingBounds.height - this.buttonStack.height;
		// this.buttonStack.x = (this.content.dockingBounds.width - this.buttonStack.width) * .5;
	}

	/**
	 * Show the popin
	 *
	 * @access public
	 */
	public show(): void {
		this.activateButtons(true);

		if (this.parent) {
			this.resizePopin();
		}

		this.visible = true;
	}

	protected abstract activateButtons(activate: boolean): void;

	protected async executeButtonCallback(callback: () => Promise<void>): Promise<void> {
		this.activateButtons(false);

		try {
			if (callback) {
				await callback();
			}

			this.visible = false;
		} catch (e) {
			// Empty
		}
	}
}
