import { Texture } from "pixi.js";
import i18n from "i18next";
import { Tools } from "@gdk/core";
import { Button, Docking, ContainerExporter, ContainerImporter, TextField } from "@gdk/core-pixi";
import type { IStyles, IButtonStyle } from "@gdk/core-pixi";

import { TextSlide, ELayout } from "./index";
import { getSlideStylePaddingValue } from "./textSlide";
import type { ITrigger } from "../../../com";
import type { Rules, ISlideTextStyle, ISlideStylePadding } from "./index";

export interface IAgreementStyle extends ISlideTextStyle {
	okButtonStyle?: IButtonStyle | Button;
	gameRuleStyle?: TextField | IStyles;
	gameRulePadding?: ISlideStylePadding<AgreementSlide>;
	generalRuleStyle?: TextField | IStyles;
	generalRulePadding?: ISlideStylePadding<AgreementSlide>;
}

export class AgreementSlide extends TextSlide {
	/**
	 * Ok button reference being added when the agreement slide is shown
	 */
	public okButton: Button;
	/**
	 * The general rules textfield working as a link
	 */
	public generalRule: TextField;
	/**
	 * The game rules textfield working as a link
	 */
	public gameRule: TextField;
	/**
	 * If the agreement slide should be auto-configured in the .configure method. True by default.
	 */
	public autoConfigure: boolean = true;
	/**
	 * The com trigger reference used to open general rules and game rules
	 */
	public com: ITrigger = null;

	// Private members
	private _gameRulePadding: ISlideStylePadding<AgreementSlide> = {};
	private _generalRulePadding: ISlideStylePadding<AgreementSlide> = {};

	/**
	 * Constructor
	 * @param com communication trigger
	 * @param style slide style
	 */
	public constructor(com: ITrigger, style: IAgreementStyle) {
		super(style);

		this.com = com;
		this.setName("rules:agreement");

		// OK Button
		this.okButton =
			style.okButtonStyle instanceof Button
				? style.okButtonStyle
				: new Button(style.okButtonStyle).setName("rules:ok");
		this.okButton.doNotExport = true;
		this.okButton.docking = new Docking(Docking.bottomRight);

		// Links
		this.generalRule =
			style.generalRuleStyle instanceof TextField
				? style.generalRuleStyle
				: new TextField(
						i18n.t("rules.agreement.generalRule"),
						style.generalRuleStyle || style.text.style
				  ).setName("rules:agreement:generalRules");
		this.generalRule.docking = new Docking(Docking.centerAll);
		this.generalRule.interactive = true;
		this.addChild(this.generalRule);
		this._generalRulePadding = Tools.assign(this._generalRulePadding, style.generalRulePadding);

		this.gameRule =
			style.gameRuleStyle instanceof TextField
				? style.gameRuleStyle
				: new TextField(
						i18n.t("rules.agreement.gameRule"),
						style.gameRuleStyle || style.text.style
				  ).setName("rules:agreement:gameRules");
		this.gameRule.docking = new Docking(Docking.centerAll);
		this.gameRule.interactive = true;
		this.addChild(this.gameRule);
		this._gameRulePadding = Tools.assign(this._gameRulePadding, style.gameRulePadding);

		this.generalRule.on("link", () => this.com.trigger("GLOBAL_RULES"));
		this.gameRule.on("link", () => this.com.trigger("GAME_RULES"));
	}

	/**
	 * Configures the slide in the given popup
	 * @param rules the popup containing the slide
	 */
	public configure(rules: Rules): void {
		// Add ok button which is not visible right now
		this.okButton.visible = false;
		rules.addChild(this.okButton);

		if (!this.autoConfigure) {
			return;
		}

		// Keep default behavior
		super.configure(rules);

		// handle text padding layout
		if (this.layout === ELayout.TextPadding) {
			delete this.gameRule.docking;

			if (this._gameRulePadding.left !== undefined || this._gameRulePadding.right !== undefined) {
				this.gameRule.x = getSlideStylePaddingValue(this._gameRulePadding.left, this);
				this.gameRule.width =
					this.dockingBounds.width -
					(getSlideStylePaddingValue(this._gameRulePadding.right, this) - this.gameRule.x);
			}
			if (this._gameRulePadding.top !== undefined || this._gameRulePadding.bottom !== undefined) {
				this.gameRule.y = getSlideStylePaddingValue(this._gameRulePadding.top, this);
				this.gameRule.height =
					this.dockingBounds.height -
					(getSlideStylePaddingValue(this._gameRulePadding.bottom, this) - this.gameRule.y);
			}

			delete this.generalRule.docking;

			if (
				this._generalRulePadding.left !== undefined ||
				this._generalRulePadding.right !== undefined
			) {
				this.generalRule.x = getSlideStylePaddingValue(this._generalRulePadding.left, this);
				this.generalRule.width =
					this.dockingBounds.width -
					(getSlideStylePaddingValue(this._generalRulePadding.right, this) - this.gameRule.x);
			}
			if (
				this._generalRulePadding.top !== undefined ||
				this._generalRulePadding.bottom !== undefined
			) {
				this.generalRule.y = getSlideStylePaddingValue(this._generalRulePadding.top, this);
				this.generalRule.height =
					this.dockingBounds.height -
					(getSlideStylePaddingValue(this._generalRulePadding.bottom, this) - this.gameRule.y);
			}

			return;
		}

		// Reposition text
		this.text.docking.y = -this.generalRule.height / 2;

		// Place links
		this.generalRule.docking.y = this.text.height / 2;
		this.generalRule.docking.x = -this.dockingBounds.width / 2 + this.generalRule.width + 25;

		this.gameRule.docking.y = this.text.height / 2;
		this.gameRule.docking.x = this.dockingBounds.width / 2 - this.gameRule.width - 25;
	}

	/**
	 * Called when showing the slide in the rules popup
	 * @param rules the popup containing the slide
	 */
	public onShow(rules: Rules): void {
		super.onShow(rules);

		this.okButton.visible = true;
		rules.addChild(this.okButton);

		rules.nextButton.visible = false;
		rules.popup.closeButton.visible = false;
	}

	/**
	 * Called when hiding the slide in the rules popup
	 * @param rules the popup containing the slide
	 */
	public onHide(rules: Rules): void {
		this.okButton.visible = false;

		rules.nextButton.visible = true;
		this.visible = false;

		rules.popup.closeButton.visible = true;
	}

	/**
	 * On destroy the slide
	 * @param rules the popup containing the slide
	 */
	public onDestroy(rules: Rules): void {
		rules.removeChild(this.okButton);
		rules.nextButton.visible = true;
	}

	/**
	 * Exports the agreement slide
	 */
	public export(): any {
		this.children.forEach((c) => (c.doNotExport = true));

		return Tools.assign(ContainerExporter.exportContainer(this), {
			type: "rules.AgreementSlide",
			hasBullet: this.hasBullet,
			agreement: {
				okButtonStyle: this.okButton.export(),
				generalRuleStyle: this.generalRule.export(),
				gameRuleStyle: this.gameRule.export(),
				text: this.text.export(),
				title: this.title.export(),
			} as IAgreementStyle,
		});
	}

	/**
	 * Creates a new agremment slide. Function used by project loader
	 * @param config the serialized data
	 * @param baseUrl the base url of the loader
	 */
	public static parse(config: any, baseUrl: string): AgreementSlide {
		const slide = new AgreementSlide(null, {
			// Minimal configuration
			okButtonStyle: {
				clickedTexture: Texture.WHITE,
				idleTexture: Texture.WHITE,
				overTexture: Texture.WHITE,
				disabledTexture: Texture.WHITE,
			},
			text: {
				style: config.agreement.text.styles,
			},
		});
		slide.autoConfigure = false;

		Button.parse(config.agreement.okButtonStyle, baseUrl, slide.okButton);

		ContainerImporter.import(config.agreement.title, baseUrl, slide.title);
		ContainerImporter.import(config.agreement.text, baseUrl, slide.text);
		ContainerImporter.import(config.agreement.generalRuleStyle, baseUrl, slide.generalRule);
		ContainerImporter.import(config.agreement.gameRuleStyle, baseUrl, slide.gameRule);

		ContainerImporter.importContainer(config, baseUrl, slide);

		if (config.hasBullet !== undefined) {
			slide.hasBullet = config.hasBullet;
		}

		return slide;
	}
}
