import { Container, Sprite } from "pixi.js";
import { Button, TextArea, EAlign, Docking } from "@gdk/core-pixi";
import type { IButtonStyle, IStyles } from "@gdk/core-pixi";

/**
 * Home text style, represents a text in the home component
 */
export interface IHomeTextStyle {
	text?: string;
	styles: IStyles;
}

/**
 * Home button style, represents a the style of the play button
 */
export interface IHomeButtonStyle {
	style: IButtonStyle;
	text?: IHomeTextStyle;
}

/**
 * Represents the overall style of the home component
 */
export interface IHomeStyle {
	playButton: Button | IHomeButtonStyle;
	gameName: string | Container | IHomeTextStyle;
	claim: string | Container | IHomeTextStyle;
}

export class Home extends Container {
	// Public members
	public playButton: Button;
	public gameName: TextArea | Sprite | Container;
	public claim: TextArea | Sprite | Container;

	/**
	 * Constructor
	 * @param config The home style
	 */
	public constructor(config: IHomeStyle) {
		// PIXI.Container
		super();

		// Game name
		if (typeof config.gameName === "string") {
			// We should build the sprite
			this.gameName = Sprite.fromFrame(config.gameName);
			this.gameName.pivot.x = (this.gameName as Sprite).texture.width / 2;
			this.gameName.pivot.y = (this.gameName as Sprite).texture.height / 2;
			this.gameName.docking = new Docking(Docking.centerAll);
		} else if (config.gameName instanceof Container) {
			// It is a sprite
			this.gameName = config.gameName;
		} else {
			// Should be a textfield
			this.gameName = new TextArea(config.gameName.text || "", config.gameName.styles, {
				align: EAlign.Center,
			});
			this.gameName.docking = new Docking(Docking.centerAll);
		}

		this.addChild(this.gameName);

		// Claim
		if (typeof config.claim === "string") {
			// We should build the sprite
			this.claim = Sprite.fromFrame(config.claim);
			this.claim.docking = new Docking(Docking.centerAll);
		} else if (config.claim instanceof Container) {
			// It is a sprite
			this.claim = config.claim;
		} else {
			// Should be a textfield
			this.claim = new TextArea(config.claim.text || "", config.claim.styles, {
				align: EAlign.Center,
			});
			this.claim.docking = new Docking(Docking.centerAll);
		}

		this.addChild(this.claim);

		// Button
		if (config.playButton instanceof Button) {
			this.playButton = config.playButton;
		} else {
			this.playButton = new Button(
				config.playButton.style,
				config.playButton.text.styles,
				config.playButton.text.text
			);
			this.playButton.docking = new Docking(Docking.centerAll);
		}
		// Because of tracking, the play button must always have the same name, so we force the name
		this.playButton.setName("play");

		this.addChild(this.playButton);

		// Placing elements
		this.gameName.docking.y = -this.claim.height / 2 - this.gameName.height / 2 - 35;
		this.playButton.docking.y =
			this.claim.height / 2 + this.playButton.background.texture.height / 2 + 35;
	}
}
