import i18n from "i18next";
import type { IPrsTicketInfos } from "@falcon/bridge/typings/prs";
import type {
	IMtrModel,
	IMtrSession,
	IMtrBetRequest,
	IMtrBetResult,
	IService,
	IMtrReplaySession,
	IBetRequest,
} from "@falcon/bridge";
import type { IAmount, IGameConf } from "@gdk/core";
import type { ApplicationOptions } from "pixi.js";

import { EPopinLevel, PopinButtonAction, EMessageType } from "../../com";
import { AbstractGame } from "../abstractGame";
import type { MotorsModel } from "./model";
import type { MotorsSession } from "./session";

/**
 * Action game.
 * @template TModel Model typings.
 * @template TGameData Session typings.
 * @template TEventResponse "evr" typings.
 * @template TEventParameters Event parameters.
 * @template TMotorsSession override session typings.
 */
export class Action<
	TModel,
	TGameData,
	TEventResponse,
	TEventParameters,
	TMotorsSession extends MotorsSession<
		TGameData,
		TEventParameters,
		TEventResponse,
		TModel
	> = MotorsSession<TGameData, TEventParameters, TEventResponse, TModel>
> extends AbstractGame {
	// Public members
	public model: MotorsModel<TModel>;
	public session: TMotorsSession;

	public selectedStake: IAmount;

	/**
	 * Constructor
	 * @param gameConfig: the game configuration (game.conf.json)
	 * @param width: the canvas width
	 * @param height: the canvas height
	 * @param noWebGL: enable/disable WebGL (to force Canvas)
	 * @param options: the Pixi.js applicaiton options
	 */
	public constructor(
		gameConfig: IGameConf,
		width: number,
		height: number,
		noWebGL: boolean,
		options: ApplicationOptions
	) {
		// Initialize
		super(gameConfig, width, height, noWebGL, options);
		this.selectedStake =
			gameConfig.stakes[gameConfig.defaultStakeIndex ? gameConfig.defaultStakeIndex : 0];
	}

	/**
	 * Returns the Motors model of the given service name (optional, default is taken from game config).
	 * @param parameters: the request parameters (applicationId and service name)
	 */
	public async getModel(parameters: IService = {}, demoMode?: boolean): Promise<IMtrModel<TModel>> {
		return await this.com.getModel<TModel>(parameters, demoMode);
	}

	/**
	 * Returns the Presto ticket information for the current player (token dependant).
	 */
	public async getPrsTicketInfos(): Promise<IPrsTicketInfos> {
		return await this.com.getPrsTicketInfos();
	}

	/**
	 * Returns the Motors available sessions
	 * @param parameters: the request parameters (applicationId and service name)
	 */
	public getSession(
		parameters: IService = {},
		demoMode: boolean = false
	): Promise<IMtrSession<TGameData, TEventParameters, TEventResponse>> {
		return this.com.getMtrSession<TGameData, TEventParameters, TEventResponse>(
			parameters,
			undefined,
			demoMode
		);
	}

	/**
	 * Looks for session from bet it.
	 * Returns a IMtrReplaySession if existing or undefined otherwise.
	 * @param parameters
	 */
	public getSessionFromBetId(
		parameters: IService = {}
	): Promise<IMtrReplaySession<TEventResponse, TEventParameters>> {
		return this.com.getMtrSessionToReplay<TEventResponse, TEventParameters>(parameters);
	}

	/**
	 * Starts a Motors session (bet)
	 * @param parameters: the start session parameters
	 */
	public async bet(
		parameters: IMtrBetRequest<TEventParameters>
	): Promise<IMtrBetResult<TEventResponse>> {
		// Bet
		const bet = await this.com.createMtrBet<TEventResponse, TEventParameters>(parameters);

		if (bet.message) {
			this.com.messageManager.push(bet.message);
		}

		// Update purse
		await this.getPurse(true);

		return bet;
	}

	/**
	 * Has to be called before buying a ticket in real money
	 * @returns promise
	 * The lottery must resolve whenever it wants the player to continuer with his bet
	 * The lottery must reject if ever it wants to cancel the player's bet process
	 */
	public async preBet(betRequest: IBetRequest): Promise<void> {
		return this.com.preBet(betRequest);
	}

	/**
	 * Shows the continue game popup
	 * @param done callback once the user closes the popup
	 */
	public async showResumeGamePopup(): Promise<void> {
		await this.com.messageManager.display({
			code: "MTR.RESUME",
			type: EMessageType.Immediate,
			popup: {
				level: EPopinLevel.Info,
				text: i18n.t("motors.resume.text"),
				buttons: [
					{
						label: i18n.t("motors.resume.button"),
						action: PopinButtonAction.ClosePopin,
					},
				],
			},
		});
	}

	/**
	 * Execute get context
	 * @param parameters
	 * @todo support gamif :)
	 */
	public async getContext(parameters: any): Promise<any> {
		const serviceName = parameters.serviceName || this.gameConfig.lotteryGameCode;
		const context = await this.com.getContext<TModel>(serviceName);

		return context;
	}
}
