import { Container, Rectangle, Point } from "pixi.js";
import { TweenMax, Cubic } from "gsap";
import i18next from "i18next";
import type { TextField, Button } from "@gdk/core-pixi";
import type { mesh, Sprite } from "pixi.js";

/**
 * the popin buttons options
 */
export interface IMessagePopinButtonOptions {
	name: string; // the button name (used for tracking)
	label: string; // the label (text)
	value: unknown; // the value returned by this button.
	type: string; // the type (primary, secondary, cancel...) must exist in editor.
	exit?: boolean; // if the button trigger a rejection when clicked
}

/**
 * the Popin required options
 */
export interface IMessagePopinOptions {
	name: string; // the popin name (used for tracking)
	title: string; // the popin title
	body: string; // the popin body
	buttons: IMessagePopinButtonOptions[]; // the popin buttons
}

/**
 * the result returned by the Promise
 */
export interface IMessagePopinResult {
	result?: unknown; // if there is a result
	error?: unknown; // if there is an error
}

interface IButton {
	[name: string]: Button;
}

export class MessagePopin extends Container {
	public static ERROR_NO_POPIN: string = "Error: requested popin doesn't exist in i18n";

	/**
	 * the padding (rectangle) between popin borders and it's content
	 */
	public padding: Rectangle = new Rectangle(35, 20, 35, 30);

	/**
	 * the spacing (Point) between popin contents
	 */
	public spacing: Point = new Point(15, 30);

	private background: mesh.NineSlicePlane = null;
	private title: TextField = null;
	private body: TextField = null;
	private overlay: Sprite = null;
	private _hidden: boolean = false;
	private _buttonsStack: Button[] = [];
	private _buttons: Button[] = [];
	private _buttonsByName: IButton[] = []; // Button[] = [];

	private _buttonsStackMinSize: number = 0;
	private _pending: IMessagePopinOptions;
	private _pendingResolve: (value?: unknown) => void;
	private _pendingReject: (reason?: unknown) => void;

	private _primary: Button = null;
	private _secondary: Button = null;
	private _cancel: Button = null;
	private _close: Button = null;

	/**
	 * called once when loaded, init editor components
	 */
	public onLoaded(): void {
		const buttonsTemplates = [this._primary, this._secondary, this._cancel, this._close];
		buttonsTemplates.forEach((button) => {
			this._buttonsStackMinSize = Math.max(this._buttonsStackMinSize, button.nineSliceMinSize.x);
			button.visible = false;
			this.removeChild(button);
		});

		// Force invisible
		this.hide(0);
	}

	/**
	 * will open a popin based on i18n object
	 * @param key the i18n key
	 */
	public async openFromi18n(
		key: string,
		options: i18next.TranslationOptions<object> = {}
	): Promise<IMessagePopinResult> {
		const popinSettings = this.getFromi18n(key, options);
		if (popinSettings) {
			return this.open(popinSettings);
		} else {
			return { error: MessagePopin.ERROR_NO_POPIN };
		}
	}

	/**
	 * open a message popin (text + optionnal buttons)
	 * will get the popin settings based on an object from i18n
	 * @param key the object key in i18n
	 * @param options i18n options like replacement properties.
	 */
	public getFromi18n(key: string, options: i18next.TranslationOptions<object> = {}): any {
		return i18next.t(key, Object.assign(options, { returnObjects: true, defaultValue: null }));
	}

	/**
	 * open a message popin (text + optionnal buttons)
	 * will get the popin settings based on an object
	 * @param options the popin options
	 */
	public async open(options: IMessagePopinOptions): Promise<IMessagePopinResult> {
		this._pending = options;

		if (!this.parent) {
			return Promise.reject({
				error: `popin isn't in the display list (it doesn't have a parent)`,
			});
		}

		this.addOverlay();
		this.removeButtons();

		// const primary = this.primaryButton.clone();
		this._pending.buttons.forEach((datas) => {
			// get a new button from the prefabs
			const button = this[`_${datas.type}`].clone();

			if (button) {
				// for the tracking :
				button.setName(`${options.name}:${datas.name}`);
				// configure the button
				button.visible = true;
				this.addChild(button);

				if (datas.type !== "close") {
					button.label = datas.label;
					this._buttonsStack.push(button);
				}
				this._buttons.push(button);
				this._buttonsByName[datas.name] = button;

				button.on("pointertap", async () => {
					await this.hide();
					if (datas.exit || datas.type === "close") {
						this._pendingReject(datas.value);
					} else {
						this._pendingResolve(datas.value);
					}
				});
			}
		});

		// Resize the settings background
		this._resizeBackground();

		await this.show();

		return await new Promise((resolve, reject) => {
			this._pendingResolve = resolve;
			this._pendingReject = reject;
		}).then(
			(result) => {
				return { result };
			},
			(error) => {
				return { error };
			}
		);
	}

	/**
	 * will show the popin
	 * @param duration the transition duration
	 */
	public async show(duration: number = 0.2): Promise<void> {
		return new Promise((resolve) => {
			if (this._hidden) {
				this.visible = true;
				TweenMax.fromTo(
					this,
					duration,
					{ scaleXY: 0.85, alpha: 0 },
					{
						scaleXY: 1,
						alpha: 1,
						ease: Cubic.easeOut,
						onComplete: () => {
							this.buttonsSetEnabled(true);
							resolve();
						},
					}
				);
			} else {
				resolve();
			}

			this._hidden = false;
		});
	}

	/**
	 * will hide the popin
	 * @param duration the transition duration
	 */
	public async hide(duration: number = 0.2): Promise<void> {
		return new Promise((resolve) => {
			this.buttonsSetEnabled(false);
			TweenMax.fromTo(
				this,
				duration,
				{ scaleXY: 1, alpha: 1 },
				{
					scaleXY: 1.075,
					alpha: 0,
					ease: Cubic.easeOut,
					onComplete: () => {
						this.visible = false;
						this.removeOverlay();
						resolve();
						this.removeButtons();
					},
				}
			);
			this._hidden = true;
		});
	}

	/**
	 * give read only access to the popin buttons list (just in case)
	 */
	public get buttons(): IButton[] {
		return this._buttonsByName;
	}

	private _resizeBackground(): void {
		this.title.docking.y = this.padding.top;
		this.title.height = 0;
		this.title.text = ``;
		this.title.width = 0;
		this.body.height = 0;
		this.body.text = ``;
		this.body.width = 0;

		const buttons = this._buttonsStack.filter((button) => button.visible);

		let maxButtonHeight = 0;
		let maxButtonWidth = 0;

		buttons.forEach((button) => {
			// resize button at min size
			button.nineSliceMinSize.x = this._buttonsStackMinSize;
			button.label = button.label;

			maxButtonHeight = Math.max(maxButtonHeight, button.height);
			maxButtonWidth = Math.max(maxButtonWidth, button.width);
		});

		const buttonsWidth = buttons.length * maxButtonWidth + this.spacing.x * (buttons.length - 1);

		this.title.width = Math.max(buttonsWidth, this.title.width);
		this.title.text = this._pending.title;

		this.body.width = Math.max(this.title.width, this.body.width);
		this.body.docking.y = this.title.docking.y + this.title.height + this.spacing.y;
		this.body.text = this._pending.body;

		// there is an error in case body text is empty
		if (this.body.text === "") {
			this.body.text = ".";
			this.body.visible = false;
		} else {
			this.body.visible = true;
		}

		const totalWidth = Math.max(buttonsWidth, this.title.width, this.body.width);
		const totalHeight = this.title.height + this.body.height + maxButtonHeight + this.spacing.y * 2;

		buttons.forEach((button, index) => {
			// resize button
			button.nineSliceMinSize.x = maxButtonWidth;
			button.label = button.label;
			// position button
			button.docking.x =
				(totalWidth - buttonsWidth) * 0.5 +
				(maxButtonWidth + this.spacing.x) * index +
				this.padding.x;
			button.docking.y = -this.padding.height;
			// enable button
			button.enabled = true;
		});

		this.background.width = totalWidth + this.padding.x + this.padding.width;
		this.background.height = totalHeight + this.padding.y + this.padding.height;
		this.dockingBounds.setBounds(this.background.width, this.background.height);
	}

	private addOverlay(): void {
		if (this.parent) {
			this.parent.addChild(this.overlay);
			// move to foreground
			this.parent.addChild(this);
		}
	}

	private removeOverlay(): void {
		if (this.parent && this.overlay.parent) {
			this.parent.removeChild(this.overlay);
		}
	}

	private buttonsSetEnabled(value: boolean): void {
		// this.closeButton.interactive = value;
		this._buttons.forEach((button) => (button.enabled = value));
	}

	private buttonsSetInteractive(value: boolean): void {
		// this.closeButton.interactive = value;
		this._buttons.forEach((button) => (button.interactive = value));
	}

	private removeButtons(): void {
		this._buttons.forEach((button) => {
			if (button.parent) {
				this.removeChild(button);
				button.interactive = false;
				button.removeAllListeners();
				button.destroy();
			}
		});

		this._buttonsStack = [];
		this._buttons = [];
	}
}
