import {
	Amount,
	EOrientation,
	ErrorEvent,
	GameLoaded,
	GameQuit,
	HowlerLoader,
	Loader,
	Logger,
	Numeral,
	Tools,
	tracker,
} from "@gdk/core";
import { GamePixi, Resize, Scratch2, SymbolSprite, SymbolSpriteError } from "@gdk/core-pixi";
import { TweenMax, Power1 } from "gsap";
import { Howler } from "howler";
import i18n from "i18next";
import { Graphics, ticker, utils } from "pixi.js";
import type { IGamePixiOptions, TextField } from "@gdk/core-pixi";
import type { IAmount, IGameConf } from "@gdk/core";
import type { EGameMode, IOpenGameParameters } from "@falcon/bridge";

import {
	AdaptativeVisualIdentity,
	EVisualIdentityAction,
	EVisualIdentityEvent,
} from "../adaptativeVisualIdentity";
import { Com, EMessageType, EPopinLevel, Popin, PopinButtonAction } from "../com";
import { EGameEvents } from "./gameEvents";
import { Notifier } from "./notifier/notifier";
import { TweenUtils } from "./tweenUtils";

/**
 * Strings used as identifier for reasons to pause the game. They are stored in
 * the AbstractGame "pauseReasons" instance property, and used to know if the game
 * should be paused or not. Each reason should be dedidated to exactly one use case,
 * and not shared, otherwise shared reasons could conflict and unpause the game when
 * it shouldn't be.
 */
type PauseReason =
	| "lockscreen" // lockscreen is displayed (VI lockscreen)
	| "blur" // game window lost focus
	| "settings" // settings were open (and game parameter pauseOnSetting is set)
	| "rules" // rules are open
	| "messagemanager" // com/messageManager is displaying a message
	| `notifier_${string}`; // pauses related to notifier

export class AbstractGame extends GamePixi {
	// Public members
	public com: Com;
	public vi: AdaptativeVisualIdentity;

	public gameConfig: IGameConf;
	public skin: any = null;

	public rulesVisible: boolean = false;

	public events: utils.EventEmitter = new utils.EventEmitter();

	private _muted: boolean = false;

	private _disableInteractivityOverlay: Graphics;

	/**
	 * List of unique reasons this game was paused for.
	 * This set is empty means: the game is not paused. Better use `paused` getter
	 * to know if the game is paused.
	 *
	 * This set is updated by methods `pause` and `unpause`.
	 */
	protected pauseReasons: Set<PauseReason> = new Set();

	/**
	 * Constructor
	 * @param gameConfig: the game configuration (game.conf.json)
	 * @param width: the canvas width
	 * @param height: the canvas height
	 * @param noWebGL: enable/disable WebGL (to force Canvas)
	 * @param options: the Pixi.js applicaiton options
	 */
	public constructor(
		gameConfig: IGameConf,
		width: number,
		height: number,
		noWebGL: boolean,
		options: IGamePixiOptions
	) {
		// Initialize
		super(
			width,
			height,
			noWebGL,
			Tools.assign(options, {
				createGameDiv: options.createGameDiv === undefined,
			})
		);

		this.com = new Com(this);

		// merge behaviour into gameParameters, every property must be accessed in gameConfig.gameParameters
		gameConfig.gameParameters = Tools.merge(
			gameConfig.gameParameters,
			gameConfig.behavioursParameters[gameConfig.selectedBehaviour]
		);

		// Configure popin
		Popin.wrapper = this.gameDiv;

		// Init logger
		Logger.init();

		// Create Visual identity
		this.vi = new AdaptativeVisualIdentity(this, {
			lottery: gameConfig.behavioursParameters[gameConfig.selectedBehaviour].vi,
			theme:
				gameConfig.behavioursParameters[gameConfig.selectedBehaviour].viParameters.theme ||
				"default",
			parameters: gameConfig.behavioursParameters[gameConfig.selectedBehaviour].viParameters,
		});

		// Create the overlay used to disable all interactivity (ex: replay of a game)
		this._createOverlay();

		// Events
		this.onResize = () => this.resize();
		this.onShowLockScreen = () => {
			this.mute(true);
			this.pause("lockscreen");

			// TODO[epic=jackpot] JACKPOT_SHOW_LOCKSCREEN
			this.com.bridge.emitEvent("JACKPOT_SHOW_LOCKSCREEN");
		};

		this.onHideLockScreen = () => {
			this.unpause("lockscreen");

			this.tryToMute(false);

			// TODO[epic=jackpot] JACKPOT_HIDE_LOCKSCREEN
			this.com.bridge.emitEvent("JACKPOT_HIDE_LOCKSCREEN");
		};

		this.vi.emitter.on(EVisualIdentityAction.SoundEnabled, (enabled: boolean) => {
			this.mute(!enabled);
		});

		// Track quit event
		this.vi.emitter.on(EVisualIdentityEvent.QuitGame, () => {
			tracker.track(new GameQuit());
			this.com.bridge.executeAction("CLOSE_GAME");
		});

		// Handle Rules buttons
		let previouslyEnabled = false;

		this.vi.emitter.on(EVisualIdentityEvent.ShowSettings, () => {
			// Pause the game
			if (this.gameConfig.gameParameters.pauseOnSettings ?? true) {
				this.pause("settings");
			}

			// On show settings, enable or disable rules buttons
			let enabled =
				this.vi.inGameView.settingsComponent && this.vi.inGameView.settingsComponent.rulesEnabled;

			previouslyEnabled = enabled;

			if (this.state.current() === "rules") {
				enabled = false;
			}

			if (this.vi.inGameView.settingsComponent) {
				this.vi.inGameView.settingsComponent.rulesEnabled = enabled;
			}
		});

		this.vi.emitter.on(EVisualIdentityEvent.HideSettings, () => {
			if (this.gameConfig.gameParameters.pauseOnSettings ?? true) {
				this.unpause("settings");
			}

			if (this.vi.inGameView.settingsComponent) {
				this.vi.inGameView.settingsComponent.rulesEnabled = previouslyEnabled;
			}
		});

		this.vi.emitter.on(EVisualIdentityEvent.ShowRules, () => {
			this.onRulesOpen();
		});
		this.vi.emitter.on(EVisualIdentityEvent.HideRules, () => {
			this.onRulesQuit();
		});
		this.vi.emitter.on(EVisualIdentityEvent.ShowMainRules, () => {
			this.onRulesOpen();
		});
		this.vi.emitter.on(EVisualIdentityEvent.ShowWinningTable, () => {
			this.onRulesOpen();
		});

		// Override events from game-core
		// (blur & focus)
		this.onFocus = () => {
			if (this.gameConfig.gameParameters.pauseOnBlur) {
				this.unpause("blur");
			}

			if (this.gameConfig.gameParameters.muteSoundOnBlur !== false) {
				this.tryToMute(false);
			}

			this.vi.emitter.emit(EVisualIdentityEvent.GameFocused);
		};
		this.onBlur = () => {
			// games may want to remains active on blur.
			if (this.gameConfig.gameParameters.pauseOnBlur) {
				this.pause("blur");
			}

			// We check !== false to ensure backward compatibility where muteSoundOnBlur
			// was undefined.
			if (this.gameConfig.gameParameters.muteSoundOnBlur !== false) {
				this.tryToMute(true);
			}

			this.vi.emitter.emit(EVisualIdentityEvent.GameBlurred);
		};

		// Request fullscreen
		this.application.view.addEventListener("touchend", () => {
			this.requestFullscreen();
		});

		// handle context menu blocking
		if (gameConfig.gameParameters.contextMenuBlocked) {
			document.addEventListener(
				"contextmenu",
				(e) => {
					e.preventDefault();
				},
				false
			);
		}

		// handle right click rejection
		if (gameConfig.gameParameters.rightClickRejected) {
			const interaction = this.application.renderer.plugins.interaction;

			if (interaction.supportsPointerEvents) {
				window.document.removeEventListener("pointermove", interaction.onPointerMove, true);
				interaction.interactionDOMElement.removeEventListener(
					"pointerdown",
					interaction.onPointerDown,
					true
				);
				window.removeEventListener("pointerup", interaction.onPointerUp, true);
			} else {
				window.document.removeEventListener("mousemove", interaction.onPointerMove, true);
				interaction.interactionDOMElement.removeEventListener(
					"mousedown",
					interaction.onPointerDown,
					true
				);
				window.removeEventListener("mouseup", interaction.onPointerUp, true);
			}

			const legacyOnPointerDown = interaction.onPointerDown;

			interaction.onPointerDown = function (e: MouseEvent): void {
				if (e.button !== 2) {
					legacyOnPointerDown.call(this, e);
				}
			};

			const legacyOnPointerUp = interaction.onPointerUp;

			interaction.onPointerUp = function (e: MouseEvent): void {
				if (e.button !== 2) {
					legacyOnPointerUp.call(this, e);
				}
			};

			const legacyOnPointerMove = interaction.onPointerMove;

			interaction.onPointerMove = function (e: MouseEvent): void {
				if (e.buttons === 0 || (e.buttons & 1) === 1) {
					legacyOnPointerMove.call(this, e);
				}
			};

			if (interaction.supportsPointerEvents) {
				window.document.addEventListener("pointermove", interaction.onPointerMove, true);
				interaction.interactionDOMElement.addEventListener(
					"pointerdown",
					interaction.onPointerDown,
					true
				);
				window.addEventListener("pointerup", interaction.onPointerUp, true);
			} else {
				window.document.addEventListener("mousemove", interaction.onPointerMove, true);
				interaction.interactionDOMElement.addEventListener(
					"mousedown",
					interaction.onPointerDown,
					true
				);
				window.addEventListener("mouseup", interaction.onPointerUp, true);
			}
		}

		// Register sound loader
		Loader.register(new HowlerLoader());

		this.events.on(EGameEvents.GameEnd, () => {
			this.com.messageManager.onGameEnd();
		});

		// Configure numeral
		Numeral.locale = gameConfig.selectedLocale;
		Numeral.currencyCode = gameConfig.stakes[0].currency;
	}

	/**
	 * Return true if the game is paused for one of the given reasons.
	 * If no reason passed, this method simply returns true if the game is paused.
	 */
	public isPaused(...reasons: PauseReason[]): boolean {
		if (reasons.length === 0) {
			return this.pauseReasons.size > 0;
		}

		for (const reason of reasons) {
			if (this.pauseReasons.has(reason)) {
				return true;
			}
		}

		return false;
	}

	/**
	 * Add a reason to pause the game. The game is actually paused if it wasn't yet.
	 */
	public pause(reason: PauseReason): void {
		const wasPaused = this.isPaused();
		this.pauseReasons.add(reason);

		console.debug(
			`game.pause(${reason}).` +
				` Game pause reasons: [${Array.from(this.pauseReasons).join(", ")}]`
		);

		if (!wasPaused) {
			this.events.emit(EGameEvents.GamePaused, true, reason);

			// send events to jackpot
			switch (reason) {
				case "blur":
					this.com.bridge.emitEvent("JACKPOT_PAUSE");
					break;
				case "lockscreen":
					this.com.bridge.emitEvent("JACKPOT_SHOW_LOCKSCREEN");
					break;
				case "rules":
				case "settings":
				case "messagemanager":
					this.com.bridge.emitEvent("JACKPOT_CONFIGURE", { interactive: false });
					break;
			}

			TweenUtils.pauseAllTweens(false);
			ticker.shared.stop();

			// Mute scratch sounds
			if (Scratch2.scratchSound) {
				Scratch2.scratchSound.mute(true);
			}
		}
	}

	/**
	 * Unregister given reason from the list of reasons to pause and resume the game
	 * is necessary.
	 *
	 * @returns boolean: was the game resumed ?
	 *     true if this call did actually unpause the game,
	 *     false otherwise
	 */
	public unpause(reason: PauseReason): boolean {
		const wasPaused = this.isPaused();
		this.pauseReasons.delete(reason);

		console.debug(
			`game.unpause(${reason}).` +
				` Game pause reasons: [${Array.from(this.pauseReasons).join(", ")}]`
		);

		// try to unmute sound, if it was muted
		if (!this.isPaused()) {
			this.mute(false);
		}

		if (wasPaused && !this.isPaused()) {
			this.events.emit(EGameEvents.GamePaused, false, reason);

			// send events to jackpot
			switch (reason) {
				case "blur":
					this.com.bridge.emitEvent("JACKPOT_RESUME");
					break;
				case "lockscreen":
					this.com.bridge.emitEvent("JACKPOT_HIDE_LOCKSCREEN");
					break;
				case "rules":
				case "settings":
				case "messagemanager":
					if (!this.isPaused("rules", "settings", "messagemanager")) {
						this.com.bridge.emitEvent("JACKPOT_CONFIGURE", { interactive: true });
					}
					break;
			}

			TweenUtils.resumeAllTweens();
			ticker.shared.start();

			// Unmute scratch sounds
			if (Scratch2.scratchSound) {
				Scratch2.scratchSound.mute(false);
			}

			return true;
		}

		return false;
	}

	/**
	 * Pause tween animations
	 */
	public onRulesOpen(): void {
		this.pause("rules");

		TweenUtils.resumeRulesTweens();
		if (Scratch2.scratchSound) {
			Scratch2.scratchSound.mute(true);
		}

		this.rulesVisible = true;
		this.events.emit(EGameEvents.RulesOpen);
	}

	/**
	 * Resume tween animations
	 */
	public onRulesQuit(): void {
		TweenUtils.pauseRulesTweens();
		if (Scratch2.scratchSound) {
			Scratch2.scratchSound.mute(false);
		}

		this.unpause("rules");

		this.rulesVisible = false;
		this.events.emit(EGameEvents.RulesQuit);
	}

	/**
	 * Returns if the game sound is active (helper)
	 */
	public get soundStatus(): boolean {
		return this.vi.soundEnabled;
	}

	/**
	 * Returns if the game is super jackpotized
	 * (default: false)
	 */
	public get hasSuperJackpot(): boolean {
		// TODO[epic=jackpot] change this
		return true;
	}

	/**
	 * Updates the purse
	 * @param updateVi if should update the VI purse
	 */
	public async getPurse(updateVi: boolean): Promise<IAmount> {
		const updatePurse = (updatedAmount: IAmount) => {
			if (updateVi) {
				this.vi.emitter.emit(
					EVisualIdentityAction.SetPurse,
					updatedAmount ? new Amount(updatedAmount) : null
				);
			}
		};

		const amount = await this.com.getPurse();
		updatePurse(amount);

		return amount;
	}

	/**
	 * Initializes all game's components:
	 * - Com
	 * - i18n
	 * - getGameConfiguration
	 * @param done called once initialization is done (com, i18n, etc.)
	 */
	public async init(done: () => void): Promise<void> {
		await this.com.init();

		const EVENTID_DIMENSIONS: string = "DIMENSIONS";
		const EVENTID_DIMENSIONS_LEGACY: string = "dimensions";
		const dimensions = {
			width: this.settings.orientation === EOrientation.Portrait ? 480 : 800,
			height: this.settings.orientation === EOrientation.Portrait ? 640 : 600,
		};
		this.com.bridge.emitEvent(EVENTID_DIMENSIONS, dimensions);
		this.com.bridge.emitEvent(EVENTID_DIMENSIONS_LEGACY, dimensions);

		if (this.com.bridge.channel.setNotifier) {
			this.com.bridge.channel.setNotifier(new Notifier(this));
		}

		tracker.init({ com: this.com, logger: Logger.getLogger() });

		// Init i18n
		this.settings.language = this.gameConfig.selectedLocale;

		i18n.init({
			lng: this.gameConfig.selectedLocale,
			resources: {},
		});

		i18n.setDefaultNamespace("game");

		// Init com i18n
		await this.com.initI18n();

		done();
	}

	/**
	 * Starts the game.
	 * @param view Specifies the first view to make appear.
	 * @param done Optionnal callback called once start is done.
	 * @returns A promise resolved when the game is loaded and started or void for backward compatibility.
	 */
	public start(state: string, done?: () => void): Promise<void> | void {
		return this._start(state, done);
	}

	private async _start(state: string, done?: () => void): Promise<void> {
		// Give event emitter to state.
		if (this.state) {
			this.state.eventEmitter = this.events;
		}

		await this._startVI();

		// When loading, we disable the lock screen.
		// The lock screen is only enabled when the game has been loaded.
		// To perform this action, just force resize
		this.lockScreenEnabled = false;
		this.resize(innerWidth, innerHeight, true);

		// Now add the canvas into the webpage
		this.gameDiv.appendChild(this.application.view);

		// Update purse using the Communication module. Then, update the VI's purse
		await this.getPurse(true);

		// Load all the game's assets!
		await new Promise<void>((resolve) => {
			this._loadAssets(
				() => {
					resolve();
					// Must call done there or the loading screen won't work.
					// Load process is unreliable.
					if (done) {
						done();
					}
				},
				() => {
					// Once assets loaded, start Visual Identity introduction
					this.lockScreenEnabled = true;

					// Show the Visual Identity introduction once game's assets are loaded
					// Once animation finished, start the game!
					this.vi.showIntroduction(async () => {
						// Show the ingame view of the Visual Identity first.
						this.vi.showInGame();

						// Be sure game is resized to ensure transformations of the inGameView
						// of the Visual Identity. It is not deconnant.
						this.resize(innerWidth, innerHeight);

						// Finally start the game
						await super.start(state);

						this.verifySymbolSpriteSignatures();

						// Notify ready and show in-game Visual Identity
						this.vi.emit(EVisualIdentityAction.SetGameReady);
					});

					// Resize the back buffer and check
					// if we are in the right orientation
					// To perform this action, just force resize
					this.resize(innerWidth, innerHeight, true);
				}
			);
		});

		// TODO[epic=jackpot] JACKPOT_CONFIGURE send lotteryGameName from locale
		this.com.bridge.emitEvent("JACKPOT_CONFIGURE", {
			lotteryGameName: i18n.t("gameName", { returnEmptyString: true }),
		});
	}

	protected verifySymbolSpriteSignatures() {
		let authorizedSignatures = this.gameConfig.symbolSprites ?? [];

		const wrongSignatures = authorizedSignatures.filter((s) => s.length !== 7 && s.length !== 64);
		if (wrongSignatures.length > 0) {
			this.displayErrorPopin(this.com.t("no-network", "error"));
			throw new SymbolSpriteError(
				`Incorrect signatures: "${wrongSignatures.join(
					", "
				)}": signatures must be 7 or 64 characters long`
			);
		}
		const symbolSpriteInstances = SymbolSprite.instances;
		const currentSignatures = symbolSpriteInstances.map((symbolSprite) => symbolSprite.signature);

		const unauthorizedSignatures = new Set(
			currentSignatures.filter((signature) => {
				for (const authorizedSignature of authorizedSignatures) {
					const signatureToCompare =
						authorizedSignature.length === 7 ? signature.slice(0, 7) : signature;
					if (authorizedSignature === signatureToCompare) {
						return false;
					}
				}
				return true;
			})
		);
		if (unauthorizedSignatures.size > 0) {
			this.displayErrorPopin(this.com.t("no-network", "error"));
			throw new SymbolSpriteError(
				`Found symbolSprite signatures that were not specified in config.symbolSprites: "${[
					...unauthorizedSignatures,
				].join(", ")}"`
			);
		}
		const currentSignaturesToCompare = [
			...new Set([...currentSignatures, ...currentSignatures.map((s) => s.slice(0, 7))]),
		];
		const signaturesNotUsed = new Set(
			authorizedSignatures.filter((signature) => {
				return !currentSignaturesToCompare.includes(signature);
			})
		);
		if (signaturesNotUsed.size > 0) {
			this.displayErrorPopin(this.com.t("no-network", "error"));
			throw new SymbolSpriteError(
				`Signatures "${[...signaturesNotUsed].join(
					", "
				)}" were specified in config.symbolSprites but not used by any symbolSprite in the project`
			);
		}
		for (const symbolSprite of symbolSpriteInstances) {
			symbolSprite.seal();
		}
	}

	protected displayErrorPopin(text: string): void {
		this.com.messageManager
			.display({
				code: undefined,
				type: EMessageType.Immediate,
				popup: {
					level: EPopinLevel.Error,
					text,
					closable: false,
					buttons: [
						{
							label: this.com.t("close", "ui"),
							action: PopinButtonAction.CloseGame,
						},
					],
				},
			})
			.finally();
	}

	private async _startVI(): Promise<void> {
		// Set the base URL of all loaders to 'game/local/' (depends on the environment)
		const baseUrl = this.gameConfig.frameworkPath;

		// Configure the visual identity loader by giving the base URL.
		// The base URL is available in game.conf.json then it can be customized by users
		const viFolder = Tools.getFolder(this.gameConfig.modules.paths["@gdk/gamekit"]);
		this.vi.loader.baseUrl = `${baseUrl}${viFolder}/../assets/visualidentity/`;

		// Init the Visual Identity module. This will load all the required
		// assets and create the required views
		await this.vi.init();

		// Setup illiko introduction. To skip introduction, just set skipIntro to true
		this.vi.introductionView.skipIntro =
			this.gameConfig.behavioursParameters[this.gameConfig.selectedBehaviour].viParameters
				.skipIntroduction ?? false;

		// Once done, settings are enabled by default. This must be done once the VI
		// has been initialized. Also, by default, the rules are disabled until the game
		// enters in the "home" view
		this.vi.emitter.emit(EVisualIdentityAction.SettingsEnabled, true);
		this.vi.emitter.emit(EVisualIdentityAction.RulesEnabled, false);
		this.vi.emitter.emit(
			EVisualIdentityAction.SoundEnabled,
			this.gameConfig.gameParameters.soundEnabled ?? true
		);

		// Just show the loading view (spinner + percentage)
		this.vi.showLoadingView();
	}

	/**
	 * Mute / demute all sounds in game. If sound state was already in the right state,
	 * does nothing. If asked to unmute (muted == false) and sound was deactivated
	 * from settings (this.vi.soundEnabled == false), does nothing (excepted if force == true).
	 * If sound is actually changed, returns true, notifies com, and emit event
	 * EVisualIdentityEvent.SoundStatusChanged
	 *
	 * @param value desired mute state
	 * @param force: boolean: if true, force vi sound state to synchronized with given state
	 * @return boolean: true is sound status changed
	 */
	public mute(muted: boolean, force: boolean = false): boolean {
		if (this.muted === muted) {
			return false;
		} else if (force) {
			this._muted = muted;
			this.vi.soundEnabled = true;
		}

		const changed = this.tryToMute(muted);

		if (changed) {
			this._muted = muted;
			this.com.sendSoundStatus(!muted);
			this.vi.emitter.emit(EVisualIdentityEvent.SoundStatusChanged);
		}
	}

	/**
	 * Tries to disable/enable the sound in the applet. If the applet is already forced to be
	 * muted, then nothing changes.
	 * @param disabled defines wether or not the sound should be disabled.
	 * @returns boolean: true if sound status was changed, false otherwise
	 */
	public tryToMute(muted: boolean): boolean {
		// QUICKFIX: prevent stacking of sounds by howler,
		// erasing sound resume handlers filters sounds started when audio context was disabled
		// not really elegant since it could break things based on sound resume handlers
		if (!muted) {
			const canceledSounds: string[] = [];
			for (const howl of (Howler as any)._howls) {
				if (!howl._loop) {
					const l = howl._onresume.length;
					howl._onresume = howl._onresume.filter(({ fn }) => fn.name !== "playWebAudio");
					if (howl._onresume.length < l) {
						canceledSounds.push(howl._src);
					}
				}
			}
			if (canceledSounds.length > 0) {
				const canceledSoundNames = canceledSounds.map((s) => s.substr(s.lastIndexOf("/") + 1));
				console.debug(
					`Sound actived: resume of these sounds was canceled` +
						` since they are not looping: ${canceledSoundNames.join(", ")}`
				);
			}
		}

		if (!muted && !this.vi.soundEnabled) {
			return false;
		}

		Howler.mute(muted);
		this.events.emit(EGameEvents.GameMuted, muted);

		return true;
	}

	/**
	 * Puts an interactive overlay over the game so all interaction are catched
	 */
	public showInteractivityOverlay(): void {
		this.stage.addChild(this._disableInteractivityOverlay);
		TweenMax.fromTo(
			this._disableInteractivityOverlay,
			0.15,
			{ alpha: 0 },
			{ alpha: 1, ease: Power1.easeInOut }
		);
	}

	/**
	 * Removes the interactive overlay
	 */
	public removeInteractivityOverlay(): void {
		TweenMax.fromTo(
			this._disableInteractivityOverlay,
			0.15,
			{ alpha: 1 },
			{
				alpha: 0,
				ease: Power1.easeInOut,
				onComplete: () => {
					this.stage.removeChild(this._disableInteractivityOverlay);
				},
			}
		);
	}

	/**
	 * will return current game sounds state (muted or not)
	 * @return true if the sound is muted.
	 */
	public get muted(): boolean {
		return (Howler as any).state !== "running" || this._muted;
	}

	/**
	 * Returns the path of the assets folder according to the current theme in GameConf.
	 */
	public getThemePath(): string {
		const themeKey = this.gameConfig.selectedTheme;
		const themePath = this.gameConfig.gameDescription.assets.themes[themeKey];
		return `${this.gameConfig.gamePath}assets/${themePath}`;
	}

	/**
	 * Return the game's infos. Should be overidden to add
	 * more informations to log
	 */
	protected async getInfos(): Promise<any> {
		const infos = await super.getInfos();
		infos.envInfos.bridge = this.com.bridge.version;
		infos.envInfos.renderMode = this.isCanvasRenderer ? "Canvas" : "WebGL";
		infos.envInfos.renderSize = { width: this.renderer.width, height: this.renderer.height };
		return infos;
	}

	/**
	 * Get open game parameters
	 */
	public getOpenGameParameters(): IOpenGameParameters {
		return this.com.getOpenGameParameters();
	}

	/**
	 * Resolves when done, with the current GameMode
	 */
	public getGameMode(): Promise<EGameMode> {
		return this.com.getGameMode();
	}

	/**
	 * Loads the game assets
	 * @param startCallback callback when starting the game once assets loaded
	 * @param done callback when everything has been loaded and created
	 */
	private async _loadAssets(startCallback: () => void, done: () => void): Promise<void> {
		// Track game is loaded
		tracker.track(
			new GameLoaded({
				technicalName: this.gameConfig.lotteryGameCode,
				gameType: this.gameConfig.engine,
				locale: this.gameConfig.selectedLocale,
				currency: this.gameConfig.stakes[0].currency,
				theme: this.gameConfig.selectedTheme,
			})
		);

		this.loader.onError = (file: string) => {
			tracker.track(new ErrorEvent(`Failed to load ${file}`));
		};

		// Now, just configure the loader of the game to have an empty
		// base URL
		this.loader.baseUrl = "";

		// Add game locales
		const expFolder =
			this.gameConfig.frameworkPath +
			Tools.getFolder(this.gameConfig.modules.paths["@gdk/gamekit"]);
		this.loader.add(`${expFolder}/../assets/game/${this.settings.language}.json`, "game-i18n");

		// Set the assets path of the game (game path + "assets/") and
		// call the main loader to load from the current Game Config File (game.conf.json)
		this.loader.assetsPath = `${this.gameConfig.gamePath}assets/`;
		this.loader.loadFrom(this.gameConfig, () => {
			// Merge game locales into the current ones
			i18n.addResourceBundle(
				this.settings.language,
				"game",
				this.loader.get("game-i18n"),
				true,
				false
			);

			// Callback the user in order to add his/her own background(s)
			// view(s) etc.
			if (startCallback.length === 0) {
				startCallback();
			}

			return done();
		});
	}

	private _createOverlay(): void {
		// When updating the session becuase the browser was hidden, draw an overlay
		this._disableInteractivityOverlay = new Graphics();
		this._disableInteractivityOverlay.beginFill(0x0, 0.6);
		this._disableInteractivityOverlay.drawRect(0, 0, 1, 1);
		this._disableInteractivityOverlay.resize = new Resize(Resize.cover);
		this._disableInteractivityOverlay.interactive = true;
	}
}
