import { EMtrSessionState } from "@falcon/bridge";
import type {
	IMtrBetResult,
	IMtrSession,
	IMtrReplaySession,
	IMtrSessionStep,
} from "@falcon/bridge";
import type { IAmount } from "@gdk/core";

import { EMessageType, EPopinLevel, PopinButtonAction } from "../../com";
import type { Action } from "./action";

export enum ESessionMode {
	Default = "default",
	Resume = "resume",
	Replay = "replay",
}

/**
 * Motors Session.
 * @template TGameData Session typings.
 * @template TEventParameters "prm" typings.
 * @template TEventResponse "evr" typings.
 * @template TModel model definition.
 */
export class MotorsSession<TGameData, TEventParameters, TEventResponse, TModel> {
	/**
	 * Demo mode.
	 */
	public demo?: boolean;
	/**
	 * Session id.
	 */
	public id?: string = undefined;
	/**
	 * Current session state.
	 */
	public state?: string = undefined;
	/**
	 * Session game data. Must be used ONLY for resuming game.
	 * It will be undefined in replay mode.
	 */
	public sessionData?: TGameData = undefined;
	/**
	 * Bet amount.
	 */
	public betAmount?: IAmount = undefined;
	/**
	 * Won amount, defined only when the game is done or in replay mode.
	 */
	public wonAmount?: IAmount = undefined;
	/**
	 * Last bet/updatebet EVR.
	 */
	public lastResponse?: TEventResponse = undefined;
	/**
	 * Mtr session mode.
	 */
	public mode?: ESessionMode = undefined;
	/**
	 * Game model id.
	 */
	public gmid?: number;
	/**
	 *  Service name
	 */
	public serviceName?: string;
	/**
	 * Application id
	 */
	public applicationId: string;

	private _game: Action<
		TModel,
		TGameData,
		TEventResponse,
		TEventParameters,
		MotorsSession<TGameData, TEventParameters, TEventResponse, TModel>
	>;
	/**
	 * In case of a replay/resume, steps of the session.
	 */
	private _steps?: Array<IMtrSessionStep<TEventParameters, TEventResponse>>;
	/**
	 * In case of a replay/resume, parameters of the bet.
	 */
	private _initialStepParameters?: TEventParameters;

	/**
	 * Constructor.
	 * @param payload mtrSession or mtrBetResult
	 */
	public constructor(
		game: Action<
			TModel,
			TGameData,
			TEventResponse,
			TEventParameters,
			MotorsSession<TGameData, TEventParameters, TEventResponse, TModel>
		>,
		payload:
			| IMtrSession<TGameData, TEventParameters, TEventResponse>
			| IMtrBetResult<TEventResponse>
			| IMtrReplaySession<TEventResponse, TEventParameters>
	) {
		this._game = game;
		if ("res" in payload.data) {
			this._initFromBetRespone(payload as IMtrBetResult<TEventResponse>);
		} else if ("gmd" in payload.data) {
			this._initFromSession(payload as IMtrSession<TGameData, TEventParameters, TEventResponse>);
		} else {
			this._initFromReplaySession(payload as IMtrReplaySession<TEventResponse, TEventParameters>);
		}
	}

	/**
	 * Executes a Motors action and update the session with the response.
	 * @param event name of the event to trigger.
	 * @param parameters parameters for the event.
	 */
	public async updateBet(event: string, parameters?: TEventParameters): Promise<void> {
		if (this.mode === ESessionMode.Replay && this.hasReplaySteps()) {
			this._updateFromBetResponse(this._getReplayStepAsBetResult());
			return;
		}

		// Update
		const response = await this._game.com.updateMtrBet<TEventResponse, TEventParameters>({
			serviceName: this.serviceName,
			applicationId: this.applicationId,
			parameters: {
				prm: parameters,
				evn: event,
				sid: this.id,
			},
			demo: this.demo,
		});

		if (response.message) {
			this._game.com.messageManager.push(response.message);
		}

		this._updateFromBetResponse(response);
	}

	/**
	 * Check if session has replay steps.
	 */
	public hasReplaySteps(): boolean {
		return this._steps !== undefined;
	}

	/**
	 * Usable on replay mode.
	 * Returns next replay step as a betResult.
	 */
	private _getReplayStepAsBetResult(): IMtrBetResult<TEventResponse> {
		if (!this._steps || !this._steps.length) {
			throw new Error("No steps in the session.");
		}
		const step = this._steps.shift();
		const result: IMtrBetResult<TEventResponse> = {
			applicationId: this.applicationId,
			serviceName: this.serviceName,
			betAmount: this.betAmount,
			wonAmount: this.wonAmount,
			receiptId: "", // @todo is it important ?
			platformBetId: "", // @todo is it important ?
			serverMessage: [],
			data: {
				res: "ok",
				data: {
					evr: step.res,
					st: step.st,
					gmid: this.gmid,
					id: this.id,
				},
			},
			demo: false, // no replay for demo
		};
		if (!this._steps.length) {
			this._steps = undefined;
		}
		return result;
	}

	/**
	 * Usable on replay mode.
	 * Reurns initial action parameters.
	 */
	public getInitialReplayParameters(): TEventParameters {
		if (!this._initialStepParameters) {
			throw new Error("No steps in the session.");
		}
		return this._initialStepParameters;
	}

	/**
	 * Usable on replay mode.
	 * Returns next action parameters.
	 */
	public getNextReplayStepParameters(): TEventParameters {
		if (!this._steps) {
			this._game.com.messageManager.display({
				code: "MTR.REPLAY.RESOLVED",
				type: EMessageType.Immediate,
				popup: {
					level: EPopinLevel.Error,
					text: this._game.com.t("motors-replay-unfinished", "error"),
					buttons: [
						{
							label: this._game.com.t("close", "ui") || " Close",
							action: PopinButtonAction.CloseGame,
						},
					],
				},
			});
			throw new Error("No steps in the session.");
		}
		return this._steps[0].par;
	}

	/**
	 * Update state, last response and eventually wonAmount from BetResult.
	 * @param betResult
	 */
	protected _updateFromBetResponse(betResult: IMtrBetResult<TEventResponse>): void {
		this.state = betResult.data.data.st;
		if (betResult.wonAmount) {
			this.wonAmount = betResult.wonAmount;
		}
		this.lastResponse = betResult.data.data.evr;
	}

	/**
	 * Init session data from BetResult.
	 * @param betResult
	 */
	private _initFromBetRespone(betResult: IMtrBetResult<TEventResponse>): void {
		this.demo = betResult.demo;
		this.id = betResult.data.data.id;
		this.state = betResult.data.data.st;
		this.betAmount = betResult.betAmount;
		if (betResult.wonAmount) {
			this.wonAmount = betResult.wonAmount;
		}
		this.lastResponse = betResult.data.data.evr;
		this.mode = ESessionMode.Default;
		this.gmid = betResult.data.data.gmid;
		this.applicationId = betResult.applicationId;
		this.serviceName = betResult.serviceName;
	}

	/**
	 * Init session data from IMtrSession.
	 * This method must be used ONLY for resuming game.
	 * @param session
	 */
	private _initFromSession(
		session: IMtrSession<TGameData, TEventParameters, TEventResponse>
	): void {
		this._initFromSessionOrReplaySession(session);
		this.state = session.data.st;
		this.sessionData = session.data.gmd;
	}

	/**
	 * Init session data from IMtrReplaySession.
	 * This method must be used ONLY for replaying or resuming a game.
	 * @param session
	 */
	private _initFromReplaySession(
		session: IMtrReplaySession<TEventResponse, TEventParameters>
	): void {
		this._initFromSessionOrReplaySession(session);
		if (session.data.steps && session.data.steps.length) {
			const steps = session.data.steps.slice(1); // ignore first step (bet)
			if (steps.length) {
				this._steps = steps;
			}
			this._initialStepParameters = session.data.steps[0].par;
			this.lastResponse = session.data.steps[0].res;
			this.wonAmount = session.wonAmount;
		}
	}

	private _initFromSessionOrReplaySession(
		session:
			| IMtrSession<TGameData, TEventParameters, TEventResponse>
			| IMtrReplaySession<TEventResponse, TEventParameters>
	): void {
		this.demo = false; // no demo information on IMtrReplaySession so presume that demo = false
		this.id = session.id;
		this.betAmount = session.betAmount;
		this.mode =
			session.data.state === EMtrSessionState.InProgress
				? ESessionMode.Resume
				: ESessionMode.Replay;
		this.gmid = session.data.gmid;
		this.applicationId = session.applicationId;
		this.serviceName = session.serviceName;
	}
}
